<template>
  <div class="ds-super-search mobile">
    <div class="ds-super-search-box" :class="{flex: btList.length < 3}">
      <template v-for="(item, index) in btList">
        <dsf-super-search-bt-down v-if="item.id" :key="index" :item="item" />
        <dsf-super-search-bt-sort v-else :key="index" :item="item" />
      </template>
      <!--<div class="ds-super-search-bt">-->
      <!--  <i class="iconfont icon-qiapianmoshi_kuai"></i>-->
      <!--  <span>分类</span>-->
      <!--</div>-->
      <!--<div class="ds-super-search-bt">-->
      <!--  <span>最新</span>-->
      <!--  <i class="sort-bar asc"></i>-->
      <!--  &lt;!&ndash;<i class="sort-bar desc"></i>&ndash;&gt;-->
      <!--</div>-->
      <!--<div class="ds-super-search-bt">-->
      <!--  <span>品牌</span>-->
      <!--  <i class="down-bar"></i>-->
      <!--  &lt;!&ndash;<i class="down-bar opened"></i>&ndash;&gt;-->
      <!--</div>-->
    </div>
    <div v-if="moreList.length" class="ds-super-search-more" @click="moreBtClick">
      <span>筛选</span>
      <i class="iconfont icon-shaixuan"></i>
    </div>
    <transition v-if="!isDesign" name="el-fade-in">
      <div
        v-show="!!dropdownActive"
        class="dropdown-box"
        :style="dropdownBoxStyle"
        @click="maskClick"
      >
        <template v-for="(item, index) in btList">
          <dsf-super-search-down-item
            v-if="item.id"
            :key="index"
            ref="downItem"
            :show="dropdownActive === item.id"
            :item="item"
          />
        </template>
      </div>
    </transition>
    <dsf-super-search-more-popup
      v-if="moreList.length && !isDesign"
      :more-list="moreList"
    />
  </div>
</template>

<script>
import DsfSuperSearchBtDown from './superSearchBtDown.vue';
import DsfSuperSearchBtSort from './superSearchBtSort.vue';
import DsfSuperSearchDownItem from './superSearchDownItem.vue';
import DsfSuperSearchMorePopup from './superSearchMorePopup.vue';

export default {
  name: 'DsfMobileSuperSearch',
  components: {
    DsfSuperSearchBtDown,
    DsfSuperSearchBtSort,
    DsfSuperSearchDownItem,
    DsfSuperSearchMorePopup
  },
  inject: {
    $list: {
      default: null
    }
  },
  provide() {
    return {
      $superSearch: this
    };
  },
  props: {
    isDesign: {
      type: Boolean,
      default: false
    },
    owner: {
      type: Object,
      default() {
        return null;
      }
    },
    // 高级查询
    searchItemSetting: {
      type: Array,
      default() {
        return [];
      }
    },
    // 自定义排序
    customerOrder: {
      type: Array,
      default() {
        return [];
      }
    },
    queryValue: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      dropdownActive: null,
      moreShow: false,
      dropdownBoxStyle: {},
      searchItems: [],
      loaded: false,
      isFirstLoad: false,
    }
  },
  computed: {
    btList() {
      let l = this.customerOrder.length;
      if (l < 3) {
        let searchItems = [];
        let sL = this.searchItems.length;
        sL = sL < 3 - l ? sL : 3 - l;
        for (let i = 0; i < sL; i++) {
          searchItems.push(this.searchItems[i]);
        }
        return [
          ...searchItems,
          ...this.customerOrder
        ];
      }
      return this.customerOrder.slice(0, 3);
    },
    moreList() {
      let l = this.customerOrder.length;
      if (l >= 3) return this.searchItems;
      let sL = this.searchItems.length;
      let searchItems = [];
      for (let i = 3 - l; i < sL; i++) {
        searchItems.push(this.searchItems[i]);
      }
      return searchItems;
    }
  },
  watch: {
    searchItemSetting: {
      async handler(v) {
        await this.updateSearchItemSetting();
      },
      immediate: true
    },
  },
  mounted() {
    let { bottom, left, width } = this.$el.getBoundingClientRect();
    this.dropdownBoxStyle = {
      top: bottom + 'px',
      left: left + 'px',
      width: width + 'px',
    };
    if (this.isDesign) {
      this.dropdownBoxStyle.height = '613px';
    }
  },
  methods: {
    maskClick() {
      let downItem = _.find(this.$refs.downItem, it => {
        return this.dropdownActive === it.item.id;
      });
      this.dropdownActive = null;
      downItem?.close();
    },
    moreBtClick() {
      this.dropdownActive = null;
      this.moreShow = true;
    },
    emitChange() {
      let value = this.emitValue();
      this.$nextTick(() => {
        this.$emit("change", value, true);
      });
    },
    // 以下方法从pc端高级筛选拷贝
    async updateSearchItemSetting() {
      this.searchItems = _.map(this.searchItemSetting, (el) => {
        // let v = _this.$eval(el.defaultValue, this.owner.$vm);
        let defaultValue = null;
        if (dsf.isDef(el.defaultValue) && el.defaultValue != "") {
          defaultValue = this.owner.$eval(el.defaultValue, this.owner.$vm);
        }
        if (el.searchType == "enum") {
          if (dsf.isDef(defaultValue)) {
            defaultValue = dsf.type(defaultValue) != "array" ? [defaultValue] : defaultValue;
          } else {
            defaultValue = [];
          }
          defaultValue = _.map(defaultValue, (v) => v + "");
        } else if (el.searchType == "datetime") {
          if (el.operator == "between") {
            if (dsf.isDef(defaultValue)) {
              defaultValue = dsf.type(defaultValue) != "array" ? [defaultValue] : defaultValue;
            } else {
              defaultValue = [];
            }
          }
        } else {
          defaultValue = dsf.isDef(defaultValue) ? defaultValue + "" : "";
        }
        // let v = this.value ? this.value[el.field] : null;
        return {
          //元数据标识
          id: el.id,
          //字段名称
          field: el.field,
          //字段描述文本
          label: el.fieldCaption,
          //高级检索配置内容，比如数据字典等
          valueSource: el.setting,
          //操作符号
          operator: el.operator,
          //检索类型
          type: el.searchType,
          //绑定数据字典的key
          optionKey: el.id || "",
          //用户选择的值
          options: [],
          //树形结构选中存储
          treeNodeSelected: [],
          //提交给后端的值
          values: defaultValue,
          //显示方式，文本框、日期等
          showModel: el.model,
          //是否点击了更多展开
          isExtend: false,
          //是否有更多按钮
          isMore: false,
          //是否点击多选后展开
          tileMultipleState: false,
          //是否多选状态
          multiple: false,
          //是否允许多选按钮
          isAllowMultipe: !(el.searchType === "enum" && el.operator === "between"),
          //布局
          layout: el.layout || 24,
          //区间选择是自定义输入内容
          customerBetween: [],
          //是否是筛选条展开
          isFilterExtend: false,
          //字段
          fields: _.filter(el.children, (c) => c.join == 1),
          // 是否断行
          isLineBreak: el.isLineBreak || false,
          // 级联子级字段名
          childField: el.childField || null
        };
      });
      this.optionsMap = await this.updateData();
      //将options选项值赋予到每个相关的item中
      _.each(this.searchItems, (item, key) => {
        if (this.optionsMap[item.id]) {
          item.options = this.optionsMap[item.id];
        }
      });
      this.loaded = true;
      this.$nextTick(() => {
        if (!this.isFirstLoad) {
          this.isFirstLoad = true;
          let v = this.emitValue();
          console.log(v);
          this.$nextTick(() => {
            this.$emit("loaded", v);
          });
        }
      });
    },
    async updateData() {
      if (this.owner.isDesign) {
        return {};
      }
      let optionsMap = {};
      let optionsRequests = [];
      let dictArr = [];
      _.each(this.searchItems, (v) => {
        if (v.type == "enum") {
          if (v.valueSource?.type == "dict") {
            let params = {
              key: v.id,
              dictId: v.valueSource.value.key
            };
            dictArr.push(params);
          } else if (v.valueSource?.type == "dbsource") {
            let path = v.valueSource.value.key.replace(/\./g, "/");
            optionsRequests.push({
              id: v.id,
              request: (params) => {
                let p = {
                  ...(this.owner?.queryString || {}),
                  ...(params || {})
                };
                return this.$webAPI.dbSourceInterface(p, path);
              }
            });
          } else if (v.valueSource?.type == "interface") {
            optionsRequests.push({
              id: v.id,
              request: (params) => {
                let p = {
                  ...(this.owner?.queryString || {}),
                  ...(params || {})
                };
                return this.$http.get(this.$replace(v.valueSource.value), p);
              }
            });
          } else {
            if (v.valueSource && v.valueSource.value) {
              optionsMap[v.id] = v.valueSource.value;
            } else {
              optionsMap[v.id] = [];
            }
          }
        }
      });
      if (dictArr.length > 0) {
        let params = {
          dicts: dictArr
        };
        optionsRequests.unshift({
          id: "dicts",
          request: () => {
            return this.$webAPI.getBatchDict(params);
          }
        });
      }
      try {
        let optionsMap2 = await this.loopOptionsHttps(optionsRequests);
        return dsf.mix(optionsMap, optionsMap2, {});
      } catch (err) {
        // dsf.layer.message("查询检索选项出现错误", false);
        dsf.layer.mobile.message("查询检索选项出现错误,页面无法创建", false);
      }
    },
    loopOptionsHttps(requests, callback) {
      let optionsMap = {};
      let p = new Promise((reslove, reject) => {
        let index = -1;
        let next = function () {
          index++;
          if (index >= requests.length) {
            reslove(optionsMap);
            return;
          }
          if (requests.length > 0) {
            exec();
          }
        };
        function exec() {
          let opts = requests[index];
          opts
            .request()
            .then(({ data }) => {
              if (opts.id == "dicts") {
                for (let k in data.data) {
                  optionsMap[k] = data.data[k];
                }
              } else {
                optionsMap[opts.id] = data.data;
              }
              if (callback) {
                callback(
                  {
                    request: requests[index],
                    data: data.data
                  },
                  next
                );
              } else {
                next();
              }
            })
            .catch((ex) => {
              reject();
            });
        }
        next();
      });
      return p;
    },
    emitValue() {
      let value = {};
      _.each(this.searchItems, (el) => {
        if (el.type == "datetime") {
          let baseformat = "yyyy-mm-dd";
          if (el.operator == "between") {
            let arr = dsf.type(el.values) == "array" ? el.values : [el.values];
            arr = _.filter(arr, (v) => v);
            value[el.field] = _.map(arr, (v, i) => {
              if (el.showModel == "ym") {
                if (i == 1) {
                  let m = v.getMonth() + 1;
                  let y = v.getFullYear();
                  let d = new Date(y, m, 0);
                  //将结束日期格式化到月最后一天
                  return dsf.date.format(d, baseformat);
                }
              }
              return dsf.date.format(v, baseformat);
            });
          } else if (el.operator == "gt" || el.operator == "lteq") {
            let val = dsf.type(el.values) == "array" ? el.values[0] : el.values;
            if (el.showModel == "ym") {
              if (val) {
                let m = val.getMonth() + 1;
                let y = val.getFullYear();
                let d = new Date(y, m, 0);
                value[el.field] = val ? dsf.date.format(d, baseformat) : "";
              } else {
                value[el.field] = "";
              }
            } else {
              value[el.field] = val ? dsf.date.format(val, baseformat) : "";
            }
          } else if (el.operator == "gteq" || el.operator == "lt") {
            if (el.showModel == "ym") {
              if (el.values) {
                let m = el.values.getMonth();
                let y = el.values.getFullYear();
                let d = new Date(y, m, 1);
                value[el.field] = el.values ? dsf.date.format(d, baseformat) : "";
              } else {
                value[el.field] = "";
              }
            } else {
              value[el.field] = el.values ? dsf.date.format(el.values, baseformat) : "";
            }
          } else if (el.operator == "eq") {
            let val = dsf.type(el.values) == "array" ? el.values[0] : el.values;
            if (el.showModel == "ym") {
              if (val) {
                let m = val.getMonth() + 1;
                let y = val.getFullYear();
                let d = new Date(y, m, 0);
                value[el.field] = val ? dsf.date.format(d, baseformat) : "";
              } else {
                value[el.field] = "";
              }
            } else {
              value[el.field] = val ? dsf.date.format(val, baseformat) : "";
            }
          }
        } else if (el.type == "enum") {
          if (el.operator == "between") {
            if (el.customerBetween.length > 0 && el.customerBetween[0] && el.customerBetween[1]) {
              value[el.field] = el.customerBetween;
            } else {
              let arr = el.values?.[0] ? el.values[0].split(";") : "";
              if (arr.length == 2) {
                value[el.field] = arr;
              } else if (arr.length == 1) {
                value[el.field] = [arr[0], arr[0]];
              } else {
                value[el.field] = [];
              }
            }
          } else {
            let arr = dsf.type(el.values) == "array" ? el.values : [el.values];
            let result = _.filter(arr, (v) => {
              return dsf.isDef(v);
            });
            value[el.field] = result;
          }
          // if (el.showModel == 'treeselect') {
          //   let treePath = value[el.field];
          //   value[el.field] = _.map(treePath, (el) => {
          //     return el[el.length - 1];
          //   })
          // }
        } else {
          value[el.field] = el.values;
        }
      });
      return value;
    },
  }
}
</script>