<template>
  <div class="work_flow-usersDialog">
    <div class="content" :class="{ isFristSend: isFristSend }">
      <template v-if="isFristSend">
        <div class="txt">
          <van-field v-model="message" rows="2" autosize type="textarea" placeholder="请输入审批意见" />
        </div>
        <div v-if="tpls.length > 0" class="template">
          <div>
            <span v-for="(tpl, i) in tpls" :key="i" @click="message = tpl.key_word">{{ tpl.key_word }}</span>
          </div>
        </div>
        <div class="funs">
          <i class="iconfont icon-fujian" @click="uploader(0)"></i>
          <i class="iconfont icon-tupian" @click="uploader(1)"></i>
        </div>

        <van-uploader
          ref="uploader"
          v-model="uploaderData"
          :class="{ no: uploaderData.length == 0 }"
          :max-count="9"
          multiple
          :after-read="uploaderAfterRead"
          :accept="acceptsType"
          @click-preview="preview"
        />

        <van-popup v-model="previewing" :style="{ height: '80%', width: '100%' }">
          <div>
            <dsf-iframe v-if="previewing" :src="filePath"></dsf-iframe>
          </div>
        </van-popup>
      </template>
      <div class="users">
        <p>您即将发给：</p>
        <!-- <DsfMobileDisposeIdea v-if="isFristSend"></DsfMobileDisposeIdea> -->
        <div
          v-for="d in data.filter((d) => {
            return d.users.length > 0;
          })"
          :key="d.id"
          class="dept"
          :class="{ noSeletc: !Object.keys(d.selects).length }"
        >
          <div class="desc">
            <div class="title">{{ getLinkName(d.name) }}</div>
            <div class="num">{{ d.selects.length || 0 }}人审核</div>
          </div>
          <div v-if="d.users && d.users.length <= 3" class="imgs">
            <div v-for="(user, i) in d.users" :key="user.ID" :class="{ noSeletc: !user.isSelect }" @click="changeUser(user, i, d)">
              <van-image round width="40px" height="40px" fit="cover" :src="'/oua2/getUserPhoto?type=relativePath&userId=' + user.ID">
                <template v-slot:loading>
                  <van-loading type="spinner" size="20" />
                </template>
                <template v-slot:error>
                  <img class="header" src="~_platform/assets/styles/themes/workFlow/default-header.png" />
                </template>
              </van-image>
              <van-icon v-if="user.isSelect" name="checked" />
              <p>{{ user.Name }}</p>
            </div>
          </div>
          <div v-if="d.users && d.users.length > 3" class="imgs">
            <template v-if="d.selects && d.selects.length <= 2">
              <div v-for="(user, i) in d.selects" :key="user.ID" :class="{ noSeletc: !user.isSelect }" @click="changeUser(user, i, d)">
                <van-image round width="40px" height="40px" fit="cover" :src="'/oua2/getUserPhoto?type=relativePath&userId=' + user.ID">
                  <template v-slot:loading>
                    <van-loading type="spinner" size="20" />
                  </template>
                  <template v-slot:error>
                    <img class="header" src="~_platform/assets/styles/themes/workFlow/default-header.png" />
                  </template>
                </van-image>
                <van-icon v-if="user.isSelect" name="checked" />
                <span>{{ user.Name }}</span>
              </div>
            </template>
            <div v-if="d.selects && d.selects.length > 2" class="all-users">
              <div class="circle">
                <template v-for="(user, index) in d.selects">
                  <van-image
                    v-if="index <= 4"
                    :key="user.ID"
                    width="20"
                    height="20"
                    fit="cover"
                    :src="'/oua2/getUserPhoto?type=relativePath&userId=' + user.ID"
                  >
                    <template v-slot:loading>
                      <van-loading type="spinner" size="20" />
                    </template>
                    <template v-slot:error>
                      <img class="error-header" width="20" height="20" src="~_platform/assets/styles/themes/workFlow/default-header.png" />
                    </template>
                  </van-image>
                </template>
              </div>
              <p>{{ d.selects[0].Name }}等{{ d.selects.length }}人</p>
            </div>
            <div class="add" @click="showPicker(d)">
              <i class="iconfont icon-hao"></i>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "DsfMobileChooseUsers",
  props: {
    lines: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  data() {
    return {
      data: [],
      show: false,
      message: "",
      loader: null,
      tpls: [],
      isShowBack: false,
      acceptTypes: [".doc,.docx,.xml,application/msword,application/vnd.openxmlformats-officedocument.wordprocessingml.document", "image/*"],
      acceptsType: "",
      uploaderData: [],
      previewing: false,
      filePath: "",
      isFristSend: true,
    };
  },
  created() {
    this.init(this.lines);
    this.getTemplate();
  },
  methods: {
    yes() {
      let disposeIdea;
      if (this.isFristSend) {
        disposeIdea = {
          inputValue: this.message,
          multipleFiles: this.uploaderData.map((item) => item.data),
        };
      }
      return {
        sReceivers: this.param.join(";"),
        disposeIdea: disposeIdea,
      };
    },
    validate() {
      let param = [];
      this.data.forEach((line) => {
        if (line.selects && line.selects.length > 0) {
          let id = "";
          line.selects.forEach((item) => {
            id += (!id ? "" : ",") + item.ID;
          });
          if (id) {
            id = line.id + ":" + id;
            param.push(id);
          }
        }
      });
      if (param.length == 0) {
        dsf.layer.message("请选择办理人", false);
        return false;
      }
      this.param = param;
      return true;
    },
    init(data) {
      this.show = true;
      this.data = [];
      if (data && data.length) {
        let lines = data;
        for (let i in lines) {
          let line = lines[i];
          this.data.push({
            name: line.Name,
            id: line.LineID,
            users: [],
            selects: [],
            sNum: 0,
            MultiplayerEnabled: line.MultiplayerEnabled,
          });
          if (line.Selected) {
            this.getUserData(line.Receivers.Dept, null, this.data.length - 1);
          }
        }
      }
      if (this.data.length > 0) {
        let userNum = 0;
        let j = 0;
        for (let i in this.data) {
          let _d = this.data[i];
          if (_d.sNum > 0) {
            j = -1;
            break;
          }
          if (_d.users.length == 1) {
            j = i;
          }
          userNum += _d.users.length;
        }
        // 一个人的时候自动选中
        if (userNum == 1 && j != -1) {
          let user = this.data[j].users[0];
          user.isSelect = true;
          this.data[j]["selects"].push(user);
          this.data[j]["sNum"]++;
        }
      }
    },
    getLinkName(name) {
      let ns = name.split("→");
      return ns[ns.length - 1];
    },
    getUserData(d, parent, index) {
      if (!d) {
        return;
      }
      if (d.Objs) {
        d.ChildDepts = d.ChildDepts.concat(d.Objs);
      }
      if (d.Type != 0) {
        if (d.Selected * 1 > 0) {
          // 0 人工选择、1 默认选择、2必选
          this.data[index]["selects"].push(d);
          this.data[index]["sNum"]++;
          d.isSelect = true;
        }
        d["index"] = this.data[index]["users"].length;
        this.data[index]["users"].push(d);
      }
      for (let i in d.ChildDepts) {
        this.getUserData(d.ChildDepts[i], d, index);
      }
    },
    getTemplate() {
      let loader = dsf.layer.loading(),
        msg = "";
      this.$http
        .get("wfr/getOpinionKeyWord", {})
        .done((res) => {
          if (res.success) {
            this.tpls = res.data || [];
          } else {
            msg = `获取意见模板，请求异常`;
          }
        })
        .error((res) => {
          msg = `获取意见模板，服务器异常`;
        })
        .always(() => {
          dsf.layer.closeLoading(loader);
          if (msg) {
            dsf.layer.toast(msg, false);
          }
        });
    },
    changeUser(user, i, d) {
      if (!d.MultiplayerEnabled) {
        d.users.forEach((item) => {
          if (item.ID !== user.ID) {
            this.$set(item, "isSelect", false);
          }
        });
        this.$set(user, "isSelect", !user.isSelect);
      } else {
        this.$set(user, "isSelect", !user.isSelect);
      }
      d.selects = d.users.filter((user) => user.isSelect);
    },
    showPicker(d) {
      dsf.layer.openDialog({
        showClose: false,
        title: "请选择审核人",
        height: "50vh",
        content: "DsfMobileReChooseUsers",
        params: {
          users: JSON.parse(JSON.stringify(d.users)),
          Multi: d.MultiplayerEnabled,
        },
        btns: [
          {
            text: "确定",
            handler: (component, refs) => {
              if (component.validate()) {
                const users = component.yes();
                // this.changeUser(user, user.index, d);
                d.users.forEach((item) => {
                  this.$set(item, "isSelect", false);
                  users.forEach((user) => {
                    if (user.ID === item.ID) {
                      this.$set(item, "isSelect", user.isSelect);
                    }
                  });
                });
                d.selects = d.users.filter((user) => user.isSelect);
                return true;
              }
              return false;
            },
          },
        ],
      });
    },
    uploader(acceptsIndex) {
      this.acceptsType = this.acceptTypes[acceptsIndex];
      this.$nextTick(() => {
        this.$refs.uploader.chooseFile();
      });
    },
    uploaderAfterRead(files) {
      if (!files.length) {
        files = [files];
      }
      _.forEach(files, (file) => {
        file.status = "uploading";
        file.message = "上传中...";

        let error = false;
        let formData = new FormData();
        formData.append("file", file.file);
        this.$http
          .post("file/upload", formData, {
            headers: {
              "Content-Type": "multipart/form-data",
            },
          })
          .done((d) => {
            if (d.state == "20000") {
              file.status = "done";
              file.data = d.data[0];
            } else {
              error = d.message || true;
            }
          })
          .error((d) => {
            error = d.message || true;
          })
          .always(() => {
            if (error) {
              file.status = "failed";
              file.message = "上传失败";
              file._message = error;
            }
          });
      });
    },
    preview(files) {
      if (files && files.data && files.data.contentType && files.data.contentType.slice(0, 5) !== "image") {
        this.filePath = dsf.config.webRoot + "preview/file?fileId=" + files.data.id;
        this.previewing = true;
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.choose-content {
  position: relative;
  width: 100%;
  height: 100%;
}
.fl-box {
  padding: 0 10px;
  box-sizing: border-box;
  height: 100%;
  overflow: auto;
  float: left;
  width: 65%;
  border-left: 1px solid #e2e3e4;
  &.ds_tree_box {
    width: 35%;
  }
  .top {
    overflow: hidden;
    height: 40px;
    line-height: 40px;
    padding-bottom: 10px;
    .ds-button {
      float: right;
    }
  }
  .el-table .cell {
    .iconfont {
      width: 20px;
      display: inline-block;
      text-align: right;
      cursor: pointer;
    }
  }
}
</style>
