<template>
  <div class="ds-control mobile ds-me" :class="{ 'ds-no-padding': !isDesign }">
    <div class="ds-me-wrap">
      <slot name="user-info" v-bind="userInfoConfig">
        <DsfMobileMeUserInfo :isDesign="isDesign" v-bind.sync="userInfoConfig">
        </DsfMobileMeUserInfo>
      </slot>
      <div class="ds-me-menus-box">
        <slot name="menus">
          <DsfMobileMeMenus :isDesign="isDesign" v-bind.sync="menusConfig">
          </DsfMobileMeMenus>
        </slot>
        <slot name="buttons">
          <div class="ds-me-menus-bottom-box">
            <div class="ds-me-menus-bottom-line"></div>
            <div
              class="ds-me-menus-bottom-btn"
              v-if="changeGroup && changeGroup.dsfa_oua_group_id"
              @click="toGoaddress"
            >
              切换{{
                dsf.cookies.get("userGroupCode") == "001" ? "教职工" : "学员"
              }}
            </div>
            <div class="ds-me-menus-bottom-btn" @click="loginOut">退出登录</div>
          </div>
        </slot>
      </div>
    </div>
  </div>
</template>
<script>
export default dsf.component({
  name: "DsfMobileMe",
  ctrlCaption: "我的",
  mixins: [$mixins.control],
  props: {
    userInfoConfig: {
      type: Object,
      default: () => ({
        url: ":/dsfa/teas/oua/user/account/studentMsg",
        updatePhotoUrl: "/teas/mobile/student/updatePhoto",
      }),
    },
    menusConfig: {
      type: Object,
      default: () => ({
        url: "/teas/mobile/queryMyPageMenu",
        menus: [
          {
            id: "1",
            name: "个人信息",
            icon: "tab-addressBook-d",
            url: "1",
            subhead: "",
          },
          {
            id: "2",
            name: "修改密码",
            icon: "password",
            url: "1",
            subhead: "",
          },
          {
            id: "3",
            name: "我的班级",
            icon: "guidang",
            url: "1",
            subhead: "已参加0个班级",
          },
          {
            id: "4",
            name: "我的网课",
            icon: "shipinbofangyingpian",
            url: "1",
            subhead: "",
          },
          {
            id: "5",
            name: "我的收藏",
            icon: "shoucang",
            url: "1",
            subhead: "",
          },
          {
            id: "6",
            name: "我的订阅",
            icon: "iconfontzhizuobiaozhun13",
            url: "1",
            subhead: "",
          },
        ],
      }),
    },
    roleUrl: {
      type: String,
      default: ":/dsfa/common/group/getUserMobileGroup",
    },
  },
  data() {
    return {
      info: {},
      changeGroup: null,
    };
  },
  computed: {},
  beforeCreate() {},
  created() {
    this.getRole();
    if (dsf.isUnDef(this.menusConfig.menus)) {
      this.$set(this.menusConfig, "menus", []);
    }
    if (dsf.isUnDef(this.userInfoConfig.info)) {
      this.$set(this.userInfoConfig, "info", {});
    }
  },
  beforeMount() {},
  mounted() {},
  methods: {
    loginOut() {
      if (this.isDesign) return;
      dsf.layer
        .confirm("确定要退出吗?")
        .then(() => {
          this.$webAPI
            .loginOut({}, {}, $$webRoot.dsfa)
            .then(({ success, data, message }) => {
              if (success) {
                // app端
                if (!dsf.client.weixin() && window.$sharedPreferences) {
                  $sharedPreferences.clear(function () {});
                }
                // 默认逻辑
                dsf.cookies.remove("authorization_token");
                dsf.cookies.remove("user_name");
                dsf.cookies.remove("user_id");
                dsf.cookies.remove("isLogin");
                dsf.cookies.remove("allGroup");
                dsf.cookies.remove("userGroupCode");
                dsf.cookies.remove("class_name");
                dsf.cookies.remove("stuName");
                dsf.cookies.remove("stuClassBol");
                dsf.cookies.remove("class_id");
                dsf.cookies.remove("user_id");
                dsf.cookies.remove("stuId");
                dsf.cookies.remove("branchEnable");
                dsf.cookies.remove("user_name");
                dsf.cookies.remove("branch_id");
                dsf.cookies.remove("allGroup");
                dsf.storage.session.clear();
                let url = dsf.url.getMobileLogin();
                location.replace(url);
              } else {
                dsf.layer.message(message || "请求异常", false);
              }
            })
            .catch((err) => {
              dsf.layer.message(err.message || "请求异常", false);
            })
            .finally(() => {});
        })
        .catch(() => {});
    },
    getRole() {
      if (this.isDesign) return;
      this.changeGroup = "";
      this.$http
        .get(this.roleUrl)
        .then(({ data }) => {
          if (data && data.success && data.data) {
            this.changeGroup = data.data || null;
          }
        })
        .catch(() => {});
    },
    toGoaddress() {
      let { changeGroup, isDesign } = this;
      if (!changeGroup || !changeGroup.dsfa_oua_group_id || isDesign) return;
      this.$http
        .post(
          "/CommonLogin/changeGroup",
          {
            groupId: changeGroup.dsfa_oua_group_id,
            terminalType: 2,
          },
          $$webRoot.dsfa
        )
        .then(({ data }) => {
          if (data && data.success) {
            this.setStateIsLogin(data.data);
          }
        });
    },
    // 储存cookie登录状态
    async setStateIsLogin(data) {
      //通过后端返回的appId进入不同的app
      dsf.storage.session.clear();
      let groupGo = data.groupGo;
      let appId = this.$root.getAppId();
      let isStudent = dsf.getCookie("userGroupCode") == "001" ? 1 : 0;
      dsf.storage.local.set("logined", true, true);
      dsf.storage.local.set("userType", isStudent, true);
      let toUrl = "";
      let from = this.$route.query.from;
      if (groupGo) {
        let urlInfo = dsf.url.parse(groupGo);
        if (urlInfo.searchQuery["appid"]) {
          appId = urlInfo.searchQuery["appid"];
          delete urlInfo.searchQuery["appid"];
        }
        toUrl =
          (urlInfo.protocol ? urlInfo.origin : urlInfo.hostname) +
          urlInfo.pathname +
          (dsf.isEmptyObject(urlInfo.searchQuery)
            ? ""
            : "?" + dsf.url.queryStringStringify(urlInfo.searchQuery)) +
          urlInfo.hash;
      }
      toUrl = "";
      await this.$root.initApp(appId);
      if (from) {
        toUrl = decodeURIComponent(from);
      } else {
        if (!toUrl) {
          //获取当前页面地址
          toUrl = window.location.pathname;
          if (window.location.search) {
            toUrl += window.location.search;
          }
        }
        let toUrlInfo = dsf.url.parse(toUrl);
        if (!toUrlInfo.hash) {
          toUrl += "#" + this.$root.navs[0].href;
        }
        location.replace(toUrl);
      }
    },
  },
  beforeDestroy() {},
  destroyed() {},
});
</script>