<template>
  <div
    class="ds-control ds-label-box mobile"
    :style="{ height }"
    :class="getCss"
    @dblclick="beginEdit"
  >
    <label
      v-if="!isDesign"
      class="ds_label"
      :class="oneLine ? 'one-line' : ''"
      :title="value"
    >
      {{ value }}
    </label>
    <template v-else>
      <label v-if="!editting" class="ds_label">
        {{ value || "无内容" }}
      </label>
      <div
        v-else
        ref="contenteditable"
        class="contenteditable"
        style="display: block"
        contenteditable
        @blur="endEdit"
        v-text="value"
      ></div>
    </template>
  </div>
</template>

<script>
export default dsf.component({
  name: "DsfMobileLabel",
  mixins: [$mixins.text],
  inheritAttrs: false,
  ctrlCaption: "文本",
  props: {
    value: {
      type: [String, Number],
      default: "文本标签",
    },
    textAlign: {
      type: String,
      default: "",
    },
    height: {
      type: String,
      default: "auto",
    },
    oneLine: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      editting: false,
      $required: false,
    };
  },
  computed: {
    getCss() {
      const $class = this.$getClass();
      $class.push(
        this.textAlign,
        this.$required ? "ds_required" : ""
      );
      return $class;
    },
  },
  methods: {
    beginEdit() {
      if (this.isDesign) {
        this.editting = true;
        setTimeout(() => {
          this.$refs.contenteditable.focus();
        }, 0);
      }
    },
    endEdit(e) {
      if (this.isDesign) {
        this.editting = false;
        this.value = e.target.innerHTML;
      }
    },
  },
});
</script>