<template>
  <div class="ds-super-search-bt">
    <div class="content" @click="itemClick">
      <span>{{ item.fieldCaption }}</span>
      <i :class="['sort-bar', item.sortType]"></i>
    </div>
  </div>
</template>

<script>
// 排序
export default {
  name: "DsfSuperSearchBtSort",
  inject: {
    $list: {
      default: null
    },
    $superSearch: {
      default: null
    }
  },
  data() {
    return {};
  },
  props: {
    item: {
      type: Object,
      required: true
    }
  },
  methods: {
    itemClick() {
      if (this.$superSearch.isDesign) return;
      this.$superSearch.dropdownActive = null;
      let type;
      if (this.item.sortType === '') {
        type = 'desc';
      } else if (this.item.sortType === 'desc') {
        type = 'asc';
      } else {
        type = '';
      }
      this.$set(this.item, 'sortType', type);
      // 触发列表重新请求
      this.$superSearch.emitChange();
    }
  }
}
</script>