<template>
  <div class="ds-mobile-platform-card-item-by-channel">
    <template v-if="list.length > 0">
      <div class="swiper-container swiper-channel">
        <div class="swiper-wrapper">
          <div class="swiper-slide" v-for="(item, index) in list" :key="index" @click="_clickBefore(itemClick, item)">
            <van-image
              class="swiper-img"
              :show-loading="false"
              :src="item.cover | imgFormat"
              fit="cover"
            />
          </div>
        </div>
      </div>
      <div class="title">{{ avtiveTitle }}</div>
    </template>
    <van-empty v-else description="暂无数据" />
  </div>
</template>

<script>
export default dsf.component({
  name: "DsfMobilePlatformCardItemByChannel",
  ctrlCaption: "轮播卡片二",
  mixins: [$mixins.control],
  props: {
    // 获取轮播数据接口
    dataApi: {
      type: String,
      default:''
    },
    // 详情跳转链接
    detailUrl: {
      type: String,
      default:''
    },
    // 字段映射
    keysMap: {
      type: Array,
      default() {
        return [
          { from: 'cover', to: 'cover', type: 'text', desc: '封面' },
          { from: 'title', to: 'title', type: 'text', desc: '标题' }
        ];
      }
    },
    clickBefore: {
      type: Function,
      required: false
    }
  },
  data() {
    return {
      activeIndex: 0,
      swiperList: []
    }
  },
  computed: {
    avtiveTitle() {
      return this.swiperList[this.activeIndex]?.title
    },
    list({swiperList,keysMap,isDesign}) {
      if (isDesign) {
        return swiperList
      }
      return _.map(swiperList, s => {
        _(keysMap).forEach(({ to, from }) => {
          s[to] = s[from]
        });
        return s
      })
    }
  },
  mounted() {
    if (window.Swiper === undefined) {
      this.loadScript(() => {
        this.initSwiper()
      });
    } else {
      this.initSwiper()
    }
  },
  methods: {
    // 初始化swiper
    async initSwiper() {
      const _this = this
      if (!this.isDesign) {
        this.swiperList = []
        if (!this.dataApi) return
        try {
          await this.loadData()
        } catch (error) {
          dsf.error(error);
        }
      } else {
        this.swiperList = [
          {cover: 'https://cube.elemecdn.com/6/94/4d3ea53c084bad6931a56d5158a48jpeg.jpeg',title: '一带一路与中华文化海外传播01'},
          {cover: 'https://img01.yzcdn.cn/vant/apple-1.jpg',title: '一带一路与中华文化海外传播02'},
          {cover: 'https://img01.yzcdn.cn/vant/apple-2.jpg',title: '一带一路与中华文化海外传03'}
        ]
      }
      this.$nextTick(() => {
        this.mySwiper = new Swiper ('.swiper-channel', {
          loop: true, // 循环模式选项
          slidesPerView: "auto",
          centeredSlides: true,
          centeredSlidesBounds: true,
          on: {
            slideChange: function () {
              _this.activeIndex = this.realIndex
            },
          },
        })
      })
    },
    loadData() {
      return new Promise((resolve,reject) => {
        let loading = dsf.layer.loading();
        const api = this.$replace(this.dataApi, this);
        this.$http.get(api)
          .done(({success, data, message}) => {
            if (success) {
              this.swiperList = data
              resolve()
            } else {
              this.swiperList = []
              dsf.layer.message(message || '请求异常', false);
              reject()
            }
          })
          .error(err => {
            dsf.error(err);
            dsf.layer.message(err?.message || '请求异常', false);
            reject()
          }).always(() => {
            dsf.layer.closeLoading(loading);
          });
      })
    },
    _clickBefore(next, params) {
      if (this.isDesign) return;
      let res = this.clickBefore?.(this.local);
      if (res && res.then) {
        res.then(r => {
          if (r !== false) {
            next(params);
          }
        });
      } else if (res !== false) {
        next(params);
      }
    },
    // 点击图片
    itemClick(item) {
      if (this.$listeners.itemClick) {
        this.$dispath('itemClick', item)
      } else if (this.detailUrl){
        let url = this.$replace(this.detailUrl, item);
        window.location.href = dsf.url.getWebPath(url)
      }
    },
    // 加载js
    loadScript(callback) {
      this.$http
        .importFiles([
          [dsf.url.getWebPath("$/js/libs/swiper/swiper-bundle.min.css"),'.css'],
          [dsf.url.getWebPath("$/js/libs/swiper/swiper-bundle.min.js"),'.js']
        ])
        .then(() => {
          callback?.();
        })
        .catch(() => {
          dsf.layer.message("加载fabric文件报错", false);
          reject();
        });
    },
  }
});
</script>