<template>
  <div ref="container" class="ds-control mobile ds-banner" :class="{'ds-no-padding':!padding}" :style="swipeBoxStyle">
    <van-swipe
      v-if="reload"
      ref="swipe"
      class="banner-swipe"
      :loop="loop"
      :autoplay="autoPlay"
      :duration="duration"
      :style="swipeHeight"
      :height="height"
      :show-indicators="showIndicators"
      :vertical="vertical"
      :touchable="touchable"
      :indicator-color="indicatorColor"
      @change="(index) => { activeIndex = index }">
      <van-swipe-item @click="(ev) => onSwipeClick(ev, item)" v-for="(item, index) in images" :key="`ds-banner-item-${index}`">
        <slot name="default" v-bind="item">
          <div slot="default" class="ds-banner-item" :style="containerStyles">
            <van-image :src="item.image" :style="imgStyles" width="100%" :radius="pxToRem(borderRadius)" @dragstart.prevent.stop>
              <template v-slot:loading>
                <van-loading type="spinner" size="20" />
              </template>
              <template v-slot:error>
                暂无图片
              </template>
            </van-image>
            <!-- <img v-lazy="item.image" :style="imgStyles" @dragstart.prevent.stop /> -->
          </div>
        </slot>
      </van-swipe-item>
      <template v-if="showIndicators" #indicator>
        <div class="ds-custom-indicator">
          <div :class="['ds-custom-indicator-item', { active: activeIndex === index }]" v-for="(item, index) in data" :key="`ds-banner-item-${index}`" :style="{ backgroundColor: activeIndex === index ? indicatorColor : indicatorDefaultColor }">
          </div>
        </div>
      </template>
    </van-swipe>
    <template v-if="showButton">
      <div class="ds-banner-btn dbt-l" @click="prev">
        <i class="iconfont icon-zuo"></i>
      </div>
      <div class="ds-banner-btn dbt-r" @click="next">
        <i class="iconfont icon-weibiaoti34"></i>
      </div>
    </template>
  </div>
</template>
<script>
import bannerdemo1 from "../../../assets/styles/themes/img/banner-demo1.png";
import bannerdemo2 from "../../../assets/styles/themes/img/banner-demo2.png";
export default dsf.component({
  name: "DsfMobileBanner",
  mixins: [$mixins.control],
  ctrlCaption: "轮播图",
  props: {
    height: {
      type: String,
      default: "150px"
    },
    list: {
      type: Array,
      default() {
        return [{ image: bannerdemo1 }, { image: bannerdemo2 }];
      }
    },
    url: {
      type: String,
      default: ""
    },
    showButton: {
      type: Boolean,
      default: true
    },
    showIndicators: {
      type: Boolean,
      default: true
    },
    padding: {
      type: Boolean,
      default: false
    },
    vertical: {
      type: Boolean,
      default: false
    },
    touchable: {
      type: Boolean,
      default: true
    },
    //为空默认#E8E8E8
    indicatorDefaultColor: {
      type: String,
      default: ""
    },
    //为空默认使用皮肤辅色
    indicatorColor: {
      type: String,
      default: ""
    },
    autoPlay: {
      type: [String, Number],
      default: 3000
    },
    loop: {
      type: Boolean,
      default: true
    },
    duration: {
      type: Number,
      default: 500
    },
    borderRadius: {
      type: [String, Number],
      default: "0px"
    },
    toUrl: {
      type: String,
      default: ""
    },
    max: {
      type: Number,
      default: 0
    },
    // 配置字段名
    imageKeyName: {
      type: String,
      default: 'image'
    },
    titleKeyName: {
      type: String,
      default: 'title'
    },
    descKeyName: {
      type: String,
      default: 'desc'
    }
  },
  computed: {
    swipeBoxStyle() {
      return {
        borderRadius: this.pxToRem(this.borderRadius),
        overflow: "hidden",
        height: this.pxToRem(this.height)
      };
    },
    containerStyles() {
      return {
        height: this.pxToRem(this.height)
      };
    },
    imgStyles() {
      return {
        ...this.containerStyles
      };
    },
    swipeHeight() {
      if (this.vertical) {
        return {
          height: this.pxToRem(this.height)
        };
      }
      return {};
    },
    swipeProps() {
      return {
        loop: this.loop
      };
    },
    images() {
      let data = this.data;
      let max = this.max;
      if (max) {
        return (data || []).slice(0, max);
      } else {
        return data;
      }
    }
  },
  data() {
    return {
      data: [],
      activeIndex: 0,
      reload: true
    };
  },
  activated() {
    if (this.$refs.swipe) {
      this.$refs.swipe.swipeTo(this.activeIndex);
    }
  },
  mounted() {
    // window.addEventListener('resize', this.resize);
  },
  created() {
    if (this.isDesign) {
      let data = [];
      if (!this.list.length) {
        data = [{ image: "" }, { image: "" }];
      } else {
        data = this.list;
      }
      this.data = dsf.mix(true, [], data);
    } else {
      if (!this.url) {
        this.data = dsf.mix(true, [], this.list);
      } else {
        this.loadData();
      }
    }
  },
  destroyed() {
    // window.removeEventListener('resize', this.resize);
  },
  methods: {
    resize() {
      this.$refs.swipe.resize();
    },
    prev() {
      this.$refs.swipe.prev();
    },
    next() {
      this.$refs.swipe.next();
    },
    validateApiFiled(data) {
      if (!Array.isArray(data.data)) {
        console.error("数据源必须为数组");
      } else {
        const flag = data.data.every((it) => {
          return Object.prototype.toString.call(it) === "[object Object]" && Object.prototype.hasOwnProperty.call(it, "image");
        });
        if (!flag) {
          console.error(`
            接口数据格式请参考: \n
            [{\n
              image: '',\n
              title: '',\n
              desc: ''\n
            }]
          `);
        }
      }
    },
    formatApiData(data) {
      if (!Array.isArray(data)) {
        return [];
      }
      return data.map((it) => {
        return {
          url: "",
          ...it,
          image: dsf.getFilePath(it[this.imageKeyName]),
          title: it[this.titleKeyName],
          desc: it[this.descKeyName],
        };
      });
    },
    loadData() {
      let url = this.$replace(this.url, this);
      this.$http.get(url).then(({ data }) => {
        this.data = this.formatApiData(data.data);
      });
    },
    onSwipeClick(ev, item) {
      //如果二次开发注入锅click事件则无视配置执行二次开发事件
      if (this.$listeners["click"]) {
        this.$dispatch("click", item);
      } else {
        //如果数据中有跳转地址以数据中的优先
        if (item.url) {
          this.$open({ url: item.url });
        } else if (this.toUrl) {
          let url = this.$replace(this.toUrl, item);
          this.$router.push({
            path: dsf.url.toRoutePath(url)
          });
        }
      }
    }
  },
  watch: {
    list(v, ov) {
      if (!this.url) {
        this.data = dsf.mix(true, [], v);
      }
    },
    vertical(v, ov) {
      this.reload = false;
      this.$nextTick(() => {
        this.reload = true;
      });
    }
  }
});
</script>