<template>
  <div class="ds-super-search-form-select">
    <div
      v-for="it in (item.options || [])"
      :key="it.value"
      class="ds-super-search-form-select-item van-hairline--bottom"
      :class="{active: value === it.value}"
      @click="itemClick(it)"
    >
      {{ it.text }}
      <i class="iconfont icon-duigou"></i>
    </div>
  </div>
</template>

<script>
export default {
  name: "DsfSuperSearchFormItemSelect",
  props: {
    isDesign: {
      type: Boolean,
      default: false,
    },
    values: {
      type: [String, Array, Object],
      default() {
        return []
      },
    },
    item: {
      type: Object,
      default() {
        return {};
      }
    },
  },
  data() {
    return {
      value: null
    }
  },
  watch: {
    values: {
      handler(v) {
        if (dsf.type(v) == "array") {
          this.value = v.length ? v[0] : null;
        } else {
          this.value = v;
        }
      },
      immediate: true,
    },
  },
  methods: {
    itemClick(it) {
      if (this.value === it.value) {
        this.value = null;
      } else {
        this.value = it.value;
      }
      this.$emit("update:values", this.value);
      this.$nextTick(() => {
        this.$emit("change", this.value);
      });
    },
  }
}
</script>