
//vant需要在此import一下他的css,因为vant的样式都是以px作为单位，需要再次引入进行一次转rem的编译
import { Lazyload } from 'vant';
import 'vant/lib/index.css';
//注册pc端按钮
import "./sysButton";

import bhc from "./bhc";
import home from './home'
import me from './me'
import other from './other'

//注入平台全局过滤器
import filters from "../filters";
//注入平台全局自定义指令
import directives from "../directives";

import mobileDirectives from "./directives";
//用于加载配置完成的页面,预览页面
import output from "../output";

import login from "./login"


function install(Vue) {
  Vue.use(bhc);
  Vue.use(home);
  Vue.use(me);
  Vue.use(other);
  Vue.use(filters);
  Vue.use(directives);
  Vue.use(output);
  Vue.use(mobileDirectives);
  Vue.use(login);
  //注入vant的懒加载组件
  Vue.use(Lazyload, {
    lazyComponent: true
  })
}

export default install;

export {
  install,
  bhc,
  home,
  filters,
  directives,
  output,
  mobileDirectives
};
