<template>
  <dsf-mobile-tree-selector
    ref="selector"
    :max-tags="multiple ? 9999 : 1"
    :data="item.options"
    data-source="6"
  />
</template>

<script>
export default {
  name: "DsfSuperSearchDownItemTree",
  props: {
    values: {
      type: String,
      default: '',
    },
    item: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  // data() {
  //   return {
  //     value: ''
  //   }
  // },
  computed: {
    multiple() {
      return this.item.isAllowMultipe;
    }
  },
  // watch: {
  //   values: {
  //     handler(v) {
  //       if (this.multiple) {
  //         if (dsf.type(v) == "array") {
  //           this.value = v;
  //         } else if (!v) {
  //           this.value = [v];
  //         } else {
  //           this.value = [];
  //         }
  //       } else if (dsf.type(v) == "array") {
  //         this.value = v.length ? [v[0]] : [];
  //       } else {
  //         this.value = v ? [v] : [];
  //       }
  //     },
  //     immediate: true,
  //   },
  // },
  methods: {
    reset() {
      this.$refs.selector.result = [];
      this.value = [];
      let v = this.multiple ? [] : null;
      this.$emit("update:values", v);
      this.$nextTick(() => {
        this.$emit("change", v);
      });
    },
    sure() {
      let v = _.map(this.$refs.selector.result, '_id');
      this.$emit("update:values", v);
      this.$nextTick(() => {
        this.$emit("change", v);
      });
    }
  }
}
</script>