import { DsfMobileAddress, DsfMobileUnitAddress } from './address'
import { DsfMobileAdvancedSearch } from './search';
import { DsfMobilePendingList } from './pending';




function install(Vue) {
  Vue.component(DsfMobileAdvancedSearch.name, DsfMobileAdvancedSearch);
  Vue.component(DsfMobileUnitAddress.name, DsfMobileUnitAddress);
  Vue.component(DsfMobileAddress.name, DsfMobileAddress);
  Vue.component(DsfMobilePendingList.name, DsfMobilePendingList);
}

export default {
  install
};

export {
  install,
  DsfMobileAdvancedSearch
};
