<template>
  <div class="ds-control ds-page mobile" :class="getCss" :style="pageStyle">
    <!--页头-->
    <div class="ds-page-head ds-page-head-color" ref="head">
      <div v-if="hasHeader" ref="headInner" class="head-inner" :slot-name="slots[1].name">
        <slot :name="slots[1].name"></slot>
      </div>
    </div>
    <!--正文主体-->
    <div class="ds-page-content" :style="[pageContentStyle, getBackgroundStyle]">
      <div v-mobile-scroll class="ds-page-center" :style="conentStyle" :class="pageCenterCss" :slot-name="slots[0].name">
        <!--页面banner背景风格设置，正文部分 start-->
        <template v-if="styles=='circular-color-block'">
          <div class="styles-circular-color-block content">
            <div class="page-banner"></div>
          </div>
        </template>
        <!--页面banner背景风格设置，正文部分 end-->
        <slot :name="slots[0].name">
        </slot>
      </div>
    </div>
    <!--页脚-->
    <div ref="foot" class="ds-page-foot ds-page-foot-color" :class="{'bottom-safe-area':($bottomSafeArea)}" :style="{'background-color':footerBackground}">
      <div v-if="hasFooter" ref="footInner" :style="footInnerStyle" class="foot-inner" :slot-name="slots[2].name">
        <slot :name="slots[2].name"></slot>
      </div>
    </div>
    <div style="display:none" v-html="customCss"></div>
    <a v-if="!isDesign&&gotoIcon" ref="eleDesgin" v-drag data-flag="true" :title="`进入${title}设计模式`" class="to_desgin" @click="gotoPreview">
      <i class="iconfont icon-shezhi"></i>
    </a>
  </div>
</template>
<script>
export default dsf.component({
  name: "DsfMobilePage",
  ctrlCaption: "移动端页面",
  mixins: [$mixins.view, $mixins.vDrag],
  data() {
    return {
      gotoIcon: (dsf.config.setting_public_gotoicon || dsf.config.setting_public_gotoIcon) == "1",
      headHeight: 0,
      footHeight: 0,
      footInnerHeight: 0,
      headInnerHeight: 0,
      topSafeAreaHeight: 0,
      bottomSafeAreaHeight: 0,
      centerHeight: "auto",
      headerStyle: {},
      footerStyle: {},
      leftAndRIghtStyle: {},
      containerHeight: 0,
      containerWidth: 0,
      isShowNavsBar: this?.$app?.isShowNavsBar
    };
  },
  props: {
    formItemLabelWidth: {
      type: Number,
      default: 120
    },
    slots: {
      type: Array,
      default() {
        return [
          { name: "center", controls: [] },
          { name: "head", controls: [] },
          { name: "foot", controls: [] }
          // { name: "left", controls: [] },
          // { name: "right", controls: [] }
        ];
      }
    },
    pageLevel: {
      type: Number,
      default: 1
    },
    isKeepAlive: {
      type: Boolean,
      default: false
    },
    topSafeArea: {
      type: Boolean,
      default: true
    },
    bottomSafeArea: {
      type: Boolean,
      default: true
    },
    contentPadding: {
      type: Array,
      default: function () {
        return ["0px", "0px", "0px", "0px"];
      }
    },
    //顶部圆弧色块：circular-color-block
    styles: {
      type: String,
      default: ""
    }
  },
  beforeCreate() {
    // console.time("加载页面");
  },
  computed: {
    $bottomSafeArea() {
      let isDesign = this.isDesign;
      let footInnerHeight = this.footInnerHeight;
      if (!isDesign) {
        let hasNavsBar = this.isShowNavsBar;
        let bottomSafeArea = this.bottomSafeArea;
        let result = hasNavsBar ? false : bottomSafeArea;
        //如果页脚有内容且页面为底部适配为true时
        if (footInnerHeight > 1 && result) {
          return true;
        }
      }
      return false;
    },
    $topSafeArea() {
      let isDesign = this.isDesign;
      // let footInnerHeight = this.footInnerHeight;
      if (!isDesign) {
        return true;
        // let hasNavsBar = this.isShowNavsBar;
        // let bottomSafeArea = this.bottomSafeArea;
        // let result = hasNavsBar ? false : bottomSafeArea;
        // //如果页脚有内容且页面为底部适配为true时
        // if (footInnerHeight > 1 && result) {
        //   return true;
        // }
      }
      return false;
    },
    getCss() {
      return [...this.$getClass(), this.fit === "1" || this.fit === true ? "fit" : ""];
    },
    pageStyle() {
      var headHeight = this.headHeight;
      var footHeight = this.footHeight;
      if (!this.isDesign) {
        headHeight = headHeight;
        footHeight = footHeight || 0;
      }
      return {
        "padding-top": headHeight + "px",
        "padding-bottom": footHeight + "px"
      };
    },
    pageContentStyle() {
      return {
        width: this.isDesign ? "auto" : this.pageWidth == "100%" || !this.pageWidth ? "auto" : this.pageWidth,
        margin: "auto"
      };
    },
    conentStyle() {
      return {
        "padding-left": this.pxToRem(this.contentPadding[3]),
        "padding-right": this.pxToRem(this.contentPadding[1]),
        "padding-top": this.pxToRem(this.contentPadding[0]),
        "padding-bottom": this.pxToRem(this.contentPadding[2])
      };
    },
    pageCenterCss() {
      return [this.isBannerBackground ? "banner-background" : "", !this.isDesign ? this.splitWidthHorizontal : ""];
    },
    hasHeadInner() {
      return this.headInnerHeight > 1;
    },
    hasFootInner() {
      return this.footInnerHeight > 1;
    },
    footInnerStyle() {
      return {
        visibility: this.hasFootInner ? "visible" : "hidden"
      };
    }
  },
  created() {},
  destroyed() {},
  beforeDestroy() {
    if (this.$refs.eleDesgin) {
      this.$refs.eleDesgin.parentNode && this.$refs.eleDesgin.parentNode.removeChild(this.$refs.eleDesgin);
    }
  },
  beforeMount() {
    // debugger;
    // console.log(this.$scopedSlots)
  },
  mounted() {
    // console.log(this.$scopedSlots)
    // console.timeEnd("加载页面");
    if (this.isDesign) {
      this.watchCenterHeight();
      this.resize();
    }
    if (this.$refs.eleDesgin) {
      document.body.append(this.$refs.eleDesgin);
    }
  },
  updated() {
    // console.log("dfdfdf")
  },
  methods: {
    watchCenterHeight() {
      // this.resize();
      this.setCenterHeight();
      window.setTimeout(this.watchCenterHeight, 500);
    },
    setCenterHeight() {
      let head = this.$refs.head ? this.$refs.head : null;
      let foot = this.$refs.foot ? this.$refs.foot : null;
      let footInner = this.$refs.footInner ? this.$refs.footInner : null;
      let headInner = this.$refs.headInner ? this.$refs.headInner : null;
      // let bottomSafeArea = this.$refs.bottomSafeArea ? this.$refs.bottomSafeArea : null
      this.headHeight = head ? (head.offsetHeight > 1 ? head.offsetHeight : 0) : 0;
      this.footHeight = foot ? (foot.offsetHeight > 1 ? foot.offsetHeight : 0) : 0;
      this.footInnerHeight = footInner ? (footInner.offsetHeight > 1 ? footInner.offsetHeight : 0) : 0;
      this.headInnerHeight = headInner ? (headInner.offsetHeight > 1 ? headInner.offsetHeight : 0) : 0;
      // this.bottomSafeAreaHeight = bottomSafeArea ? bottomSafeArea.offsetHeight : 0;
    },
    //设置容器高度
    setContainerSize() {
      if (this.$el) {
        let container = $(this.$el.parentNode);
        this.containerHeight = container.innerHeight();
        this.containerWidth = container.innerWidth();
      }
    },
    resize() {
      this.setContainerSize();
      this.setCenterHeight();
    },
    gotoPreview() {
      let isDrag = this.$refs.eleDesgin.getAttribute("data-flag");
      if (isDrag === "true") {
        let previewUrl = `${dsf.url.getWebPath("~/designer.html#/mobile/page?B=" + this.context.B + "&M=" + this.context.M + "&pname=" + this.name + "&title=" + this.title)}`;
        window.open(previewUrl);
      }
    }
  }
});
</script>