<template>
  <div class="ds-control ds-form-item ds-checkBox mobile" :class="getCss">
    <template v-if="simple && readOnly && !isDesign">
      <DsfTextProxy v-model="value" v-bind="_self.$props"></DsfTextProxy>
    </template>
    <template v-else-if="readOnly && !isDesign">
      <van-field
        :label-width="getLabelWidth(true)"
        :label="(showLabel && label) || ''"
        :disabled="disabled"
        :readonly="readOnly"
        :error-message="errorsMsg"
        :error-message-align="inputAlign"
        input-align="right">
        <template #input>
          <DsfTextProxy v-model="value" v-bind="_self.$props" :show-label="false"></DsfTextProxy>
        </template>
      </van-field>
    </template>
    <template v-else>
      <van-field
        :label-width="getLabelWidth(true)"
        :label="(showLabel && label) || ''"
        :input-align="inputAlign"
        :error-message="errorsMsg"
        :error-message-align="inputAlign"
        :disabled="disabled">
        <template #input>
          <van-checkbox-group
            v-model="radioValue"
            :direction="direction"
            :icon-size="iconSize"
            :readonly="readOnly"
            :disabled="disabled"
            :max="maxChooseNum < 0 ? 0 : maxChooseNum"
            @change="change"
          >
            <van-checkbox
              v-for="(item, index) in $items"
              :key="item.value"
              :name="item.value + '_._' + index"
              :shape="shape"
              :label-position="labelPosition"
              :disabled="item.disabled || disabledItemValues.indexOf(item.value) > -1"
            >
              {{ item.text }}
            </van-checkbox>
          </van-checkbox-group>
        </template>
      </van-field>
    </template>
  </div>
</template>

<script>
export default dsf.component({
  name: "DsfMobileCheckBox",
  mixins: [$mixins.formControl],
  ctrlCaption: "复选框",
  props: {
    value: {
      type: [Array, String, Number, Boolean, Object],
      default() {
        return [];
      },
    },
    items: {
      type: Array,
      default() {
        return [];
      },
    },
    // 是否可取消
    cancel: {
      type: Boolean,
      default: false,
    },
    // 是否其他
    isOther: {
      type: Boolean,
      default: false,
    },
    formatter: {
      type: String,
      default: "textValue^text",
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("items-meta-data");
      },
    },
    // 文字对齐方式
    inputAlign: {
      type: String,
      default: "right",
    },
    // 排列方式：水平(horizontal)，垂直(vertical)
    direction: {
      type: String,
      default: "horizontal",
    },
    // 图标大小，默认单位为px
    iconSize: {
      type: String,
      default: "1.43em",
    },
    // 图标形状：方形(square)，圆形(round)
    shape: {
      type: String,
      default: "square",
    },
    // 文本位置
    labelPosition: {
      type: String,
      default: "right",
    },
    maxChooseNum: {
      type: Number,
      default: 0,
    },
    // 需要禁用选项的value值得集合
    disabledItemValues: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  data() {
    return {
      result: [],
    };
  },
  computed: {
    $items() {
      let res = this.items || [];
      if (this.isDesign && !res.length) {
        res = [
          { value: "1", text: "数据1" },
          { value: "2", text: "数据2" },
        ];
      }
      if (this.isOther) {
        return [...res, { value: "else", text: "其他" }];
      }
      return res;
    },
    radioValue: {
      get() {
        return this.result;
      },
      set(to) {
        if (!to) to = [];
        this.result = to;
        let value = [];
        to.forEach((v) => {
          let index = v.split("_._");
          if (index.length == 2) {
            index = parseInt(index[1]);
            value.push(this.$items[index]);
          }
        });
        this.emitValueChange(value);
        this.$nextTick(() => {
          this.emitFormValidate(value);
        });
      },
    },
  },
  watch: {
    value(to) {
      this.initValue(to);
    },
  },
  created() {
    this.initValue(this.value);
  },
  methods: {
    initValue(v) {
      if (this.simple || this.readOnly || this.isDesign) {
        return;
      }
      let type = dsf.type(v);
      if (type == "object") {
        v = v.value;
        type = "string";
      }
      if (dsf.isUnDef(v)) {
        this.radioValue = [];
        return;
      }
      if (type == "string" || type == "number" || type == "boolean") {
        let index = this.$items.findIndex((item) => item.value == v);
        if (index == -1) {
          this.radioValue = [];
        } else {
          this.radioValue = [this.$items[index].value + "_._" + index];
        }
      } else if (type == "array") {
        let res = [];
        v.forEach(({ value }) => {
          if (value) {
            let index = this.$items.findIndex((item) => item.value == value);
            res.push(value + "_._" + index);
          }
        });
        if (!_.isEqual(res, this.result)) {
          this.radioValue = res;
        }
      } else {
        this.radioValue = [];
      }
    },
    change() {
      this.$dispatch("change", this.value);
    },
  },
});
</script>
