<template>
  <!-- <van-picker ref="picker" :columns="lines" value-key="Name" title="标题" /> -->
  <div class="choose-send-node mobile">
    <header class="header">
      <span>{{ title }}</span>
      <span @click="close" class="cancel">取消</span>
    </header>
    <van-list>
      <van-cell v-for="item in lines" :key="item.LineID" :title="item.Name" @click="chooseNode(item)" />
    </van-list>
    <footer v-if="showBack" class="footer">
      <DsfMobileButton class="ds-button-bar-item" :title="'返回'" @click="back"> 返回 </DsfMobileButton>
    </footer>
  </div>
</template>
<script>
export default dsf.component({
  name: "DsfMobileChooseSendNode",
  props: {
    title: {
      type: String,
      default() {
        return "";
      },
    },
    lines: {
      type: Array,
      default() {
        return [];
      },
    },
    chooseNode: {
      type: Function,
      default() {
        return () => {};
      },
    },
    showBack: {
      type: Boolean,
      default: false,
    },
    back: {
      type: Function,
      default() {
        return () => {};
      },
    },
  },
  data() {
    return {};
  },
  created() {},
  mounted() {
    console.log(this);
  },
  methods: {
    yes() {
      return this.$refs.picker.getValues()[0];
    },
    validate() {
      if (!this.$refs.picker.getValues()[0]) {
        dsf.layer.message("请选择要发送的环节", false);
        return false;
      }
      return true;
    },
    close() {
      this.$parent.close();
    },
  },
});
</script>
