<template>
  <div class="ds-control ds-nav-menu-cell mobile" :class="rootClass">
    <template v-for="(row, index) in data">
      <div :key="row._id" class="ds-nav-menu-cell-item" @click="_clickBefore(onClick, row, index)">
        <div class="ds-nav-menu-cell-item-left">
          <slot :name="'row-left-' + row._id" :row="row" :$index="index">
            <slot :name="'index-left-' + index" :row="row" :$index="index">
              <slot name="left" :row="row" :$index="index">
                <div class="ds-nav-menu-cell-item-left-icon" :style="itemIconBoxStyle">
                  <van-image
                    :src="getImgUrl(row) | imgFormat"
                    :style="[itemBgStyle, getItemStyle(row)]">
                    <template v-slot:error>
                      <i :class="['iconfont', getIcon(row.icon)]" :style="[itemIconStyle, getIconStyle(row)]" />
                    </template>
                  </van-image>
                  <div v-if="row[badgeKey]" class="ds-nav-menu-cell-item-left-badge" :style="badgeStyle">{{ badgeText(row) }}</div>
                </div>
              </slot>
            </slot>
          </slot>
        </div>
        <div class="ds-nav-menu-cell-item-content van-hairline--bottom">
          <div class="ds-nav-menu-cell-item-center">
            <slot :name="'row-' + row._id" :row="row" :$index="index">
              <slot :name="'index-' + index" :row="row" :$index="index">
                <slot name="default" :row="row" :$index="index">
                  <div class="ds-nav-menu-cell-item-row">
                    <div class="ds-nav-menu-cell-item-title ds-multi-ellipsis-1">{{ row[titleKey] }}</div>
                    <div v-if="row[timeKey] && row[timeKey].length > 0" class="ds-nav-menu-cell-item-time">{{ row[timeKey] }}</div>
                  </div>
                  <slot :name="'row-subtitle-' + row._id" :row="row" :$index="index">
                    <slot :name="'index-subtitle-' + index" :row="row" :$index="index">
                      <div v-if="subtitleKey != ''" class="ds-nav-menu-cell-item-subtitle" :class="['ds-multi-ellipsis-' + subtitleLineNum]">
                        {{ row[_subtitleKey] }}
                      </div>
                    </slot>
                  </slot>
                  <div v-if="row.exts && row.exts.length" class="ds-nav-menu-cell-item-row exts">
                    <div v-for="(exts, i) in row.exts" :key="i">{{ exts.text }}: {{ exts.value }}</div>
                  </div>
                </slot>
              </slot>
            </slot>
          </div>
          <div class="ds-nav-menu-cell-item-right">
            <slot :name="'row-right-' + row._id" :row="row" :$index="index">
              <slot :name="'index-right-' + index" :row="row" :$index="index">
                <slot name="right" :row="row" :$index="index">
                  <i v-if="rightIcon != ''" class="ds-nav-menu-cell-item-right-icon iconfont" :class="[getIcon(rightIcon)]" :style="{height: menuBorderSize + 'rem'}" />
                </slot>
              </slot>
            </slot>
          </div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
const defaultData = [
  {
    _id: 1,
    picture: 'https://img.yzcdn.cn/vant/cat.jpeg',
    icon: 'icon-location-solid',
    color: '',
    foreColor: '',
    opentype: '1',
    badge: '99+',
    title: '这里是标题',
    subtitle: '这里是描述，这里是描述，这里是描述，这里是描述，这里是描述，这里是描述',
    time: '10月1号',
    url: '',
    exts: [
      {text: '已评价', value: '45人'},
      {text: '未评价', value: '4人'},
      {text: '请假', value: '1人'}
    ]
  },
  {
    _id: 2,
    picture: '',
    icon: 'icon-xiaolianmanyifuwu',
    color: '',
    foreColor: '',
    opentype: '1',
    badge: true,
    title: '这里是标题这里是标题这里是标题这里是标题',
    subtitle: '这里是描述，这里是描述',
    time: '10月1号',
    url: ''
  }
];

export default dsf.component({
  name: "DsfMobileNavMenuCell",
  mixins: [$mixins.control],
  ctrlCaption: "列导航菜单",
  design: {
    isMask: false
  },
  props: {
    // 标题字段名
    titleKey: {
      type: String,
      default: 'title'
    },
    // 时间字段名
    timeKey: {
      type: String,
      default: 'time'
    },
    // 描述文字字段名
    subtitleKey: {
      type: String,
      default: 'subtitle'
    },
    // 角标字段名
    badgeKey: {
      type: String,
      default: 'badge'
    },
    // 描述性文字占几行
    subtitleLineNum: {
      type: Number,
      default: 2
    },
    // 右侧图标
    rightIcon: {
      type: String,
      default: 'icon-arrow-right'
    },
    // 是否强制使用默认样式
    useDefaultStyle: {
      type: Boolean,
      default: false
    },
    // 数据请求地址
    dataSourceUrl: {
      type: String,
      default: ''
    },
    /*****图标默认样式*****/
    // 展示优先级。img图片优先，icon图标优先
    showPriority: {
      type: String,
      default: 'img'
    },
    menuHasBg: {
      type: Boolean,
      default: true
    },
    menuColor: {
      type: String,
      default: 'theme-linear'
    },
    menuBgShadow: {
      type: Boolean,
      default: true
    },
    menuForeColor: {
      type: String,
      default: '#fff'
    },
    menuShadow: {
      type: Boolean,
      default: true
    },
    menuBorderRadius: {
      type: Number,
      default: .1
    },
    menuBorderSize: {
      type: Number,
      default: .9
    },
    menuIconSize: {
      type: Number,
      default: .55
    },
    /*****角标样式*****/
    // 展示方式
    badgeShowType: {
      type: String,
      default: 'text'
    },
    // 角标颜色
    badgeColor: {
      type: String,
      default: 'red'
    },
    // 二次开发传入的方法。
    itemClick: {
      type: Function,
      required: false
    },
    clickBefore: {
      type: Function,
      required: false
    }
  },
  data() {
    return {
      // 请求状态
      loading: false,
      isLoading: true,
      data: []
    }
  },
  computed: {
    rootClass() {
      const res = [];
      if (this.menuHasBg) {
        res.push('hasBg')
        if (this.menuColor == 'theme') {
          res.push('bgTheme')
        } else if (this.menuColor == 'theme-linear') {
          res.push('bgThemeLinear')
        }
      }
      if (this.menuBgShadow) {
        res.push('bgShadow')
      }
      if (this.menuShadow) {
        res.push('iconShadow')
      }
      if (this.menuForeColor == 'theme') {
        res.push('iconTheme')
      } else if (this.menuForeColor == 'theme-linear') {
        res.push('iconThemeLinear')
      }
      return res;
    },
    itemIconBoxStyle() {
      return {
        'width': this.menuBorderSize + 'rem',
        'height': this.menuBorderSize + 'rem'
      };
    },
    itemBgStyle() {
      let res = {
        'border-radius': this.menuBorderRadius + 'rem'
      };
      if (this.menuHasBg && this.menuColor != 'theme' && this.menuColor != 'theme-linear') {
        res['background-color'] = this.menuColor;
      }
      return res;
    },
    itemIconStyle() {
      let res = {
        'font-size': this.menuIconSize + 'rem'
      };
      if (this.menuForeColor != 'theme' && this.menuForeColor != 'theme-linear') {
        res['color'] = this.menuForeColor;
      }
      return res;
    },
    badgeStyle() {
      let r = this.menuBorderRadius;
      if (r > this.menuBorderSize / 2) {
        r = this.menuBorderSize / 2;
      }
      r = r / (4 * Math.sin(.5 * Math.PI));
      let res = {
        top: r + 'rem',
        right: r + 'rem'
      };
      if (this.badgeColor == 'theme') {
        res['background-color'] = this.$root.currentTheme.main;
      }
      return res;
    },
    _subtitleKey() {
      return this.isDesign ? 'subtitle' : this.subtitleKey;
    },
  },
  watch: {
    dataSourceUrl() {
      this.init();
    }
  },
  created() {
    if (this.isDesign) {
      this.data = defaultData;
    } else {
      this.init();
    }
  },
  methods: {
    reloadData() {
      this.init();
    },
    init() {
      if (this.isDesign) {
        return;
      }
      if (this.dataSourceUrl == '') {
        this.data = [];
        return;
      }
      this.loading = true;
      let dataSourceUrl = this.$replace(this.dataSourceUrl);
      this.$http.get(dataSourceUrl).done(({success, data, message}) => {
        if (success) {
          this.data = data;
          this.loading = false;
        } else {
          dsf.layer.message(message, false);
        }
      }).error(err => {
        dsf.layer.message(err.message, false);
      }).always(() => {
        this.isLoading = false;
      });
    },
    _clickBefore(next, ...params) {
      if (this.isDesign) return;
      let res = this.clickBefore?.(...params);
      if (res && res.then) {
        res.then(r => {
          if (r !== false) {
            next(...params);
          }
        });
      } else if (res !== false) {
        next(...params);
      }
    },
    onClick(row, index) {
      if (this.itemClick) {
        let res = this.itemClick(row, index);
        if (res === false) return;
        if (res.then) {
          res.then(r => {
            if (r !== false) {
              this.openPage(row);
            }
          });
          return;
        }
      }
      this.openPage(row);
    },
    openPage(menu) {
      let url = menu.url;
      if (!url || url == '') {
        return;
      }
      if (url.indexOf('#') == 0 && url.indexOf('#/') != 0) {
        url = url.replace('#', '#/');
      }
      this.$open({
        url,
        state: menu.opentype
      });
    },
    badgeText(row) {
      let text = row[this.badgeKey];
      return text !== true && this.badgeShowType == 'text' ? text : ''
    },
    getIcon(icon) {
      if (!icon || icon == '') return '';
      return !icon.indexOf('icon-') ? icon : 'icon-' + icon;
    },
    getImgUrl({ picture, icon }) {
      if (this.showPriority == 'img') {
        return picture;
      }
      if (!icon || icon == '') {
        return picture;
      }
      return '';
    },
    getItemStyle({ color }) {
      if (this.useDefaultStyle || !this.menuHasBg) {
        return {};
      }
      if (color && color != '') {
        return {
          'background': color
        }
      }
      return {};
    },
    getIconStyle({ foreColor }) {
      if (this.useDefaultStyle) {
        return {};
      }
      if (foreColor && foreColor != '') {
        return {
          'color': foreColor,
          'background': 'transparent',
          '-webkit-background-clip': 'inherit',
          '-webkit-text-fill-color': 'inherit'
        }
      }
      return {};
    },
  }
});
</script>