<template>
  <div class="ds-control mobile ds_address" :class="{'ds-no-padding':!isDesign}" v-mobile-loading="loading">
    <van-search ref="search" v-model="searchStr" :placeholder="searchPlaceholder" @search="search">
      <template v-slot:right-icon>
        <dsf-icon :name="searchRightIcon" />
      </template>
    </van-search>
    <div class="address-b" v-mobile-scroll>
      <van-index-bar :index-list="indexList">
        <div v-for="item in indexList" :key="item" class="address-item">
          <van-index-anchor :index="item">{{item}}</van-index-anchor>
          <UserItem v-for="it in list[item]" :key="getUserKey(it)" :user="it"></UserItem>
        </div>
      </van-index-bar>
    </div>
  </div>
</template>

<script>
import UserItem from "./userItem";

export default dsf.component({
  name: "DsfMobileAddress",
  ctrlCaption: "通讯录",
  mixins: [$mixins.control],
  props: {
    sourcesUrl: {
      type: String,
      default: "teas/addresslist"
    },
    /*********搜索框样式***********/
    // 搜索框圆角：shape圆形，默认矩形
    searchRadius: {
      type: String,
      default: "6px"
    },
    // 搜索框提示语
    searchPlaceholder: {
      type: String,
      default: "请输入搜索内容"
    },
    // 搜索框右侧图标
    searchRightIcon: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      allList: [],
      list: {},
      indexList: [],
      searchStr: "",
      loading: false,
      timer: null
    };
  },

  components: {
    UserItem
  },

  computed: {},
  watch: {},

  mounted() {
    let $search = this.$refs.search;
    if ($search) {
      let $searchBox = $search.querySelector(".van-search__content");
      $searchBox.style.borderRadius = this.pxToRem(this.searchRadius);
      this.unwatch = this.$watch(
        () => {
          return this.pxToRem(this.searchRadius);
        },
        (v) => {
          $searchBox.style.borderRadius = v;
        }
      );
    }
  },

  created() {
    if (this.isDesign || this.$route.name == "DsfLayoutPreView") {
      this.indexList = ["A", "B"];
      this.indexList.forEach((it) => {
        this.list[it] = [
          {
            _id: dsf.uuid(4),
            username: it + "达利",
            phone: "14900344278"
            // photo: JSON.stringify([{ relativePath: "https://img01.yzcdn.cn/vant/cat.jpeg" }])
          },
          {
            _id: dsf.uuid(4),
            username: it + "名仕",
            phone: "18666782757"
            // photo: JSON.stringify([{ relativePath: "https://img01.yzcdn.cn/vant/apple-1.jpg" }])
          }
        ];
      });
    } else {
      this.getData();
    }
  },
  beforeDestroy() {
    this.unwatch && this.unwatch();
  },

  methods: {
    search() {
      let $list = [];
      if (this.searchStr) {
        let arr = [];
        _.filter(this.allList, (user) => {
          let phone = user.phone || "";
          let uname = user.username || "";
          if (uname.indexOf(this.searchStr) >= 0 || phone.indexOf(this.searchStr) >= 0) {
            arr.push(user);
          }
        });
        $list = arr;
      } else {
        $list = this.allList;
      }
      this.toPinYinList($list);
    },
    toPinYinList(data) {
      this.indexList = [];
      this.list = {};
      _.each(data, (it) => {
        let py = it.firstSpell || it.sp;
        if (!py) {
          py = "#";
        }
        dsf.array.ensure(this.indexList, py);
        if (!this.list[py]) {
          this.$set(this.list, py, []);
        }
        this.list[py].push(it);
      });
    },
    getUserKey(user) {
      return user["_id"] || user["dsfa_oua_user_id"];
    },
    // 数据请求
    getData() {
      this.loading = true;
      this.$http
        .get(this.sourcesUrl)
        .done((res) => {
          this.loading = false;
          if (res.success && res.state == 20000 && res.data) {
            this.allList = res.data;
            this.toPinYinList(this.allList);
          } else {
            dsf.layer.message(res.message, false);
          }
        })
        .error((err) => {
          dsf.layer.message(err.message || "出错了，请重试", false);
        })
        .always(() => {
          this.loading = false;
        });
    }
  }
});
</script>
