<template>
  <div class="ds-login-page">
    <div class="ds-login-page-header">
      <img class="logo" :src="logo | imgFormat" />
      <!--波浪线-->
      <svg
        viewBox="0 50 500 35"
        preserveAspectRatio="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path
          fill="#fff"
          d="M 0 70 Q 87.5 47, 175 70 T 350 75 T 500 70 V 100 H 0 V 0"
        ></path>
      </svg>
      <!--背景-->
      <div class="ds-login-page-header-bg">
        <div></div>
        <div></div>
        <div></div>
        <div></div>
      </div>
    </div>
    <div class="ds-login-page-body" :style="bodyStyle">
      <div v-if="tabs.length > 1" class="ds-login-page-tab-header">
        <i ref="tabBar" :style="tabPositions[tabActive]"></i>
        <template v-for="item in tabs">
          <div
            ref="tabs"
            :key="item.code"
            class="ds-login-page-tab-header-item"
            :class="{ active: tabActive == item.code }"
            @click="tabClick(item)"
          >
            {{ item.name }}
          </div>
        </template>
      </div>
      <div class="ds-login-page-tab-body">
        <slot>
          <template v-for="tab in tabs">
            <template v-if="tab.code === 'username'">
              <div
                v-if="tabActive == 'username'"
                :key="tab.code"
                class="ds-login-page-tab-body-item"
              >
                <form class="ds-login-page-form" @submit.prevent>
                  <div class="ds-login-page-form-item">
                    <i class="beforeIcon iconfont icon-wodexuanzhong"></i>
                    <input
                      v-model.trim="username"
                      :placeholder="userNamePlaceholder"
                      type="text"
                      name="username"
                      autocomplete="off"
                    />
                  </div>
                  <div class="ds-login-page-form-item">
                    <i class="beforeIcon iconfont icon-password1"></i>
                    <input
                      v-model.trim="password"
                      :placeholder="passwordPlaceholder"
                      type="password"
                      name="password"
                      autocomplete="off"
                    />
                  </div>
                  <div class="ds-login-page-form-item hasCode">
                    <i class="beforeIcon iconfont icon-yanzhengma1"></i>
                    <img
                      class="imgCode"
                      :src="codeUrl"
                      @click="updateCodeUrl"
                    />
                    <input
                      v-model.trim="code"
                      :placeholder="codePlaceholder"
                      type="text"
                      name="code"
                      autocomplete="off"
                    />
                  </div>
                  <div
                    class="ds-login-page-form-submit"
                    @click="submitByUsername"
                  >
                    登 录
                  </div>
                  <input
                    type="submit"
                    style="display: none"
                    @click="submitByUsername"
                  />
                </form>
              </div>
            </template>
            <template v-else-if="tab.code === 'phone'">
              <div
                v-if="tabActive == 'phone'"
                :key="tab.code"
                class="ds-login-page-tab-body-item"
              >
                <form class="ds-login-page-form" @submit.prevent>
                  <div class="ds-login-page-form-item">
                    <div
                      class="getCodeBt"
                      :class="[codeBtType]"
                      @click.stop="getCode"
                    >
                      {{
                        codeBtType != ""
                          ? `${countdownTime}s后重新获取`
                          : "获取验证码"
                      }}
                    </div>
                    <i class="beforeIcon iconfont icon-lianxidianhua"></i>
                    <input
                      v-model.trim="phoneNumber"
                      type="tel"
                      name="phoneNumber"
                      maxlength="20"
                      autocomplete="off"
                      :placeholder="phonePlaceholder"
                    />
                  </div>
                  <div class="ds-login-page-form-item">
                    <i class="beforeIcon iconfont icon-yanzhengma1"></i>
                    <input
                      v-model.trim="phoneCode"
                      type="email"
                      name="phoneCode"
                      maxlength="12"
                      autocomplete="off"
                      :placeholder="SMSCodePlaceholder"
                    />
                  </div>
                  <div class="ds-login-page-form-submit" @click="submitByPhone">
                    登 录
                  </div>
                  <input
                    type="submit"
                    style="display: none"
                    @click="submitByPhone"
                  />
                </form>
              </div>
            </template>
            <div v-else :key="tab.code" class="ds-login-page-tab-body-item">
              <slot :name="tab.code" />
            </div>
          </template>
        </slot>
      </div>
      <div class="forgot-password" v-if="isForgotPassword"  @click="toForgotPassword">忘记密码?</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "DsfMobilePlatformLogin",
  props: {
    tabs: {
      type: Array,
      default() {
        return [
          { code: "username", name: "账号登录" },
          { code: "phone", name: "验证码登录" },
        ];
      },
    },
    // 是否显示忘记密码
    isForgotPassword:{
      type:Boolean,
      default:false
    },
    login: {
      type: Function,
      default: null,
    },
    userNamePlaceholder: {
      type: String,
      default: "请输入账户名称",
    },
    passwordPlaceholder: {
      type: String,
      default: "请输入密码",
    },
    codePlaceholder: {
      type: String,
      default: "请输入验证码",
    },
    phonePlaceholder: {
      type: String,
      default: "请输入手机号",
    },
    SMSCodePlaceholder: {
      type: String,
      default: "请输入验证码",
    },
    // 图片验证码url
    imgCodeUrl: {
      type: String,
      default: "/code/image?t=@[now().getTime()]",
    },
    logo: {
      type: String,
      default() {
        return (
          dsf.config.setting_public_mobile_login_logo ||
          dsf.config.setting_public_mobile_logo_image ||
          dsf.url.getWebPath("~/static/img/mobile-logo-login.png")
        );
      },
    },
    bottomBg: {
      type: String,
      default() {
        return dsf.config.setting_public_mobile_login_bottom_bg;
      },
    },
  },
  data() {
    return {
      tabPositions: [],
      tabActive: null,
      // 获取验证码按钮的状态。countdown：倒计时中
      codeBtType: "",
      // 验证码倒计时总的时间
      countdownTimes: 120,
      // 图片验证码
      codeUrl: "",
      // 验证码倒计时的时间
      countdownTime: 0,
      username: "",
      password: "",
      code: "",
      phoneNumber: "",
      phoneCode: "",
    };
  },
  computed: {
    bodyStyle() {
      let res = {};
      if (this.bottomBg) {
        let src = Vue.filter("imgFormat")(this.bottomBg);
        res["background-image"] = `url("${src}")`;
      }
      return res;
    },
  },
  created() {
    if (this.tabs.length) {
      this.tabActive = this.tabs[0].code;
    }
    this.updateCodeUrl();
  },
  mounted() {
    if (this.tabs.length > 1) {
      this.resize();
    }
    window.addEventListener("resize", this.resize);
  },
  destroyed() {
    clearInterval(this.timeout);
    window.removeEventListener("resize", this.resize);
  },
  methods: {
    toForgotPassword() {
      // location.replace(dsf.config.setting_mobile_public_forgot_password_page);
      location.href = dsf.config.setting_mobile_public_forgot_password_page
    },
    resize() {
      if (!this.$refs.tabs?.length) return;
      let tabBarWidth = 100;
      let tabPositions = {};
      _.forEach(this.$refs.tabs, (el, i) => {
        let scaleX = (el.offsetWidth - 6) / tabBarWidth;
        let left = el.offsetLeft + el.offsetWidth / 2 + "px";
        tabPositions[this.tabs[i].code] = {
          display: "block",
          transform: `translateX(${left}) scale3d(${scaleX}, 1, 1)`,
        };
      });
      this.tabPositions = tabPositions;
    },
    tabClick({ code }) {
      this.$refs.tabBar.style.transition = "transform .2s ease-in-out";
      setTimeout(() => {
        this.tabActive = code;
        setTimeout(() => {
          this.$refs.tabBar.style.transition = "none";
        }, 210);
      }, 0);
    },
    // 图片验证码
    updateCodeUrl() {
      this.codeUrl = dsf.url.getWebPath(this.$replace(this.imgCodeUrl));
    },
    // 获取验证码
    getCode() {
      if (this.codeBtType != "") return;
      if (this.phoneNumber === "") {
        dsf.layer.message("手机号不能为空！", false);
        return;
      }
      if (!dsf.validate("isMobileOrTelPhone", this.phoneNumber)) {
        dsf.layer.message("手机号输入有误", false);
        return;
      }
      let loading = dsf.layer.loading();
      // todo 获取验证码，这次setTimeout模拟了异步请求
      setTimeout(() => {
        // 异步请求成功
        this.codeBtType = "countdown";
        dsf.layer.closeLoading(loading);
        this.doCountdown();
      }, 1000);
    },
    // 执行倒计时
    doCountdown() {
      this.countdownTime = this.countdownTimes - 1;
      this.timeout = setInterval(() => {
        this.countdownTime--;
        if (this.countdownTime <= -1) {
          this.codeBtType = "";
          clearInterval(this.timeout);
        }
      }, 1000);
    },
    // 账号登录
    submitByUsername() {
      if (!this.login) {
        if (this.username === "") {
          dsf.layer.message("账号不能为空！", false);
          return;
        }
        if (this.password === "") {
          dsf.layer.message("密码不能为空！", false);
          return;
        }
        if (this.code === "") {
          dsf.layer.message("验证码不能为空！", false);
          return;
        }
        this.loginByUsername();
      } else if (dsf.type(this.login) == "function") {
        this.login({
          username: this.username,
          password: this.password,
          code: this.code,
          type: "usernameAndPassword",
        }).then((res) => {
          this.setStateIsLogin(res.data);
        });
      }
    },
    loginByUsername() {
      let loading = dsf.layer.loading();
      this.$webAPI
        .login({
          userName: this.username,
          password: dsf.md5(dsf.md5(this.password)),
          code: this.code || "good",
          loginType: 10,
        })
        .done(({ success, message, data }) => {
          if (success) {
            this.setStateIsLogin(data);
          } else {
            this.updateCodeUrl();
            dsf.layer.message(message, false);
          }
        })
        .error((err) => {
          this.updateCodeUrl();
          if (err) {
            dsf.layer.message(err.message || "未知错误", false);
          }
        })
        .finally(() => {
          dsf.layer.closeLoading(loading);
        });
    },
    // 手机号登录
    submitByPhone() {
      if (!this.login) {
        if (this.phoneNumber === "") {
          dsf.layer.message("手机号不能为空！", false);
          return;
        }
        if (!dsf.validate("isMobileOrTelPhone", this.phoneNumber)) {
          dsf.layer.message("手机号输入有误", false);
          return;
        }
        if (this.phoneCode === "") {
          dsf.layer.message("验证码不能为空！", false);
          return;
        }
        this.loginByPhone();
      } else if (dsf.type(this.login) == "function") {
        this.login({
          username: this.username,
          password: this.password,
          type: "phoneAndSMSCode",
        }).then((res) => {
          this.setStateIsLogin(res.data);
        });
      }
    },
    loginByPhone() {
      // todo 执行验证码登录
      dsf.layer.message("登录失败！", false);
    },
    // 储存cookie登录状态
    async setStateIsLogin(data) {
      //groupGo存在则标识后端通过登录逻辑后要求跳转指定页面
      let groupGo = data.groupGo;
      let appId = this.$root.getAppId();
      let toUrl = "";
      let from = this.$route.query.from;
      if (groupGo) {
        let urlInfo = dsf.url.parse(groupGo);
        if (urlInfo.searchQuery["appid"]) {
          appId = urlInfo.searchQuery["appid"];
          delete urlInfo.searchQuery["appid"];
        }
        toUrl =
          (urlInfo.protocol ? urlInfo.origin : urlInfo.hostname) +
          urlInfo.pathname +
          (dsf.isEmptyObject(urlInfo.searchQuery)
            ? ""
            : "?" + dsf.url.queryStringStringify(urlInfo.searchQuery)) +
          urlInfo.hash;
      }
      //登录完成后初始化app
      await this.$root.initApp(appId);

      let toPage = () => {
        if (from) {
          toUrl = decodeURIComponent(from);
        } else {
          if (!toUrl) {
            //获取当前页面地址
            toUrl = window.location.pathname;
            if (window.location.search) {
              toUrl += window.location.search;
            }
          }
          let toUrlInfo = dsf.url.parse(toUrl);
          //如果没有路由地址则默认补充导航的第一个路由
          if (!toUrlInfo.hash && this.$root.navs.length) {
            toUrl += "#" + this.$root.navs[0].href;
          }
          location.replace(toUrl);
        }
      };

      // app端
      if (!dsf.client.weixin() && window.$sharedPreferences) {
        $sharedPreferences.save(
          {
            userId: dsf.cookies.get("userId"),
            loginName: this.username,
            password: "",
          },
          toPage,
          function (e) {}
        );
      } else {
        toPage();
      }
    },
  },
};
</script>
