<template>
  <div class="ds-control ds-form-item ds-upload upload-img mobile" :class="getCss">
    <template v-if="simple&&readOnly&&!isDesign">
      <DsfTextProxy v-model="value" v-bind="_self.$props"></DsfTextProxy>
    </template>
    <template v-else>
      <van-field
        :label="showLabel && label ? label : ''"
        :required="required"
        :label-width="getLabelWidth(true)"
        :error-message="errorsMsg"
        :error-message-align="$inputAlign"
      >
        <template #input>
          <van-uploader
            v-model="fileListCopy"
            :multiple="multiple"
            :accept="isAndroid ? '' : uploadFileExts"
            :after-read="afterRead"
            :before-read="beforeRead"
            :before-delete="beforeDelete"
            :max-count="uploadFileNum"
            :show-upload="!readOnly"
          >
          </van-uploader>
        </template>
      </van-field>
    </template>
  </div>
</template>

<script>
import cropImage from "./cropImage";
export default dsf.component({
  name: "DsfMobileUploadPhoto",
  mixins: [$mixins.formControl],
  ctrlCaption: "上传照片",
  props: {
    simple: {
      type: Boolean,
      default: false,
    },
    showLabel: {
      type: Boolean,
      default: true,
    },
    label: {
      type: String,
      default: "上传照片",
    },
    multiple: {
      type: Boolean,
      default: false,
    },
    uploadFileNum: {
      type: [Number, String],
      default: 1,
    },
    uploadFileSize: {
      type: [Number, String],
      default: 1024000,
    },
    required: {
      type: Boolean,
      default: false,
    },
    switchMode: {
      type: Boolean,
      default: true,
    },
    requestUrl: {
      type: String,
      default: "",
    },
    isCutPic: {
      type: Boolean,
      default: false,
    },
    aspectRatio: {
      type: String,
      default: "1/1",
    },
    maxSideWidth: {
      type: Number,
      default: 0,
    },
    uploadFileExts: {
      type: String,
      default: ".jpg,.jpeg,.png",
    },
    extsSetting: {
      type: Object,
      default() {
        return {};
      },
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("photo-meta-data", {
          valueAttributes: [
            {
              name: "上传照片",
              code: "photo",
              type: dsf.metadata.getDataType("object"),
              length: 500,
              defaultValue: null,
              unit: null,
            },
          ],
        });
      },
    },
    inputAlign: {
      type: String,
      default: "right",
    },
  },
  data() {
    return {
      previewPopup: false, //预览文件pop
      filePath: "", // 预览文件url
      fileListCopy: [], // 文件列表
      uploadFileType: "image/*", //默认上传类型
      isAndroid: false,
      // uploadFileExts: ".bpm,.jpg,.jpeg,.png,.gif", //允许上传后缀名
    };
  },
  computed: {
    $inputAlign() {
      return this.readOnly ? "right" : this.inputAlign;
    },
    setRequestUrl() {
      return this.requestUrl || "file/upload/";
    },
  },
  watch: {
    value(to) {
      this.initValue(to);
    },
  },
  created() {
    this.initValue(this.value);
    this.checkAndroid()
  },
  methods: {
    checkAndroid() {
      let u = window.navigator.userAgent;
      if (u.indexOf("Android")) {
        this.isAndroid =  true;
      }
    },
    initValue(to) {
      if (dsf.type(to) == "string") {
        this.fileListCopy = JSON.parse(to);
      }
    },
    afterRead(files) {
      if (this.isCutPic) {
        this.$openDialog({
          title: "裁剪",
          content: cropImage,
          height: "100vh",
          width: "100vw",
          position: "right",
          showClose: false,
          params: {
            imgSrc: files.content,
            aspectRatio: this.aspectRatio,
            maxSideWidth: this.maxSideWidth
          },
          event: {
            onChange: this.saveFile,
            onCancel: this.beforeDelete,
          },
        });
      } else {
        this.saveFile(files.file, files.content);
      }
    },
    beforeRead(files) {
      // 判断文件格式
      var f = files.name.split(".")
      let testmsg = f[f.length - 1].toLowerCase();
      if (!this.uploadFileExts.includes(testmsg)) {
        dsf.layer.message(
          `请选择包含这些格式的文件：${this.uploadFileExts}`,
          false
        );
        return false;
      }
      return true;
    },
    beforeDelete() {
      this.fileListCopy = [];
      this.emitValueChange(JSON.stringify(this.fileListCopy));
      this.$nextTick(() => {
        this.emitFormValidate(this.fileListCopy);
      });
    },
    saveFile(files, base64) {
      let formData = new FormData();
      formData.append("file", files, "file.jpeg");
      this.$http
        .post(this.setRequestUrl, formData, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        })
        .done((response) => {
          this.$dispatch("upload-complete", response);
          if (response.success && response.state == 20000) {
            let data = response.data[0];
            let photo = JSON.parse(JSON.stringify(this.fileListCopy));
            data.url = data.absolutePath;
            photo[0] = response.data[0];
            delete data["absolutePath"];
            this.emitValueChange(JSON.stringify(photo));
            this.$nextTick(() => {
              this.emitFormValidate();
            });
          }
        })
        .catch((err) => {
          dsf.layer.message(err.message, false);
          this.$dispatch("upload-complete");
        });
    },
    getFileSize(data) {
      let fileSize = parseInt(data);
      if (data.indexOf("M") > -1 || data.indexOf("m") > -1) {
        fileSize = parseInt(data) * 1024;
      }
      return fileSize;
    },
    computeFileSize(fileSize) {
      let filesizeUnit = "";
      //1048576
      if (parseInt(fileSize) > 1050000) {
        filesizeUnit = (fileSize / Math.pow(1024, 2)).toFixed(2) + "M";
      } else {
        filesizeUnit = (fileSize / Math.pow(1024, 1)).toFixed(2) + "K";
      }
      return filesizeUnit;
    },
  },
});
</script>
