<template>
  <div class="ds-super-search-bt">
    <div class="content" @click="itemClick">
      <template v-if="item.type == 'enum' && item.showModel=='treeselect'">
        <i class="iconfont icon-qiapianmoshi_kuai"></i>
        <span>{{ item.label }}</span>
      </template>
      <template v-else>
        <span>{{ item.label }}</span>
        <i class="down-bar" :class="{opened: opened}"></i>
      </template>
    </div>
  </div>
</template>

<script>
// 排序
export default {
  name: "DsfSuperSearchBtDown",
  inject: {
    $superSearch: {
      default: null
    }
  },
  data() {
    return {};
  },
  props: {
    item: {
      type: Object,
      required: true
    }
  },
  computed: {
    opened() {
      return this.$superSearch.dropdownActive === this.item.id
    }
  },
  methods: {
    itemClick() {
      if (this.$superSearch.isDesign) return;
      if (!this.opened) {
        this.$superSearch.dropdownActive = this.item.id;
      } else {
        this.$superSearch.dropdownActive = null;
      }
    }
  }
}
</script>