<template>
  <div class="ds-control ds-navtab mobile" :class="{ 'ds-no-padding': !showPadding }" :style="{ height: isDesign ? '100%' : tabHeight }">
    <van-tabs v-model="activeName" :closable="enableDelete" border @click="tabClick" @disabled="tabDisabled">
      <slot>
        <van-tab v-for="d in $tabs" :key="d.name" :disabled="isdisable(d)" :name="d.name" :style="{ padding: $padding }">
          <template v-slot:title>
            {{ d.title }}
            <span v-if="Number(badgeList[d.name])" class="ds-navtab-item-badge">
              {{ badgeList[d.name] | badgeFilter }}
            </span>
          </template>
          <div :slot-name="d.name" class="ds-navtab-content">
            <slot :name="d.name"></slot>
          </div>
        </van-tab>
      </slot>
    </van-tabs>
  </div>
</template>

<script>
export default dsf.component({
  name: "DsfMobileNavTab",
  ctrlCaption: "选项卡",
  mixins: [$mixins.tabControl],
  filters: {
    badgeFilter(badge) {
      badge = Number(badge) || 0;
      return badge < 100 ? badge : "99+";
    },
  },
  props: {
    badgeUrl: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      badgeList: {},
    };
  },
  computed: {
    $tabs() {
      return _.filter(this.slots, (it) => {
        return !this.isHide(it);
      });
    },
  },
  created() {
    this.getBadgeList();
  },
  methods: {
    getBadgeList() {
      let { badgeUrl, isDesign } = this;
      if (isDesign) {
        return;
      }
      if (!badgeUrl) return (this.badgeList = {});
      const url = this.$replace(this.badgeUrl, this.local);
      this.$http
        .get(url)
        .then(({ data }) => {
          if (data && data.success) {
            this.badgeList = data.data || {};
          } else {
            this.badgeList = {};
          }
        })
        .catch(() => {
          this.badgeList = {};
        });
    },
  },
});
</script>
