<template>
  <div class="ds-super-search-item mobile">
    <!--枚举-->
    <template v-if="item.type == 'enum'">
      <!--下拉选择，下拉 / 平铺选项-->
      <template v-if="item.showModel=='select' || item.showModel=='tile'">
        <dsf-super-search-form-item-checkbox ref="item" :line="line" :values.sync="value" :item="item" />
      </template>
      <!--下拉选择，树选择-->
      <template v-else-if="item.showModel=='treeselect'">
        <dsf-super-search-form-item-tree ref="item" :line="line" :values.sync="value" :item="item" />
      </template>
    </template>
    <!--文本框-->
    <template v-else-if="item.type == 'text'">
      <dsf-super-search-form-item-input ref="item" :line="line" :values.sync="value" :item="item" />
    </template>
    <!--数字-->
    <template v-else-if="item.type == 'number'">
      <dsf-super-search-form-item-number ref="item" :line="line" :values.sync="value" :item="item" />
    </template>
    <!--时间-->
    <template v-else-if="item.type == 'datetime'">
      <dsf-super-search-form-item-date ref="item" :line="line" :values.sync="value" :item="item" />
    </template>
  </div>
</template>

<script>
import DsfSuperSearchFormItemCheckbox from './superSearchFormItemCheckbox';
import DsfSuperSearchFormItemInput from './superSearchFormItemInput';
import DsfSuperSearchFormItemNumber from './superSearchFormItemNumber';
import DsfSuperSearchFormItemDate from './superSearchFormItemDate';
import DsfSuperSearchFormItemTree from './superSearchFormItemTree';

export default {
  name: "DsfSuperSearchFormItem",
  components: {
    DsfSuperSearchFormItemCheckbox,
    DsfSuperSearchFormItemInput,
    DsfSuperSearchFormItemNumber,
    DsfSuperSearchFormItemDate,
    DsfSuperSearchFormItemTree
  },
  inject: {
    $superSearch: {
      default: null
    }
  },
  props: {
    item: {
      type: Object,
      default() {
        return {};
      }
    },
    line: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      value: null
    };
  },
  methods: {
    open() {
      this.value = _.cloneDeep(this.item.values);
    },
    reset() {
      this.$refs.item?.reset();
      this.sure();
    },
    sure() {
      this.$emit("update:values", this.value);
      this.$nextTick(() => {
        this.$emit("change", this.value);
      });
    }
  }
}
</script>