<template>
  <div class="flow-mobile-add-comment">
    <DsfMobileAddOpinion ref="addOpinion" :sOpinion.sync="sOpinion"></DsfMobileAddOpinion>
    <div class="btns">
      <span class="ds-button--icon-text addcomment-delete" @click="clearAll"><i class="iconfont icon-delete"></i> 清 空</span>
      <DsfMobileButton class="addcomment-sure" @click="sure">确 定</DsfMobileButton>
      <DsfMobileButton class="plain addcomment-cancel" @click="cancel">取 消</DsfMobileButton>
    </div>
  </div>
</template>
<script>
export default {
  name: "DsfMobileFlowAddComment",
  inject: {
    $dialog: {
      default: null,
    },
  },
  props: {
    $vm: {
      type: [Object, null],
      default() {
        return null;
      },
    },
    flow: {
      type: [Object, null],
      default() {
        return null;
      },
    },
  },
  data() {
    return {
      loaded: false,
      tpls: [],
      sOpinion: {
        Content: "",
        Attachments: null,
      },
    };
  },
  created() {
    this.initFlow();
  },
  methods: {
    yes() {
      return this.sOpinion;
    },
    validate() {
      if (!this.sOpinion.Content) {
        dsf.layer.message("处理意见不能为空", false);
        return false;
      }
      return true;
    },
    // 获取表单
    initFlow() {
      if (this.$vm?.flows?.sOpinion) {
        this.sOpinion = _.cloneDeep(this.$vm.flows.sOpinion);
      }
      this.$http.get("wfr/getOpinionKeyWord", {}).done((res) => {
        if (res.success) {
          this.tpls = res.data || [];
        }
        this.loaded = true;
      });
    },
    getValue(val) {
      this.sOpinion.Attachments = JSON.parse(val.args);
    },
    upload() {
      this.$refs.uploadFile.openSelectFile();
    },
    clearAll() {
      this.sOpinion = {
        Content: "",
        Attachments: [],
      };
      this.$refs.addOpinion.uploaderData = [];
    },
    sure() {
      if (!this.validate()) {
        return;
      }
      this.$vm.flows?.flowProcess.forEach((item) => {
        if (item.linkid === this.flow.sCurLinkID) {
          item.opinionatts = JSON.stringify(_.clone(this.sOpinion));
          item.rusername = dsf.cookies.get("user_name");
          item.ruserid = dsf.cookies.get("user_id");
        }
      });

      if (this.$vm?.flows) {
        this.$vm.flows.sOpinion = this.sOpinion;
      }
      const param = {
        sPID: this.flow.sPID,
        sCurLinkID: this.flow.sCurLinkID,
        sOpinion: JSON.stringify(this.sOpinion),
      };
      this.$http
        .get("wfr/opinion", param)
        .then((res) => {
          if (res.data.success) {
            this.$dialog.close();
          } else {
            dsf.layer.message(res.data.message, false);
          }
        })
        .catch((res) => {
          dsf.layer.message("处理意见异常", false);
        });
    },
    cancel() {
      this.$dialog.close();
    },
  },
};
</script>
<style lang="scss" scoped>
.work_flow-usersDialog {
  margin-bottom: 0.96rem;
}
.btns {
  display: flex;
  padding: 0.6rem 0.3rem;
  align-items: center;
  position: fixed;
  bottom: 0px;
  width: 100%;
  height: 0.96rem;
  box-sizing: border-box;
  background-color: #fff;
  .addcomment-delete {
    flex: 0 1 160px;
  }
  .van-button {
    width: 100%;
  }
}
</style>
