<template>
  <div class="ds-control ds-design-img" :style="{'text-align':align}" @click="clickHandler">
    <!-- <el-image v-if="url" :style="{ width: width, height: height, display: 'block' }" :src="url" :preview-src-list="[url]">
      <div slot="error" class="image-slot" style="text-align: center; font-size: 30px">
        <i class="el-icon-picture-outline"></i>
      </div>
    </el-image> -->
    <van-image v-if="url" :style="{ width: width, height: height, display: 'inline-block', minHeight: '40px' }" :src="url"/>
  </div>
</template>
<script>
export default dsf.component({
  name: "DsfMobileUploadDesignImg",
  mixins: [$mixins.control],
  ctrlCaption: "上传设计图",
  data() {
    return {};
  },
  props: {
    url: {
      type: String,
      default: "",
    },
    width: {
      type: [Number, String],
      default: "100%"
    },
    height: {
      type: [Number, String],
      default: "auto"
    },
    align:{
      type:String,
      default:"left"
    },
    toUrl:{
      type:String,
      default:""
    }
  },
  created() {},
  methods: {
    clickHandler(){
      if(this.toUrl){
        this.$openWindow({
          url:dsf.url.getWebPath(this.toUrl),
        })
      }
    }
  },
});
</script>
