<template>
  <div class="ds-control mobile ds-rollingnews ds-no-padding" v-show="show" :style="getBackgroundStyle">
    <div class="ds-rollingnews-icons">
      <dsf-icon name="shengyin_shiti" class="ds-rollingnews-voice"></dsf-icon>
    </div>
    <div :style="heightStyle" class="ds-rollingnews-wraphas">  
      <div ref="wrap" :style="wrapStyle" :class="['ds-rollingnews-wrap', { animate }]">
        <template v-for="(item,index) in data">
          <slot :data="{ item, index }">
            <div @click="onClick(item)" :style="heightStyle" class="ds-rollingnews-item ds-multi-ellipsis-1" :key="`ri-${index}`">
              <span :style="{'font-weight': prefixBold ? '700' : '400'}">{{ prefix }}</span>{{ item[field] }}
            </div>
          </slot>
        </template>
        <div class="ds-rollingnews-empty">暂无数据</div>
      </div>
    </div>
  </div>
</template>
<script>
export default dsf.component({
  name: "DsfMobileRollingNews",
  mixins: [$mixins.control],
  ctrlCaption: "滚动新闻",
  props: {
    padding: {
      type: Boolean,
      default: false
    },
    url: {
      type: String,
      default: ""
    },
    interval: {
      type: Number,
      default: 5000
    },
    height: {
      type: [String, Number],
      default: "40px"
    },
    field: {
      type: String,
      default: "title"
    },
    toUrl: {
      type: String,
      default: ""
    },
    max: {
      type: Number,
      default: 0
    },
    // 前缀
    prefix: {
      type: String,
      default: ''
    },
    // 前缀是否加粗
    prefixBold: {
      type: Boolean,
      default: true
    },
    // 处理返回值
    dataHandler: {
      type: Function,
      require: false
    },
    //无论什么情况组件都显示
    forceShow:{
      type: Boolean,
      require: false
    }
  },
  computed: {
    show() {
      let isDesign = this.isDesign;
      let data = this.data;
      let forceShow=this.forceShow;
      if (isDesign) {
        return true;
      } else {
        //forceShow为true是组件强制显示
        if(forceShow){
          return true;
        }
        //无数据时不显示
        return data.length > 0;
      }
    },
    wrapStyle() {
      let height = 0;
      if (this.$refs.wrap) {
        height = parseInt(window.getComputedStyle(this.$refs.wrap).height);
      }
      return {
        ...this.heightStyle,
        top: `-${this.currentIndex * height}px`
      };
    },
    heightStyle() {
      return {
        height: this.pxToRem(this.height),
        lineHeight: this.pxToRem(this.height)
      };
    }
  },
  data() {
    return {
      currentIndex: 0,
      $interval: null,
      animate: true,
      data: []
    };
  },
  mounted() {
    this.$refs.wrap.addEventListener("transitionend", () => {
      if (this.currentIndex === this.data.length - 1) {
        clearTimeout(this.$interval);
        this.animate = false;
        this.$nextTick(() => {
          const last = this.data[this.data.length - 1];
          const rest = this.data.slice(0, this.data.length - 1);
          this.data = [last, ...rest];
          this.currentIndex = 0;
          setTimeout(() => {
            this.animate = true;
            this.loop();
          }, 0);
        });
      }
    });
  },
  created() {
    if (this.isDesign) {
      this.data = [
        {
          [this.field]: "我是一条公告"
        },
        {
          [this.field]: "我也是一条公告"
        }
      ];
    } else {
      if (this.url) {
        this.loadData();
      }
    }
  },
  destroyed() {
    clearTimeout(this.$interval);
  },
  methods: {
    loop() {
      if (this.$interval) {
        clearTimeout(this.$interval);
      }
      this.$interval = setTimeout(() => {
        if (this.currentIndex === this.data.length - 1) {
          this.currentIndex = 0;
        } else {
          this.currentIndex += 1;
        }
        this.loop();
      }, this.interval);
    },
    loadData() {
      let url = this.$replace(this.url, this);
      this.$http
        .get(url, {
          pageNumber: 1,
          pageSize: this.max ? this.max : 9999
        })
        .done(({ success, data, message }) => { // dataHandler
          if (success) {
            if (this.dataHandler) {
              data = this.dataHandler(data);
            }
            this.data = this.formatApiData(data || []);
            this.$nextTick(() => {
              this.loop();
            });
          } else {
            dsf.layer.message(message || '请求异常', false);
          }
        }).error(err => {
          dsf.layer.message(err?.message || '请求异常', false);
        });
    },
    formatApiData(data) {
      if (!Array.isArray(data)) {
        return [];
      }
      var list = data.map((it) => {
        return {
          ...it,
          [this.field]: it[this.field]
        };
      });
      return list;
    },
    onClick(item) {
      //如果二次开发注入锅click事件则无视配置执行二次开发事件
      if (this.$listeners["click"]) {
        this.$dispatch("click", item);
      } else {
        if (this.toUrl) {
          let url = this.$replace(this.toUrl, item);
          this.$openWindow({ url: url });
        }
      }
    }
  },
  watch:{
    show:{
      handler(o,ov){
        this.$dispatch("show",o)
      },
      immediate:true
    }
  }
});
</script>