<template>
  <vue-picture-cut
    ref="pictureCut"
    :src="imgSrc"
    :max-pixel="maxSideWidth || null"
    :msk-option="mskOption"
  >
    <crop-image-menu
      slot="menu"
      confirm-name="确定"
      cancel-name="取消"
      menu-rotate-name="旋转"
      @on-change="saveFileImg"
      @on-cancel="handleClose"
    />
  </vue-picture-cut>
</template>
<script>
import { VuePictureCut } from "vue-picture-cut";
import CropImageMenu from "./cropImageMenu";

export default {
  name: "CropImage",
  components: {
    VuePictureCut,
    CropImageMenu,
  },
  props: {
    imgSrc: {
      type: String,
      default: "",
    },
    aspectRatio: {
      type: String,
      default: "2/3",
    },
    maxSideWidth: {
      type: Number,
      default: 0,
    }
  },
  data() {
    return {};
  },
  computed: {
    mskOption() {
      let [width, height] = this.aspectRatio.split('/');
      return {
        width: parseInt(width),
        height: parseInt(height),
        isRound: false,
        resize: false
      }
    }
  },
  methods: {
    saveFileImg(res) {
      this.$emit("onChange", res.blob, res.base64);
      this.$dialog?.close();
    },
    // 关闭裁剪
    handleClose() {
      this.$emit("onCancel");
      this.$dialog?.close();
    },
  },
};
</script>
