<template>
  <div class="ds-super-search-form-item center" :class="[item.operator]">
    <template v-if="item.operator === 'between'">
      <div class="ds-super-search-form-input" :class="{placeholder: !value[0]}" @click="selectDate">
        {{ value[0] || '请选择开始日期' }}
      </div>
      <i class="iconfont icon-758bianjiqi_fengexian"></i>
      <div class="ds-super-search-form-input" :class="{placeholder: !value[1]}" @click="selectDate">
        {{ value[1] || '请选择结束日期' }}
      </div>
    </template>
    <template v-else>
      <div class="ds-super-search-form-input" :class="{placeholder: !value}" @click="selectDate">
        {{ value || '请选择日期' }}
      </div>
    </template>
    <van-popup
      v-model="showPicker"
      get-container="#app"
      position="bottom"
      round
    >
      <dsf-calendar
        :show="showPicker"
        v-bind="calendarOptions"
        @cancel="showPicker = false"
        @confirm="onConfirm"
      />
    </van-popup>
  </div>
</template>

<script>
export default {
  name: "DsfSuperSearchFormItemDate",
  props: {
    values: {
      type: [String, Array],
      default() {
        return []
      },
    },
    item: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      showPicker: false,
      value: null,
      calendarOptions: {}
    }
  },
  computed: {
    format() {
      return this.item.model === 'ym' ? 'yyyy-mm' : 'yyyy-mm-dd'
    }
  },
  watch: {
    values: {
      handler(v) {
        if (this.item.operator === 'between') {
          this.value = [null, null];
          if (dsf.type(v) == "array") {
            if (v.length > 1) {
              this.value[1] = v[1];
            }
            if (v.length > 0) {
              this.value[0] = v[0];
            }
          } else {
            this.value[0] = v;
          }
        } else if (dsf.type(v) == "array") {
          this.value = v.length ? v[0] : null;
        } else {
          this.value = v;
        }
      },
      immediate: true,
    },
    showPicker: {
      handler(v) {
        if (v) {
          let defaultValue, range = this.item.operator === 'between';
          if (range) {
            if (this.value[0] && this.value[1]) {
              defaultValue = [
                dsf.date.parse(this.value[0], this.format),
                dsf.date.parse(this.value[1], this.format)
              ];
            } else {
              defaultValue = [];
            }
          } else {
            defaultValue = this.value ? dsf.date.parse(this.value, this.format) : null;
          }
          this.calendarOptions = {
            defaultValue,
            type: this.item.model === 'ym' ? 'month' : 'date',
            clearable: true,
            range
          };
        }
      },
      immediate: true
    }
  },
  methods: {
    reset() {
      if (this.item.operator === 'between') {
        this.value = [null, null];
      } else {
        this.value = null;
      }
      this.$emit("update:values", this.value);
      this.$nextTick(() => {
        this.$emit("change", this.value);
      });
    },
    selectDate() {
      this.showPicker = true;
    },
    onConfirm(date) {
      this.showPicker = false;
      if (this.item.operator === 'between') {
        let [dateS, dateE] = date ? date : ['', ''];
        this.value = [dsf.date.format(dateS, this.format), dsf.date.format(dateE, this.format)];
      } else {
        if (dsf.type(date) === 'array') {
          date = date?.[0];
        }
        this.value = date ? dsf.date.format(date, this.format) : null;
      }
      this.$emit("update:values", this.value);
      this.$nextTick(() => {
        this.$emit("change", this.value);
      });
    },
  }
}
</script>