<template>
  <div
    class="ds-mobile-platform-card-item-by-PLTR-mode-two"
    :style="controlStyle"
    @click="_clickBefore(itemClick)"
  >
    <template v-if="isDesign">
      <div class="content-wrap">
        <div class="cover">
          <van-image
            src="https://cube.elemecdn.com/6/94/4d3ea53c084bad6931a56d5158a48jpeg.jpeg"
            fit="cover"
          ></van-image>
        </div>
        <div class="title-warp">
          <div class="title ds-multi-ellipsis-1">
            上海全力打造全国新一轮全面开放新高地的新举措上海全力打造全国新一轮
            全面开放新高地的新举措上海全力打造全国新一轮全面开放新高地的新举措上海全力打造全国新一轮全面开放新高地的新举措上海全力打造
            全国新一轮全面开放新高地的新举措上海全力打造全国新一轮全面开放新高地的新举措上海全力打造全国新一轮全面开放新高地的新举措上海全力打造
            全国新一轮全面开放新高地的新举措上海全力打造全国新一轮全面开放新高地的新举措上海全力打造全国新一轮全面开放新高地的新举措上海全力打
            造全国新一轮全面开放新高地的新举措上海全力打造全国新一轮全面开放新高地的新举措
          </div>
          <div class="subtitle">{{ data["teacher"] }}-{{ data["position"] }}</div>
          <div class="study-time">
            <!-- 学时 -->
            <div class="course-time">
              {{ getPrefix("courseTime") }}10{{ getSuffix("courseTime") }}
            </div>
            <!-- 浏览量 -->
            <div class="views">
              <i class="iconfont icon-icon-visible"></i>{{ data["views"] }}
            </div>
          </div>
        </div>
      </div>
      <div class="progressBar">
        <van-progress :percentage="50" :stroke-width="8" :show-pivot="false" />
      </div>
    </template>
    <template v-else>
      <div class="content-wrap">
        <div class="cover">
          <van-image :src="data['cover'] | imgFormat" fit="cover"></van-image>
        </div>
        <div class="title-warp">
          <div class="title ds-multi-ellipsis-1">
            {{ data["title"] }}
          </div>
          <div class="subtitle ds-multi-ellipsis-1">{{ data["teacher"] }}-{{ data["position"] }}</div>
          <div class="study-time">
            <!-- 学时 -->
            <div class="course-time">
              {{ getPrefix("courseTime") }} {{ data["courseTime"] }}
              {{ getSuffix("courseTime") }}
            </div>
            <!-- 浏览量 -->
            <div class="views">
              <i class="iconfont icon-icon-visible"></i> {{ data["views"] }}
            </div>
          </div>
        </div>
      </div>
      <div class="progressBar" v-if="data['percentage'] != null">
        <van-progress
          :percentage="data['percentage'] || 0"
          :stroke-width="8"
          :show-pivot="false"
        />
      </div>
    </template>
  </div>
</template>

<script>
export default dsf.component({
  name: "DsfMobilePlatformCardItemByPLTRModeTwo",
  ctrlCaption: "左图右文模式二",
  mixins: [$mixins.card],
  props: {
    //组件边距
    padding: {
      default: () => [10, 10, 10, 10],
    },
    //静态数据
    keysMap: {
      type: Array,
      default() {
        return [
          { from: "cover", to: "cover", type: "text", desc: "封面" },
          { from: "title", to: "title", type: "text", desc: "课题" },
          {
            from: "courseTime",
            to: "courseTime",
            type: "text|number",
            desc: "学时",
            prefix: "",
            suffix: "学时",
          },
          {
            from: "views",
            to: "views",
            type: "text|number",
            desc: "浏览量",
          },
          { from: "teacher", to: "teacher", type: "text", desc: "授课老师" },
          { from: "position", to: "position", type: "text", desc: "教师职位" },
          { from: "percentage", to: "percentage", type: "text|number", desc: "学习进度" },
        ];
      },
    },
    //显示行数
    lineNum: {
      type: Number,
      default: 2,
    },
    //跳转地址
    detailUrl: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      defaultBage: require("../../../assets/styles/themes/img/pass.png"),
    };
  },
  computed: {
    controlStyle() {
      let { padding, isDesign } = this;
      let style = {
        padding: padding.join("px ") + "px",
      };
      // if (isDesign) {
      //   style.padding = "10px 10px 10px 10px";
      // }
      return style;
    },
  },
  methods: {
    itemClick() {
      if (this.$listeners.itemClick) {
        this.$dispatch("itemClick", this.local);
      } else if (this.detailUrl) {
        let url = this.$replace(this.detailUrl, this.local);
        window.location.href = dsf.url.getWebPath(url);
      }
    },
  },
});
</script>
