<template>
  <div class="ds-mobile-platform-card-item-by-teacher">
    <div v-if="list.length > 0" class="swiper-container swiper-teacher">
      <div class="left-btn" @click.stop="slidePrev"><i class="iconfont icon-left"></i></div>
      <div class="right-btn" @click.stop="slideNext"><i class="iconfont icon-right"></i></div>
      <div class="swiper-wrapper">
        <div class="swiper-slide" v-for="(item, index) in list" :key="index" @click="_clickBefore(itemClick, item)">
          <van-image
            radius="4"
            class="swiper-img"
            :src="item.cover | imgFormat"
            fit="cover"
          />
          <div class="name">{{ item.name }}</div>
        </div>
      </div>
    </div>
    <van-empty v-else description="暂无数据" />
  </div>
</template>

<script>
export default dsf.component({
  name: "DsfMobilePlatformCardItemByTeacher",
  ctrlCaption: "轮播卡片一",
  mixins: [$mixins.control],
  props: {
    // 获取轮播数据接口
    dataApi: {
      type: String,
      default:''
    },
    // 详情跳转链接
    detailUrl: {
      type: String,
      default:''
    },
    // 字段映射
    keysMap: {
      type: Array,
      default() {
        return [
          { from: 'cover', to: 'cover', type: 'text', desc: '封面' },
          { from: 'name', to: 'name', type: 'text', desc: '名称' }
        ];
      }
    },
    clickBefore: {
      type: Function,
      required: false
    }
  },
  data() {
    return {
      swiperList: []
    }
  },
  computed: {
    list({swiperList,keysMap,isDesign}) {
      if (isDesign) {
        return swiperList
      }
      return _.map(swiperList, s => {
        _(keysMap).forEach(({ to, from }) => {
          s[to] = s[from]
        });
        return s
      })
    }
  },
  mounted() {
    if (window.Swiper === undefined) {
      this.loadScript(() => {
        this.initSwiper()
      });
    } else {
      this.initSwiper()
    }
  },
  methods: {
    // 初始化swiper
    async initSwiper() {
      if (!this.isDesign) {
        this.swiperList = []
        if (!this.dataApi) return
        try {
          await this.loadData()
        } catch (error) {
          dsf.error(error);
        }
      } else {
        this.swiperList = [
          {cover: 'https://cube.elemecdn.com/6/94/4d3ea53c084bad6931a56d5158a48jpeg.jpeg',name: '张三01'},
          {cover: 'https://img01.yzcdn.cn/vant/apple-1.jpg',name: '张三02'},
          {cover: 'https://img01.yzcdn.cn/vant/apple-2.jpg',name: '张三03'}
        ]
      }
      this.$nextTick(() => {
        let rootFontSize = document.querySelector('html').style.fontSize.replace(/(\d+)px/,'$1')
        this.mySwiper = new Swiper ('.swiper-teacher', {
          spaceBetween : 0.2 * rootFontSize,
          slidesPerView: 3,
        })
      })
    },
    loadData() {
      return new Promise((resolve,reject) => {
        let loading = dsf.layer.loading();
        const api = this.$replace(this.dataApi, this);
        this.$http.get(api)
          .done(({success, data, message}) => {
            if (success) {
              this.swiperList = data
              resolve()
            } else {
              this.swiperList = []
              dsf.layer.message(message || '请求异常', false);
              reject()
            }
          })
          .error(err => {
            dsf.error(err);
            dsf.layer.message(err?.message || '请求异常', false);
            reject()
          }).always(() => {
            dsf.layer.closeLoading(loading);
          })
      })
    },
    slidePrev() {
      this.mySwiper?.slidePrev()
    },
    slideNext() {
      this.mySwiper?.slideNext()
    },
    _clickBefore(next, params) {
      if (this.isDesign) return;
      let res = this.clickBefore?.(this.local);
      if (res && res.then) {
        res.then(r => {
          if (r !== false) {
            next(params);
          }
        });
      } else if (res !== false) {
        next(params);
      }
    },
    // 点击图片
    itemClick(item) {
      if (this.$listeners.itemClick) {
        this.$dispath('itemClick', item)
      } else if (this.detailUrl){
        let url = this.$replace(this.detailUrl, item);
        window.location.href = dsf.url.getWebPath(url)
      }
    },
    // 加载js
    loadScript(callback) {
      this.$http
        .importFiles([
          [dsf.url.getWebPath("$/js/libs/swiper/swiper-bundle.min.css"),'.css'],
          [dsf.url.getWebPath("$/js/libs/swiper/swiper-bundle.min.js"),'.js']
        ])
        .then(() => {
          callback?.();
        })
        .catch(() => {
          dsf.layer.message("加载fabric文件报错", false);
          reject();
        });
    },
  }
});
</script>