<template>
  <div class="ds-super-search-form-item center">
    <input
      v-model.trim="value"
      class="ds-super-search-form-input"
      type="text"
      placeholder="请输入关键词"
      @change="change"
    />
  </div>
</template>

<script>
export default {
  name: "DsfSuperSearchFormItemInput",
  props: {
    values: {
      type: String,
      default: '',
    },
    item: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      value: ''
    }
  },
  watch: {
    values: {
      handler(v) {
        this.value = v;
      },
      immediate: true,
    },
  },
  methods: {
    reset() {
      let v = this.value = '';
      this.$emit("update:values", v);
      this.$nextTick(() => {
        this.$emit("change", v);
      });
    },
    change() {
      let v = this.value;
      this.$emit("update:values", v);
      this.$nextTick(() => {
        this.$emit("change", v);
      });
    }
  }
}
</script>