<template>
  <div class="flow-mobile-file-list">
    <van-uploader ref="uploader" v-model="files" :accept="acceptsType" :class="{ no: files.length == 0 }" @click-preview="preview" />
    <van-popup v-model="previewing" :style="{ height: '80%', width: '100%' }">
      <div>
        <dsf-iframe v-if="previewing" :src="filePath"></dsf-iframe>
      </div>
    </van-popup>
  </div>
</template>
<script>
export default {
  name: "DsfMobileFlowFileList",
  props: {
    Attachments: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  data() {
    return {
      previewing: false,
      files: [],
    };
  },
  watch: {
    Attachments(val) {
      this.files = val.map((item) => {
        item.url = dsf.url.getWebPath(item.relativePath);
        return item;
      });
    },
  },
  created() {},
  mounted() {
    this.files = this.Attachments.map((item) => {
      item.url = dsf.url.getWebPath(item.relativePath);
      return item;
    });
  },
  methods: {
    preview(files) {
      if (files && files.data && files.data.contentType && files.data.contentType.slice(0, 5) !== "image") {
        this.filePath = dsf.config.webRoot + "preview/file?fileId=" + files.data.id;
        this.previewing = true;
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.flow-mobile-file-list {
  margin: 6px 0;
  /deep/ .van-uploader__upload,
  /deep/ .van-uploader__preview-delete {
    display: none;
  }
}
</style>
