import DsfMobileMeUserInfo from './meUserInfo.vue';
import DsfMobileMeMenus from './meMenus.vue';
import DsfMobileMe from './me.vue';

function install(Vue) {
  Vue.component(DsfMobileMeUserInfo.name, DsfMobileMeUserInfo);
  Vue.component(DsfMobileMeMenus.name, DsfMobileMeMenus);
  Vue.component(DsfMobileMe.name, DsfMobileMe);
}

export default {
  install
};

export {
  install,
  DsfMobileMeUserInfo,
  DsfMobileMeMenus,
  DsfMobileMe
};