<template>
  <div class="ds-super-search-form-checkbox ds-super-search-form-item line">
    <div
      v-for="(it, index) in value"
      :key="it._id"
      class="ds-super-search-form-checkbox-item active"
      @click="remove(index)"
    >
      <div class="ds-multi-ellipsis-1">{{ it._name }}</div>
      <i class="iconfont icon-guanbi3"></i>
    </div>
    <div class="ds-super-search-form-checkbox-item border" @click="openPopup">
      <div class="ds-multi-ellipsis-1">选择</div>
      <i class="iconfont icon-hao"></i>
    </div>
    <van-popup v-model="popupShow" style="height: 100vh" get-container="body" position="bottom">
      <div class="van-calendar">
        <div class="van-calendar__body">
          <dsf-mobile-tree-selector
            ref="selector"
            :max-tags="multiple ? 9999 : 1"
            :data="item.options"
            data-source="6"
          />
        </div>
        <div class="van-calendar__footer">
          <van-goods-action style="position: static;">
            <van-goods-action-button type="info" text="返 回" @click="popupShow = false" />
            <van-goods-action-button type="primary" text="确 定" @click="surePopup" />
          </van-goods-action>
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
export default {
  name: "DsfSuperSearchFormItemTree",
  props: {
    values: {
      type: String,
      default: '',
    },
    item: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      popupShow: false,
      value: []
    }
  },
  computed: {
    multiple() {
      return this.item.isAllowMultipe;
    }
  },
  methods: {
    openPopup() {
      if (this.$refs.selector) {
        this.$refs.selector.result = Object.assign([], this.value);
      }
      this.popupShow = true;
    },
    surePopup() {
      this.value = Object.assign([], this.$refs.selector.result);
      this.popupShow = false;
    },
    remove(index) {
      this.value.splice(index, 1);
    },
    reset() {
      this.value = [];
      let v = this.multiple ? [] : null;
      this.$emit("update:values", v);
      this.$nextTick(() => {
        this.$emit("change", v);
      });
    },
    sure() {
      let v = _.map(this.value, '_id');
      this.$emit("update:values", v);
      this.$nextTick(() => {
        this.$emit("change", v);
      });
    }
  }
}
</script>