<template>
  <div class="ds-super-search-form-checkbox ds-super-search-form-item" :class="{line: line}">
    <div
      v-for="it in (item.options || [])"
      :key="it.value"
      class="ds-super-search-form-checkbox-item"
      :class="{active: value.indexOf(it.value) > -1 }"
      @click="itemClick(it)"
    >
      <div class="ds-multi-ellipsis-1">{{ it.text }}</div>
      <i class="iconfont icon-duigou"></i>
    </div>
  </div>
</template>

<script>
export default {
  name: "DsfSuperSearchFormItemCheckbox",
  props: {
    values: {
      type: [String, Array, Object],
      default() {
        return []
      },
    },
    item: {
      type: Object,
      default() {
        return {};
      }
    },
    line: {
      type: Boolean,
      default: false,
    }
  },
  data() {
    return {
      value: []
    }
  },
  computed: {
    multiple() {
      return this.item.isAllowMultipe;
    }
  },
  watch: {
    values: {
      handler(v) {
        if (this.multiple) {
          if (dsf.type(v) == "array") {
            this.value = v;
          } else if (!v) {
            this.value = [v];
          } else {
            this.value = [];
          }
        } else if (dsf.type(v) == "array") {
          this.value = v.length ? [v[0]] : [];
        } else {
          this.value = v ? [v] : [];
        }
      },
      immediate: true,
    },
  },
  methods: {
    reset() {
      this.value = [];
      let v = this.multiple ? [] : '';
      this.$emit("update:values", v);
      this.$nextTick(() => {
        this.$emit("change", v);
      });
    },
    itemClick(it) {
      let index = this.value.indexOf(it.value);
      let v = '';
      if (this.multiple) {
        if (index > -1) {
          this.value.splice(index, 1);
        } else {
          this.value.push(it.value);
        }
        v = this.value;
      } else if (index > -1) {
        this.value = [];
      } else {
        this.value = [it.value];
        v = it.value;
      }
      this.$emit("update:values", v);
      this.$nextTick(() => {
        this.$emit("change", v);
      });
    }
  }
}
</script>