<template>
  <div class="ds-control ds-form-item ds-radioBox mobile" :class="getCss">
    <template v-if="simple && readOnly && !isDesign">
      <DsfTextProxy v-model="value" v-bind="_self.$props"></DsfTextProxy>
    </template>
    <template v-else-if="readOnly && !isDesign">
      <van-field
        :label-width="getLabelWidth(true)"
        :label="(showLabel && label) || ''"
        :disabled="disabled"
        :readonly="readOnly"
        :error-message="errorsMsg"
        :error-message-align="inputAlign"
        input-align="right">
        <template #input>
          <DsfTextProxy v-model="value" v-bind="_self.$props" :show-label="false"></DsfTextProxy>
        </template>
      </van-field>
    </template>
    <template v-else>
      <van-field
        :label-width="getLabelWidth(true)"
        :label="(showLabel && label) || ''"
        :input-align="inputAlign"
        :error-message="errorsMsg"
        :error-message-align="inputAlign"
        :disabled="disabled">
        <template #input>
          <van-radio-group
            v-model="radioValue"
            :direction="direction"
            :icon-size="iconSize"
            :disabled="disabled"
            :readonly="readOnly"
            @change="change"
          >
            <van-radio
              v-for="item in $items"
              :key="item.value"
              :name="item.value"
              :shape="shape"
              :label-position="labelPosition"
              :disabled="item.disabled || disabledItemValues.indexOf(item.value) > -1"
            >
              {{ item.text }}
            </van-radio>
          </van-radio-group>
        </template>
      </van-field>
    </template>
  </div>
</template>

<script>
let missItem = { value: "", text: "" };

export default dsf.component({
  name: "DsfMobileRadioBox",
  mixins: [$mixins.formControl],
  ctrlCaption: "单选框",
  props: {
    value: {
      type: [Object, String, Number, Boolean],
      default() {
        return missItem;
      },
    },
    items: {
      type: Array,
      default() {
        return [];
      },
    },
    // 是否可取消
    cancel: {
      type: Boolean,
      default: false,
    },
    // 是否其他
    isOther: {
      type: Boolean,
      default: false,
    },
    formatter: {
      type: String,
      default: "textValue^text",
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("items-meta-data");
      },
    },
    // 文字对齐方式
    inputAlign: {
      type: String,
      default: "right",
    },
    // 排列方式：水平(horizontal)，垂直(vertical)
    direction: {
      type: String,
      default: "horizontal",
    },
    // 图标大小，默认单位为px
    iconSize: {
      type: String,
      default: "1.43em",
    },
    // 图标形状：方形(square)，圆形(round)
    shape: {
      type: String,
      default: "round",
    },
    // 文本位置
    labelPosition: {
      type: String,
      default: "right",
    },
    // 需要禁用选项的value值得集合
    disabledItemValues: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  data() {
    return {
      result: { text: "", value: "" },
    };
  },
  computed: {
    $items() {
      let res = this.items || [];
      if (this.isDesign && !res.length) {
        res = [
          { value: "1", text: "数据1" },
          { value: "2", text: "数据2" },
        ];
      }
      if (this.isOther) {
        return [...res, { value: "else", text: "其他" }];
      }
      return res;
    },
    radioValue: {
      get() {
        return this.result.value;
      },
      set(to) {
        let item = { text: "", value: "" };
        if (to && this.$items.length) {
          item = this.$items.find((v) => v.value == to) || { text: "", value: "" };
        }
        this.result = item;
        this.emitValueChange(item);
        this.$nextTick(() => {
          this.emitFormValidate(item);
        });
      },
    },
  },
  watch: {
    value(to) {
      this.initValue(to);
    },
  },
  created() {
    this.initValue(this.value);
  },
  methods: {
    initValue(v) {
      if (this.simple || this.readOnly || this.isDesign || _.isEqual(v, this.result)) {
        return;
      }
      let type = dsf.type(v);
      if (type == "object") {
        v = v.value;
        type = "string";
      }
      if (dsf.isUnDef(v)) {
        this.radioValue = null;
        return;
      }
      if (type == "string" || type == "number" || type == "boolean") {
        let res = this.$items.find((item) => item.value == v);
        if (!res) {
          this.radioValue = null;
        } else {
          this.result = res;
          this.emitValueChange(res);
        }
      } else {
        this.radioValue = null;
      }
    },
    change() {
      this.$dispatch("change", this.value);
    },
  },
});
</script>
