<template>
  <div v-if="isDesign || (sPID && sCurLinkID)" class="ds-control ds-form-item ds-textbox" :class="getCss">
    <p v-if="showLabel" class="van-cell">{{ label }}</p>
    <van-field v-if="data && data.length > 0" type="text" input-align="right">
      <template #input>
        <div v-for="(item, index) in data" :key="index" class="form-comment-box-value">
          <p class="content">{{ item.opinionatts && item.opinionatts.Content }}</p>
          <DsfMobileFlowFileList
            v-if="item.opinionatts && item.opinionatts.Attachments && item.opinionatts.Attachments.length > 0"
            :Attachments="item.opinionatts.Attachments"
          ></DsfMobileFlowFileList>
          <img class="icon" :src="require(`_platform/assets/styles/themes/workFlow/ry1.png`)" />
          <span class="name">{{ item.rusername }}</span>
          <span class="time">{{ item.receivetime }}</span>
        </div>
      </template>
    </van-field>
    <p v-if="isShowAdd" class="van-cell">
      <DsfMobileButton @click="addComment" size="small" style="width: 100%">{{ addCommentText }}</DsfMobileButton>
    </p>
  </div>
</template>
<script>
import DsfMobileFlowFileList from "./mobileFlowFileList";
export default dsf.component({
  name: "DsfMobileFormCommentBox",
  mixins: [$mixins.formControl, $mixins.formCommentBox],
  ctrlCaption: "表单意见框",
  components: {
    DsfMobileFlowFileList,
  },
});
</script>
