<template>
  <van-popup v-model="$superSearch.moreShow" style="height: 70vh" get-container="body" position="bottom" round>
    <div class="van-calendar">
      <div class="van-calendar__header" style="box-shadow: none;">
        <div class="van-calendar__header-title">全部筛选</div>
        <van-icon
          name="cross"
          size="22"
          color="#999"
          style="position: absolute;top: 0.19rem;right: 0.32rem;"
          @click="$superSearch.moreShow = false"
        />
      </div>
      <div class="van-calendar__body">
        <template v-for="item in moreList">
          <div class="ds-super-search-more-popup-item" :key="item.id">
            <div class="title">{{ item.label }}</div>
            <div class="content">
              <dsf-super-search-form-item ref="formItem" :values.sync="item.values" :item="item" line />
            </div>
          </div>
        </template>
      </div>
      <div class="van-calendar__footer">
        <van-goods-action style="position: static;">
          <van-goods-action-button type="info" text="重 置" @click="reset" />
          <van-goods-action-button type="primary" text="确 定" @click="sure" />
        </van-goods-action>
      </div>
    </div>
  </van-popup>
</template>

<script>
import DsfSuperSearchFormItem from './superSearchFormItem';

export default {
  name: "DsfSuperSearchMorePopup",
  components: {
    DsfSuperSearchFormItem
  },
  inject: {
    $list: {
      default: null
    },
    $superSearch: {
      default: null
    }
  },
  props: {
    moreList: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  data() {
    return {
    }
  },
  watch: {
    '$superSearch.moreShow'(to) {
      if (to) {
        this.open();
      }
    }
  },
  methods: {
    open() {
      _.forEach(this.$refs.formItem, it => it?.open());
    },
    reset() {
      _.forEach(this.$refs.formItem, it => it?.reset());
      this.$superSearch.moreShow = false;
      // 触发列表重新请求
      this.$superSearch.emitChange();
    },
    sure() {
      _.forEach(this.$refs.formItem, it => it?.sure());
      this.$superSearch.moreShow = false;
      // 触发列表重新请求
      this.$superSearch.emitChange();
    }
  }
}
</script>