<template>
  <div
    class="ds-control mobile ds-platform-card-box"
    :class="{horizontal: horizontal}"
    :style="rootPadding"
  >
    <div v-if="isDesign" class="ds-platform-card-list" :style="listStyle">
      <div class="ds-platform-card-item no-margin-left no-margin-top" :style="itemStyle" slot-name="default">
        <slot v-bind="{ row: {}, $index: 0 }"></slot>
      </div>
      <template v-for="$index in (columnNumber * (horizontal ? 3 : 1) - 1)">
        <div :key="$index" class="ds-platform-card-item" :class="getItemClass($index)" :style="itemStyle">
          {{ $index }}
        </div>
      </template>
    </div>
    <template v-else>
      <van-empty v-if="!realData.length" v-show="!loading" description="暂无数据" />
      <div v-else class="ds-platform-card-list" :style="listStyle">
        <template v-for="(row, $index) in realData">
          <div
            :key="row[rowKey]"
            class="ds-platform-card-item"
            :class="getItemClass($index)"
            :style="itemStyle"
          >
            <slot v-bind="{ row, $index }"></slot>
          </div>
        </template>
      </div>
    </template>
  </div>
</template>

<script>
export default dsf.component({
  name: "DsfMobilePlatformCardBox",
  ctrlCaption: "卡片列表",
  mixins: [$mixins.cardList],
  props: {
    loading: {
      type: Boolean,
      default: false
    },
    // 盒子内边距
    padding: {
      type: Array,
      default() {
        return ['0','25px','0','25px']
      }
    },
    // 一行N列
    columnNumber: {
      type: Number,
      default: 2
    },
    // 列宽
    columnWidth: {
      type: String,
      default: '150px'
    },
    // 上下间隙
    marginTop: {
      type: String,
      default: '25px'
    }
  },
  computed: {
    rootPadding() {
      let padding = _.map(this.padding, p => this.pxToRem(p));
      if (this.horizontal) return {
        'padding-top': padding[0],
        'padding-bottom': padding[2]
      };
      return {
        padding: padding.join(' ')
      }
    }
  }
});
</script>