<template>
  <div class="ds-super-search-form-item center" :class="[item.operator]">
    <template v-if="item.operator === 'between'">
      <input v-model.number="value[0]" class="ds-super-search-form-input" type="number" placeholder="请输入数字" @change="change1" />
      <i class="iconfont icon-758bianjiqi_fengexian"></i>
      <input v-model.number="value[1]" class="ds-super-search-form-input" type="number" placeholder="请输入数字" @change="change2" />
    </template>
    <template v-else>
      <input v-model.number="value" class="ds-super-search-form-input" type="number" placeholder="请输入数字" @change="change" />
    </template>
  </div>
</template>

<script>
export default {
  name: "DsfSuperSearchFormItemNumber",
  props: {
    values: {
      type: [Number, Array],
      default: null,
    },
    item: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      value: null
    }
  },
  watch: {
    values: {
      handler(v) {
        if (this.item.operator === 'between') {
          this.value = [null, null];
          if (dsf.type(v) == "array") {
            if (v.length > 1) {
              let v2 = parseFloat(v[1]);
              this.value[1] = isNaN(v2) ? null : v2;
            }
            if (v.length > 0) {
              let v1 = parseFloat(v[0]);
              this.value[0] = isNaN(v1) ? null : v1;
            }
          } else {
            let v1 = parseFloat(v);
            this.value[0] = isNaN(v1) ? null : v1;
          }
        } else if (dsf.type(v) == "array") {
          this.value = v.length ? v[0] : null;
        } else {
          this.value = v;
        }
      },
      immediate: true,
    },
  },
  methods: {
    reset() {
      if (this.item.operator === 'between') {
        this.value = [null, null];
      } else {
        this.value = null;
      }
      this.$emit("update:values", this.value);
      this.$nextTick(() => {
        this.$emit("change", this.value);
      });
    },
    change1() {
      if (this.value[1] === null) return;
      if (this.value[0] > this.value[1]) {
        this.value[1] = this.value[0];
      }
      this.change();
    },
    change2() {
      if (this.value[0] === null) return;
      if (this.value[1] < this.value[0]) {
        this.value[0] = this.value[1];
      }
      this.change();
    },
    change() {
      let v = this.value;
      this.$emit("update:values", v);
      this.$nextTick(() => {
        this.$emit("change", v);
      });
    }
  }
}
</script>