<template>
  <div class="ds-control mobile ds_address" :class="{'ds-no-padding':!isDesign}" v-mobile-loading="loading">
    <van-search
      ref="search"
      v-model="searchStr"
      :placeholder="searchPlaceholder"
      @search="search">
      <template v-if="searchRightIcon" v-slot:right-icon>
        <dsf-icon :name="searchRightIcon"/>
      </template>
    </van-search>
    <div class="address-b" v-mobile-scroll>
      <div class="unit-navs">
        <span v-for="(nav,$index) in navs" :key="nav._id" class="nav-item" @click="unitNavClick(nav)">
          <span :class="{'actived':$index==navs.length-1}">{{nav.name}}</span>
          <i class="iconfont icon-weibiaoti34" v-if="$index!=navs.length-1"></i>
        </span>
      </div>
      <div class="unit-item" v-for="unit in units" :key="unit._id" @click="unitClickHandler(unit)">
        <div class="unit-item-content van-hairline--top">
          {{unit.name}}&nbsp;&nbsp;&nbsp;{{dsf.isDef(unit.childrenCount)?"("+unit.childrenCount+")":""}}
          <i class="iconfont icon-weibiaoti34"></i>
        </div>
      </div>
      <van-index-bar :index-list="indexList">
        <div v-for="item in indexList" :key="item" class="address-item">
          <van-index-anchor :index="item">{{item}}</van-index-anchor>
          <UserItem v-for="it in usersMap[item]" :key="getUserKey(it)" :user="it"></UserItem>
        </div>
      </van-index-bar>
    </div>
  </div>
</template>

<script>
import UserItem from "./userItem";

export default dsf.component({
  name: "DsfMobileUnitAddress",
  ctrlCaption: "机构通讯录",
  mixins: [$mixins.control],
  props: {
    sourcesUrl: {
      type: String,
      default: "/oua/getUserByOrg"
    },
    /*********搜索框样式***********/
    // 搜索框圆角：shape圆形，默认矩形
    searchRadius: {
      type: String,
      default: "6px"
    },
    // 搜索框提示语
    searchPlaceholder: {
      type: String,
      default: "请输入搜索内容"
    },
    // 搜索框右侧图标
    searchRightIcon: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      navs: [],
      indexList: [],
      units: [],
      users: [],
      usersMap: {},
      searchStr: "",
      timer: null,
      loading: false,
      all: []
    };
  },

  components: {
    UserItem
  },

  computed: {},

  mounted() {
    let $search = this.$refs.search;
    if ($search) {
      let $searchBox = $search.querySelector('.van-search__content');
      $searchBox.style.borderRadius = this.pxToRem(this.searchRadius);
      this.unwatch = this.$watch(
        () => {
          return this.pxToRem(this.searchRadius);
        },
        (v) => {
          $searchBox.style.borderRadius = v;
        }
      )
    }
  },

  created() {
    if (this.isDesign) {
      this.navs = [{ name: "梦创双杨", _id: "0" }];
      this.units = [
        { name: "人事部", _id: "1" },
        { name: "财务部", _id: "2" },
        { name: "销售部", _id: "3" }
      ];
      this.usersMap = {
        A: [{ username: "员工1", _id: "4" }],
        B: [{ username: "员工2", _id: "5" }],
        "#": [{ username: "员工3", _id: "6" }]
      };
      this.indexList = ["A", "B", "#"];
    } else {
      this.getOrgData(null);
    }
  },
  beforeDestroy() {
    this.unwatch && this.unwatch();
  },

  methods: {
    search() {
      let list = this.all;
      if (this.searchStr) {
        list = [];
        _.each(this.all, (it) => {
          let name = it.name || "";
          let phone = it.phone || "";
          if (name.indexOf(this.searchStr) >= 0 || phone.indexOf(this.searchStr) >= 0) {
            list.push(it);
          }
        });
      }
      this.splitData(list);
    },
    toPinYinList(data) {
      this.indexList = [];
      this.list = {};
      _.each(data, (it) => {
        dsf.array.ensure(this.indexList, it.sp);
        if (!this.list[it.sp]) {
          this.$set(this.list, it.sp, []);
        }
        this.list[it.sp].push(it);
      });
    },
    getUserKey(user) {
      return user["_id"] || user["dsfa_oua_user_id"];
    },
    splitData(list) {
      this.users = [];
      this.units = [];
      this.usersMap = {};
      this.indexList = [];
      _.each(list, (it) => {
        let arr = it.type == 1 ? this.users : this.units;
        arr.push(it);
      });
      _.each(this.users, (it) => {
        let py = it.py;
        let sp = "#";
        if (py) {
          sp = py.substr(0, 1).toUpperCase();
        }
        if (!this.usersMap[sp]) {
          this.$set(this.usersMap, sp, []);
        }
        this.usersMap[sp].push(it);
        dsf.array.ensure(this.indexList, sp);
      });
      this.indexList = this.indexList.sort((v1, v2) => {
        return v1 > v2 ? -1 : 1;
      });
    },
    getOrgData() {
      this.loading = true;
      let url = this.sourcesUrl || "/oua/getUserByOrg";
      this.$http
        .get(url, {
          sExplanLevel: 1
        })
        .then(({ data }) => {
          let tree = data?.data?.children || [];
          this.all = _.map(tree, (it) => {
            return this.dataFormat(it);
          });
          this.splitData(this.all);
          let rootUnit = this.dataFormat(data.data);
          this.navs.push(rootUnit);
        })
        .catch((err) => {
          dsf.layer.message(err.message || "出错了，请重试", false);
        })
        .finally(() => {
          this.loading = false;
        });
    },
    // 数据请求
    getDeptData(pid) {
      this.loading = true;
      let url = "/oua/getUserByDept";
      this.$http
        .get(url, {
          deptID: pid
        })
        .then(({ data }) => {
          let tree = data?.data?.children || [];
          this.all = _.map(tree, (it) => {
            return this.dataFormat(it);
          });
          this.splitData(this.all);
        })
        .catch((err) => {
          dsf.layer.message(err.message || "出错了，请重试", false);
        })
        .finally(() => {
          this.loading = false;
        });
    },
    dataFormat(row) {
      let obj = {
        _id: row._id,
        type: row._type || row.type,
        name: row._name || row.name,
        children: row.children,
        unitId: row.UnitID || row.unitId,
        childrenCount: row?.children?.length || null,
        attrs: row.Atts || row.attrs
      };
      if (obj.type == 1) {
        obj.phone = obj.attrs.phone;
        obj.username = obj.name;
        obj.photo = obj.attrs.photo;
        obj.py = obj.attrs.pinyin || obj.attrs.py;
      }
      return obj;
    },
    unitClickHandler(unit) {
      this.navs.push(unit);
      this.getDeptData(unit._id);
    },
    unitNavClick(unit) {
      let index = _.findIndex(this.navs, (it) => it._id == unit._id);
      if (index >= 0) {
        this.navs.splice(index + 1, this.navs.length);
      }
      this.getDeptData(unit._id);
    }
  },
  watch: {}
});
</script>
