import RegisterMobileNativeEvent from './event';
import { DsfMobileDialog } from './dialog';
import { VanMultiplePicker, DsfMobileLoading, DsfMobileButton, DsfCalendar, DsfCalendarTimeBox } from '../common';
import { DsfMobilePage } from "./page";
import { DsfMobileButtonBar } from './button';
import { DsfMobileNavTab } from './tabs';
import { DsfDemoBrowserNavBar, DsfMobileButtonNav, DsfMobileBanner, DsfMobileNavMenu, DsfMobileNavMenuCell, DsfMobileCalendarNav, DsfMobileRollingNews, DsfMobileNavBar } from './nav';
import { DsfMobileHtml } from './html';
import { DsfMobileDataGrid } from "./list";
import { DsfMobileNewsInfo, DsfMobileNewsDetail } from "./news";
import { DsfMobileVote } from "./vote";
import { DsfMobileExam } from "./exam";
import { DsfMobileTextBox, DsfMobileTextArea, DsfMobileAutoComplete, DsfMobileUeditor } from "./input";
import { DsfMobileRadioBox, DsfMobileCheckBox, DsfMobileOuaSelect, DsfMobileSelect, DsfMobileDataChoice } from "./items";
import { DsfMobileViewPart, DsfMobileRow, DsfMobileCol, DsfMobileSection } from './layout';
import { DsfMobileTreeInfo, DsfMobileTreeSelector, DsfMobileCheckNode } from './tree';
import { DsfMobileUpdateTime, DsfMobileCreateTime, DsfMobileUpdateUserName, DsfMobileCreateUserName, DsfMobileCreateUserId, DsfMobileUpdateUserId, DsfMobileOrder, DsfMobileDeletedStatus } from './system';
import { DsfMobileUploadFile, DsfMobileUploadImg, DsfMobileUploadPhoto } from './upload';
import { DsfMobileDatePicker, DsfMobileDateIntervalPicker, DsfMobileTimePicker } from './date';
import { DsfMobileSubTable } from './sub';
import { DsfMobileFlowInfo, DsfMobileFlowProcess, DsfMobileDisposeIdea, DsfMobileChooseSendNode, DsfMobileChooseUsers, DsfMobileFormCommentBox, DsfMobileReChooseUsers, DsfMobileAddOpinion, DsfMobileFlowAddComment } from './flow';
// 设计类
import { DsfMobileUploadDesignImg } from './design';
// 文字组件
import { DsfMobileLabel, DsfMobileTitle, DsfMobileTitle1, DsfMobileTitle2, DsfMobileTitle3, DsfMobileTitle4, DsfMobileTitle5, DsfMobileTitle6, } from "./text";
// 卡片类
import * as cardCtrls from './card';

//引入移动端端公共弹层
import layer from "../layer/layer";
dsf.mix(dsf.layer, { 'mobile': layer });

const components = [
  DsfMobileDialog,
  VanMultiplePicker,
  DsfMobileLoading,
  DsfMobileButton,
  DsfCalendar,
  DsfCalendarTimeBox,
  DsfMobilePage,
  DsfMobileButtonBar,
  DsfMobileNavTab,
  DsfDemoBrowserNavBar,
  DsfMobileButtonNav,
  DsfMobileBanner,
  DsfMobileNavMenu,
  DsfMobileNavMenuCell,
  DsfMobileCalendarNav,
  DsfMobileRollingNews,
  DsfMobileNavBar,
  DsfMobileHtml,
  DsfMobileDataGrid,
  DsfMobileNewsInfo,
  DsfMobileNewsDetail,
  DsfMobileVote,
  DsfMobileExam,
  DsfMobileTextBox,
  DsfMobileTextArea,
  DsfMobileAutoComplete,
  DsfMobileUeditor,
  DsfMobileRadioBox,
  DsfMobileCheckBox,
  DsfMobileOuaSelect,
  DsfMobileSelect,
  DsfMobileDataChoice,
  DsfMobileViewPart,
  DsfMobileRow,
  DsfMobileCol,
  DsfMobileSection,
  DsfMobileTreeInfo,
  DsfMobileTreeSelector,
  DsfMobileCheckNode,
  DsfMobileUpdateTime,
  DsfMobileCreateTime,
  DsfMobileUpdateUserName,
  DsfMobileCreateUserName,
  DsfMobileCreateUserId,
  DsfMobileUpdateUserId,
  DsfMobileOrder,
  DsfMobileDeletedStatus,
  DsfMobileUploadFile,
  DsfMobileUploadImg,
  DsfMobileUploadPhoto,
  DsfMobileDatePicker,
  DsfMobileDateIntervalPicker,
  DsfMobileTimePicker,
  DsfMobileSubTable,
  DsfMobileFlowInfo,
  DsfMobileFlowProcess,
  DsfMobileDisposeIdea,
  DsfMobileChooseSendNode,
  DsfMobileChooseUsers,
  DsfMobileFormCommentBox,
  DsfMobileReChooseUsers,
  DsfMobileAddOpinion,
  DsfMobileFlowAddComment,
  DsfMobileUploadDesignImg,
  DsfMobileLabel,
  DsfMobileTitle,
  DsfMobileTitle1,
  DsfMobileTitle2,
  DsfMobileTitle3,
  DsfMobileTitle4,
  DsfMobileTitle5,
  DsfMobileTitle6,
  ...Object.values(cardCtrls)
]

function install(Vue) {
  Vue.prototype.pxToRem = function (v) {
    return dsf.pxToRem(v);
  };

  // 注册移动端事件
  RegisterMobileNativeEvent();

  components.forEach((it) => {
    Vue.component(it.name, it);
    if (!window.$components['platform']) {
      window.$components['platform'] = {}
    }
    window.$components['platform'][it.name] = it;
  })
}

if (typeof window !== "undefined" && window.Vue) {
  install(window.Vue);
}

export {
  install
};
export default install