<template>
  <div class="ds-control ds-form-item ds-upload upload-file mobile" :class="getCss">
    <template v-if="simple&&readOnly&&!isDesign">
      <DsfTextProxy v-model="value" v-bind="_self.$props"></DsfTextProxy>
    </template>
    <template v-else>
      <van-field
        :label="showLabel && label ? label : ''"
        :required="required"
        :label-width="getLabelWidth(true)"
        :error-message="errorsMsg"
        :error-message-align="$inputAlign">
        <template #input>
          <van-uploader
            v-model="fileListCopy"
            :after-read="afterRead"
            :before-read="beforeRead"
            :before-delete="beforeDelete"
            :accept="isAndroid ? '' : uploadFileExts"
            :multiple="multiple"
            :max-size="uploadFileSize"
            :max-count="uploadFileNum"
            :show-upload="!readOnly"
            @oversize="onOversize">
            <template #preview-cover="file">
              <div class="preview-cover">
                <p class="van-ellipsis">{{ file.originalFileName }}</p>
                <p class="bye-size">{{ computeFileSize(file.size) }}</p>
              </div>
              <div class="preview" @click="handleColumnClick('view', file)">预览</div>
            </template>
          </van-uploader>
        </template>
      </van-field>
    </template>
  </div>
</template>

<script>
export default dsf.component({
  name: "DsfMobileUploadFile",
  mixins: [$mixins.formControl],
  ctrlCaption: "文件上传",
  props: {
    simple: {
      type: Boolean,
      default: false
    },
    showLabel: {
      type: Boolean,
      default: true
    },
    formatter: {
      type: String,
      default: "multipleFiles^originalFileName"
    },
    label: {
      type: String,
      default: "文件上传"
    },
    multiple: {
      type: Boolean,
      default: true
    },
    uploadFileNum: {
      type: [Number, String],
      default: 5
    },
    uploadFileSize: {
      type: [Number, String],
      default: 1024000
    },
    zipFileName: {
      type: String,
      default: ""
    },
    required: {
      type: Boolean,
      default: false
    },
    requestUrl: {
      type: String,
      default: ""
    },
    extsSetting: {
      type: Object,
      default() {
        return {};
      }
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("file-meta-data");
      }
    },
    inputAlign: {
      type: String,
      default: "right",
    },
  },
  data() {
    return {
      previewPopup: false, //预览文件pop
      filePath: "", // 预览文件url
      fileListCopy: [], // 文件列表
      uploadFileType: "file", //默认上传类型
      uploadFileExts: "", //允许上传后缀名
      acceptMime: "", // 弹窗筛选条件MiMe类型
      isAndroid: false
    };
  },
  computed: {
    $inputAlign() {
      return this.readOnly ? "right" : this.inputAlign;
    },
    setRequestUrl() {
      return this.requestUrl || "file/upload/";
    }
  },
  watch: {
    value(to) {
      this.initValue(to);
    }
  },
  created() {
    this.initValue(this.value);
    this.rebuildDialogData();
    this.checkAndroid()
  },
  methods: {
    checkAndroid() {
      let u = window.navigator.userAgent;
      if (u.indexOf("Android")) {
        this.isAndroid =  true;
      }
    },
    initValue(to) {
      if (dsf.type(to) == "string") {
        this.fileListCopy = JSON.parse(to);
      }
    },
    beforeDelete(file, detail) {
      dsf.layer
        .confirm(`确定要删除【${file.originalFileName}】文件吗?`)
        .then(() => {
          let files = JSON.parse(JSON.stringify(this.fileListCopy));
          files.splice(detail.index, 1);
          this.emitValueChange(JSON.stringify(files));
          this.$nextTick(() => {
            this.emitFormValidate(this.fileListCopy);
          });
        })
        .catch((e) => e);
    },
    onOversize(file) {
      // 判断文件大小
      let isMaxSize = this.uploadFileSize ? file.size < this.uploadFileSize : true;
      if (!isMaxSize) {
        dsf.layer.message(`文件大小不能超过${this.computeFileSize(this.uploadFileSize)}`, false);
        return false;
      }
    },
    afterRead(files) {
      let _that = this;
      if (!files.length) {
        files = [files];
      }
      let baseIndex = this.fileListCopy.length - files.length;
      _.forEach(files, (file, index) => {
        let formData = new FormData();
        formData.append("file", file.file);
        _that.$http
          .post(this.setRequestUrl, formData, {
            headers: {
              "Content-Type": "multipart/form-data"
            }
          })
          .done((response) => {
            if (response.success && response.state == 20000) {
              let files = JSON.parse(JSON.stringify(this.fileListCopy));
              files[index + baseIndex] = response.data[0];
              this.emitValueChange(JSON.stringify(files));
              this.$nextTick(() => {
                this.emitFormValidate(this.fileListCopy);
              });
            }
          })
          .catch((err) => {
            dsf.layer.message(err.message, false);
          });
      });
    },
    beforeRead(files) {
      if (!files.length) {
        files = [files];
      }
      // 判断文件格式
      if (this.uploadFileExts) {
        _.forEach(files, (item) => {
          let f = item.name.split(".")
          let testmsg = f[f.length - 1].toLowerCase();
          if (!this.uploadFileExts.includes(testmsg)) {
            dsf.layer.message(`请选择包含这些格式的文件：${this.uploadFileExts}`, false);
            return false;
          }
        });
      }
      return true;
    },
    handleColumnClick(type, row, index) {
      const handler = {
        view: () => {
          let root = dsf.url.getWebPath("preview/file");
          const preUrl = dsf.config.setting_public_file_host;
          const relativePath = row.relativePath;
          this.filePath = `${root}?url=${preUrl}${relativePath}`;
          this.$openDialog({
            title: row.originalFileName || row.name,
            template: `<iframe :src="'${this.filePath}'" width="100%" height="100%" frameborder="0"></iframe>`,
            height: "100vh",
            width: "100vw",
            position: "right"
          });
        }
      };
      handler[type] && handler[type]();
    },
    computeFileSize(fileSize) {
      let filesizeUnit = "";
      //1048576
      if (parseInt(fileSize) > 1050000) {
        filesizeUnit = (fileSize / Math.pow(1024, 2)).toFixed(2) + "M";
      } else {
        filesizeUnit = (fileSize / Math.pow(1024, 1)).toFixed(2) + "K";
      }
      return filesizeUnit;
    },
    rebuildDialogData() {
      if (this.extsSetting.extStr) {
        let obj = this.extsSetting;
        // this.uploadFileType = 'file';//默认上传类型
        // this.uploadFileExts=""//允许上传后缀名
        // this.acceptMime =""//弹窗筛选条件MiMe类型
        this.uploadFileType = obj.fileType;
        this.isLinkeMine = obj.isLinkeMine;
        this.uploadFileExts = obj.extStr;
        this.acceptMime = obj.mineStr;
        // console.log("this.uploadFileExts", this.uploadFileExts);
        // console.log("this.acceptMime", this.acceptMime);
      }
    }
  }
});
</script>
