<template>
  <div class="ds-control mobile nav-bar-wrap" :style="{ position: isDesign ? 'relative' : 'relative' }" :class="getCss">
    <van-nav-bar :title="navTitle" @click-left="backPage()">
      <template #left v-if="showBack">
        <dsf-icon class="left-icon" name="left"></dsf-icon>
        {{label}}
      </template>
      <template v-if="rightIcon" #right>
        <slot name="right">
          <dsf-icon class="right-icon" :name="rightIcon" @click="$emit('right-click')"></dsf-icon>
        </slot>
      </template>
    </van-nav-bar>
  </div>
</template>
<script>
export default dsf.component({
  name: "DsfMobileNavBar",
  ctrlCaption: "返回",
  mixins: [$mixins.control],
  props: {
    label: {
      type: String,
      default: "返回"
    },
    navTitle: {
      type: String,
      default: ""
    },
    showBack: {
      type: Boolean,
      default: true
    },
    rightIcon: {
      type: String,
      default: ""
    },
  },
  data() {
    return {};
  },
  methods: {
    backPage() {
      this.$router.back();
    }
  }
});
</script>
<style lang="scss">
.nav-bar-wrap {
  // position: fixed;
  padding: 0 !important;
  &.design {
    position: relative;
  }
  // top: 0;
  // right: 0;
  // left: 0;
  // height: 45px;
  // line-height: 45px;
  .left-icon,
  .right-icon{
    color: #000;
    font-weight: 600;
  }
}
</style>
