<template>
  <div class="ds-control mobile ds-news-detail" :class="getCss">
    <template v-if="isDesign">
      <div class="title">人民至上，抓实抓细防汛救灾各项措施</div>
      <div class="user">
        <van-image width="40" height="40" class="img" :src="source.cover | imgFormat" fit="cover" round>
          <template v-slot:loading> <i class="iconfont icon-spinner2"></i>加载中</template>
          <template v-slot:error>
            <img :src="require(`_platform/assets/styles/themes/workFlow/default-header.png`)" alt="" />
          </template>
        </van-image>
        <div class="info">
          <p class="name">解放军报</p>
          <p class="date">2020-06-22 20:00</p>
        </div>
      </div>
      <div class="content">
        当前，全国防洪讯进入“七上八下‘阶段，长江流域中上游地区降雨偏多，黄河中上游，海河，松花江，淮河流域可能发生较重汛情。在防汛的关键时期，中央政治局常委会召开会议研究部署相关工作，习近平总书记主持会议并发表重要讲话，防汛救灾关系人民财产安全，关系粮食安全，经济安全，社会安全，国家安全。这为各方落实主体责任，加强统筹协调做好防汛救灾工作提供了根本遵循。
      </div>
    </template>
    <template v-else>
      <div class="title">{{ source.title }}</div>
      <div class="user" v-if="source">
        <van-image width="40" height="40" class="img" :src="source.cover | imgFormat" fit="cover" round>
          <template v-slot:loading> <i class="iconfont icon-spinner2"></i>加载中</template>
          <template v-slot:error>
            <img :src="require(`_platform/assets/styles/themes/workFlow/default-header.png`)" alt="" />
          </template>
        </van-image>
        <div class="info">
          <p class="name">{{ source.authorName }}</p>
          <p class="date">{{ source.publishTime }}</p>
        </div>
      </div>
      <div v-if="source.type === '0'" class="content" v-html="content"></div>
      <div v-if="source.type === '1'" class="content">
        <img :src="img.imagePath | imgFormat" v-for="(img, index) in source.articlesImagesVos" :key="index" alt="" />
      </div>
      <div v-if="source.type === '2'" class="content">
        <video controls width="100%" :src="source.video | imgFormat"></video>
      </div>
      <iframe v-if="source.type === '3'" :src="source.externalLink" frameborder="0" width="100%" height="100%" class="content"></iframe>
      <van-empty description="无数据" v-if="!source.content && !source.articlesImagesVos && !source.video && !source.externalLink" />
    </template>
  </div>
</template>
<script>
export default dsf.component({
  name: "DsfMobileNewsDetail",
  mixins: [$mixins.control],
  ctrlCaption: "新闻详情",
  props: {
    url: {
      type: String,
      default: "cms/articles/queryArticlesById",
    },
  },
  data() {
    return {
      source: {},
    };
  },
  computed: {
    content() {
      if (this.source.content && typeof this.source.content === "string") {
        return this.source.content.replace(/(\d+)px/g, (s, t) => {
          s = s.replace("px", "");
          const value = parseInt(s) * 0.01;
          return `${value}`;
        });
      }
      return "";
    },
  },
  created() {
    if (this.url) {
      this.loadData();
    }
  },
  methods: {
    params() {
      return { id: this.$route.query.id };
    },
    loadData() {
      this.$http.get(this.url, this.params()).then(({ data }) => {
        this.source = data.data;
      });
    },
  },
});
</script>
