<template>
  <div
    class="ds-control mobile ds-me-user-info"
    :class="{ 'ds-no-padding': !isDesign }"
    :style="controlStyle"
  >
    <div class="ds-me-user-info-wrap">
      <div class="ds-me-user-info-head" @click="headClick">
        <van-image width="80" height="80" fit="cover" :src="photoSrc">
          <template v-slot:error>
            <van-image
              width="80"
              height="80"
              fit="cover"
              :src="$replace(dsf.config.setting_public_user_default_header)"
            ></van-image>
          </template>
        </van-image>
      </div>
      <div class="ds-me-user-info-name">{{ (info && info.name) || "" }}</div>
      <div class="ds-me-user-info-company one-line">
        {{ (info && info.unitDuty) || "" }}
      </div>
    </div>
    <van-overlay :show="browseHeader" @click="browseHeader = false">
      <div class="ds-me-user-info-head-browse-box" @click.stop>
        <div class="ds-me-user-info-head-browse">
          <van-image width="100%" height="100%" fit="cover" :src="photoSrc">
            <template v-slot:error>
              <van-image
                width="100%"
                height="100%"
                fit="cover"
                :src="$replace(dsf.config.setting_public_user_default_header)"
              ></van-image>
            </template>
          </van-image>
          <div class="ds-me-user-info-head-browse-btns">
            <van-button type="default" @click="browseHeader = false">
              关闭
            </van-button>
            <van-button type="info" @click="uploadPhotoFn">
              重新上传
            </van-button>
          </div>
        </div>
      </div>
    </van-overlay>
    <DsfMobileUploadPhoto
      ref="upload-photo"
      class="ds-me-user-info-upload-photo"
      aspect-ratio="1/1"
      requestUrl="/file/upload"
      simple
      is-cut-pic
      @upload-complete="headUploadComplete"
    ></DsfMobileUploadPhoto>
  </div>
</template>
<script>
export default dsf.component({
  name: "DsfMobileMeUserInfo",
  ctrlCaption: "我的-用户信息",
  mixins: [$mixins.control],
  props: {
    height: {
      type: String,
      default: "auto",
    },
    padding: {
      type: Array,
      default: () => [20, 0, 20, 0],
    },
    bgColor: {
      type: String,
      default: "",
    },
    info: {
      type: Object,
      default: () => ({
        photo: "",
        name: "员工名称",
        unitDuty: "xxxxxxxxx科技股份有限公司员工",
      }),
    },
    url: {
      type: String,
      default: "",
    },
    updatePhotoUrl: {
      type: String,
      default: "/teas/mobile/student/updatePhoto",
    },
  },
  computed: {
    photoSrc() {
      let src = this.info?.photo?.[0]?.relativePath;
      return src? dsf.url.getWebPath(src) : this.$replace(dsf.config.setting_public_user_default_header);
    },
    controlStyle() {
      let { padding, height, bgColor, isDesign } = this;
      let style = {
        height,
        padding: padding.join("px ") + "px",
      };
      if (isDesign) {
        style.padding = "10px";
      }
      if (bgColor) {
        style.backgroundColor = bgColor;
      }
      return style;
    },
  },
  data() {
    return {
      browseHeader: false,
    };
  },
  watch: {
    url: {
      immediate: true,
      handler() {
        this.getInfo();
      },
    },
  },
  methods: {
    getInfo() {
      let { url, isDesign } = this;
      this.$emit("update:info", {});
      if (!url || isDesign) return;
      let params = {};
      this.$http
        .get(url, params)
        .done(({ success, data, message}) => {
          if (success) {
            data.photo = JSON.parse(data.photo);
            this.$emit("update:info", data || {});
          } else {
            dsf.layer.message(message, false);
          }
        })
        .error((error) => {
          dsf.layer.message("获取个人信息，未知错误！", false);
          console.error(error);
        });
    },
    headClick() {
      if (this.isDesign) return;
      this.browseHeader = true;
    },
    uploadPhotoFn() {
      let uploadPhoto = this.$refs["upload-photo"];
      if (uploadPhoto) {
        uploadPhoto.$el.querySelector("input")?.click();
      }
    },
    headUploadComplete({ args }) {
      if (args && args.success) {
        this.updatePhoto(args.data);
      }
      let uploadPhoto = this.$refs["upload-photo"];
      uploadPhoto.beforeDelete();
    },
    updatePhoto(photo) {
      let params = {
        photo: JSON.stringify(photo),
      };
      this.$http
        .post(this.updatePhotoUrl, params)
        .done(({ success, message}) => {
          if (success) {
            this.getInfo();
          } else {
            dsf.layer.message(message, false);
          }
        })
        .error(() => {
          dsf.layer.message("上传头像失败", false);
        });
    },
  }
});
</script>
