<template>
  <div class="ds-control ds-form-item ds-textbox mobile" :class="getCss">
    <template v-if="simple&&readOnly&&!isDesign">
      <DsfTextProxy v-model="value" v-bind="_self.$props"></DsfTextProxy>
      <slot name="error">
        <div v-if="errors.length>0" class="ds-error-text">{{ errorsMsg }}</div>
      </slot>
    </template>
    <template v-else>
      <van-field
        v-if="!readOnly"
        v-model="inputValue"
        :placeholder="placeholder"
        :disabled="disabled"
        :label-width="getLabelWidth(true)"
        :label="(showLabel&&label?label:'')"
        :required="!readOnly? required: false"
        :input-align="$inputAlign"
        :class="{'ds-error-border':errors.length>0}"
        :error-message-align="$inputAlign"
        @focus="inputFocusHandler">
        
        <template v-slot:prefix>
          <slot name="prefix"></slot>
        </template>
        <template v-slot:suffix>
          <slot name="suffix"></slot>
        </template>
      </van-field>
      <!--只读状态只显示div-->
      <div v-else-if="readOnly" class="ds-form-readonly">
        <DsfTextProxy v-model="value" v-bind="_self.$props" :show-label="false"></DsfTextProxy>
      </div>
      <slot name="error">
        <div v-if="errors.length>0" class="ds-error-text">{{ errorsMsg }}</div>
      </slot>
      <DsfMobileDialog ref="mobileDialog" :position="position" :width="dialogWidth" :visible.sync="dialogVisible" height="100%" :show-close="false" @close="onDialogClose" @open="onDialogOpen">
        <div class="ds-autocomplete-dialog">
          <form action="/">
            <van-search v-model="searchValue" placeholder="请输入搜索关键词" input-align="center" show-action @input="onSearchInput" @search="onSearch" @cancel="onSearchCancel" />
          </form>
          <van-list v-if="showList" v-model="searchLoading" :finished="finished" finished-text="没有更多了" @load="onSearchLoad">
            <van-cell v-for="(item,it) in list" :key="it" :title="item.label" :value="item.value" @click="checkItem(item)">
              <template #default>
                <span></span>
              </template>
              <template #right-icon>
                <dsf-icon v-if="item.checked" name="duigou"></dsf-icon>
              </template>
            </van-cell>
          </van-list>

          <template v-else>
            <van-cell class="search-tags">
              <van-tag v-for="(item, it) in searchHistory" :key="it" type="primary" size="large" plain closeable @close="closeTag(item)" @click="tag2search(item)">
                {{ item }}
              </van-tag>
            </van-cell>
            <p v-if="searchHistory && searchHistory.length>0" class="search-bottom" @click="clearTag">清除所有搜索历史</p>
          </template>
        </div>
      </DsfMobileDialog>
    </template>
  </div>
</template>

<script>
export default dsf.component({
  name: "DsfMobileAutoComplete",
  mixins: [$mixins.formControl],
  ctrlType: "autocomplete",
  ctrlCaption: "自动填充",
  props: {
    placeholder: {
      type: String,
      default: "请输入"
    },
    inputAlign: {
      type: String,
      default: "right"
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("text-meta-data");
      }
    },
    metaDataType: {
      type: String,
      default: "0"
    },
    chooseMetaData: {
      type: Object,
      default() {
        return {};
      }
    },
    source: {
      type: String,
      default: ""
    },
    pageSize: {
      type: Number,
      default: 10
    },
    position: {
      type: String,
      default: "right"
    },
    dialogWidth: {
      type: String,
      default: "100%"
    },
    autoSwitch: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    $inputAlign() {
      return this.readOnly ? "right" : this.inputAlign;
    },
    inputValue: {
      get() {
        return this.value;
      },
      set(v) {
        this.emitValueChange(v);
      }
    },
    searchUrl() {
      return dsf.express.replace(this.source);
    }
  },
  data() {
    return {
      dialogVisible: false,
      searchValue: "",
      list: [], //搜索出来的下拉项
      pageNum: 1, //当前页码
      totalPage: null, //总页数
      totalRow: null, //总条数
      finished: false,
      searchLoading: false,
      showList: false, //是否显示搜索返回的下拉项
      searchHistory: [],
      lastSearchInputTime: null //用于searchInput 函数节流
    };
  },
  methods: {
    inputFocusHandler() {
      this.dialogVisible = true;
    },
    onDialogClose() {
      this.dialogVisible = false;
    },
    onDialogOpen() {
      //初始化搜索参数
      this.searchValue = "";
      this.list = [];
      this.pageNum = 1;
      this.totalPage = null;
      this.totalRow = null;
      this.finished = false;
      this.searchLoading = false;
      this.showList = false;
      const _searchHistory = localStorage.getItem("autocomplete-search-history");
      this.searchHistory = _searchHistory ? _searchHistory.split(",") : [];
    },
    onSearchInput() {
      let now = new Date().getTime();
      if (!this.lastSearchInputTime || now - this.lastSearchInputTime > 300) {
        if (this.searchValue) {
          this.doSearch();
        } else {
          this.showList = false;
          this.list = [];
        }
        this.pageNum = 1;
        this.list = [];
        this.lastSearchInputTime = now;
      }
    },
    onSearch(val) {
      this.pageNum = 1;
      this.list = [];
      this.doSearch();
      setSearch2localHistory.call(this);
    },
    doSearch() {
      this.showList = true;
      this.finished = false;
      this.$http
        .get(this.searchUrl, {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          keyWord: this.searchValue
        })
        .done(({ data }) => {
          //this.pageNum = data.pageNum;
          this.totalPage = data.totalPage;
          this.totalRow = data.totalRow;
          if (_.isArray(data.list)) {
            data.list = data.list.map((item) => {
              item.checked = false;
              return item;
            });
            this.list = this.list.concat(data.list);
          }
          if (this.pageNum >= data.totalPage) {
            this.finished = true;
          }
        })
        .error((err) => {
          dsf.layer.message("搜索失败", false);
          this.finished = true;
        })
        .always(() => {
          this.searchLoading = false;
        });
    },
    onSearchCancel() {
      this.dialogVisible = false;
    },
    onSearchLoad() {
      this.pageNum += 1;
      this.doSearch();
    },
    checkItem(v) {
      v.checked = true;
      this.inputValue = v.value;
      setSearch2localHistory.call(this);

      //填充数据到其它控件
      if (this.$vm && this.autoSwitch) {
        let dict = this.$vm.getScopeMetaDataDict(this);
        if (this.local) {
          _.forOwn(v, (val, key) => {
            _.forOwn(dict, (dictVal, dictKey) => {
              let arr = dictKey.split(".");
              let smallKey = arr[1] ? arr[1] : dictKey;
              let fullKey = dictKey;
              if (dictVal.fieldsLength === 1) {
                if (dictKey === key) {
                  this.$vm.$set(this.local, dictKey, val);
                } else {
                  // let last = dictKey.lastIndexOf(".");
                  // let lastStr = dictKey.substring(last + 1, dictKey.length);
                  smallKey === key && this.$vm.$set(this.local, dictKey, val);
                }
              } else if (dictVal.fieldsLength > 1) {
                if (dictKey === key) {
                  this.$vm.$set(this.local, dictKey, val);
                } else {
                  let obj = {};
                  _.each(dictVal.valueAttributes, (el, i) => {
                    let k = smallKey + "_" + el;
                    if (dsf.isDef(v[k])) {
                      obj[el] = v[k];
                    }
                  });
                  if (Object.keys(obj).length == dictVal.valueAttributes.length) {
                    this.$vm.$set(this.local, dictKey, obj);
                  }
                }
              }
            });
          });
        }
      }

      //验证v.label
      this.$nextTick(() => {
        this.dialogVisible = false;
        let _val = v.label || v.value;
        this.emitFormValidate(_val);
        this.$nextTick(() => {
          this.$dispatch("select-complete", v);
        });
      });
    },
    closeTag(item) {
      let index = this.searchHistory.indexOf(item);
      this.searchHistory.splice(index, 1);
      localStorage.setItem("autocomplete-search-history", this.searchHistory.join(","));
    },
    clearTag() {
      this.searchHistory = [];
      localStorage.removeItem("autocomplete-search-history");
    },
    tag2search(val) {
      this.searchValue = val;
      this.doSearch();
    }
  }
});

function setSearch2localHistory() {
  let index = this.searchHistory.indexOf(this.searchValue);
  if (index !== -1) return;
  this.searchHistory.push(this.searchValue);
  localStorage.setItem("autocomplete-search-history", this.searchHistory.join(","));
}
</script>