
import FlowDemo from "../workFlow/flowDemo";
import IframeLoader from "_platform/output/iframeloader.vue";
// import test from "../test.vue"
let router = [
  {
    path: "/mobile/flow",
    component: FlowDemo
  },
  {
    path: "/iframeloader/:state?",
    name: "iframeloader",
    component: IframeLoader,
    meta: {
      title: ""
    },
    props: route => {
      return {
        src: route.query.src
      };
    }
  }
];

export default router;