<template>
  <div class="ds-control mobile ds-exam" :class="getCss">
    <div class="header">
      <span>第{{ many }}题</span>
      <van-button round color="#FFB700" size="small">{{ countTime }}秒</van-button>
    </div>
    <div class="title">
      {{ source && source.name }}
    </div>
    <template v-if="results && results.length === 0">
      <div class="item" v-for="(item, index) in source.voteOptionsVos" :key="index" @click="checkItem(item)">
        <van-button
          :class="{
            'ds-button': checked.indexOf(item) > -1,
          }"
          class="mobile"
          type="default"
          block
        >
          {{ item.name }}
        </van-button>
      </div>
    </template>
    <template v-if="results && results.length > 0">
      <div class="item result" v-for="(item, index) in results" :key="index">
        <p class="name">{{ item.name }}</p>
        <div class="process">
          <span
            class="process-item"
            :style="{
              width: (item.voteCount / joinNumber) * 100 + '%',
            }"
          ></span>
          <span class="person">{{ item.voteCount }}人</span>
        </div>
      </div>
    </template>
    <div v-if="source && source.voteOptionsVos && results && results.length === 0" class="add" @click="sumbit">
      <van-button class="ds-button mobile submit" type="default" :disabled="checked.length === 0"> {{ submitBtn || "提交" }} </van-button>
    </div>
  </div>
</template>
<script>
export default dsf.component({
  name: "DsfMobileExam",
  mixins: [$mixins.control],
  ctrlCaption: "知识竞赛",
  props: {
    url: {
      type: String,
      default: "vote/basic/queryVoteInfo",
    },
    resultUrl: {
      type: String,
      default: "vote/basic/queryVoteStatisticsInfo",
    },
    saveUrl: {
      type: String,
      default: "vote/record/saveUserVoteRecord",
    },
    submitBtn: {
      type: String,
      default: "提交",
    },
  },
  data() {
    return {
      many: 1,
      countTime: 120,
      source: {
        voteOptionsVos: [],
      },
      checked: [],
      multiple: false,
      disabled: false,
      joinNumber: 0,
      results: [],
    };
  },
  created() {
    if (this.isDesign) {
      this.source.name = "标志着中国新民主主义革命开端的历史事件是_______________？";
      this.source.voteOptionsVos = [
        {
          name: "A: 选项名称",
        },
        {
          name: "B: 选项名称",
        },
        {
          name: "C: 选项名称",
        },
        {
          name: "D: 选项名称",
        },
      ];
    } else {
      if (this.url) {
        // this.loadResult();
        this.loadData();
      }
    }
  },
  methods: {
    loadData() {
      if (!this.$route.query.voteId) {
        dsf.layer.message("投票id不能为空", false);
        return;
      }
      this.$http.get(this.url, { ...this.$route.query }).then(({ data }) => {
        if (data && data.data) {
          if (!data.data.isVote) {
            this.loadResult();
            return;
          }
          this.source = data.data;
          this.multiple = this.source.maxNumber > 1;
        }
      });
    },
    checkItem(item) {
      if (this.multiple) {
        const index = this.checked.indexOf(item);
        if (index > -1) {
          this.checked.splice(index, 1);
        } else {
          this.checked.push(item);
        }
      } else {
        this.checked = [item];
      }
    },
    sumbit() {
      if (this.checked.length === 0 || this.disabled) {
        dsf.layer.message("请选择选项", false);
        return;
      }
      this.disabled = true;
      const param = JSON.parse(JSON.stringify(this.checked));
      this.$http.post(this.url, param).then(({ data }) => {
        if (data.data.success) {
          dsf.layer.message("保存成功");
          this.loadResult();
        } else {
          dsf.layer.message(data.message, false);
        }
        this.disabled = false;
      });
    },
    loadResult() {
      this.$http.get(this.resultUrl, { ...this.$route.query }).then(({ data }) => {
        this.source.name = data.data.name;
        this.joinNumber = data.data.joinNumber;
        this.results = data.data.optionsStatistics;
      });
    },
  },
});
</script>
