<template>
  <div v-if="isDesign" class="ds-platform-card-item-by-course mobile">
    <div class="cover">
      <van-image src="https://cube.elemecdn.com/6/94/4d3ea53c084bad6931a56d5158a48jpeg.jpeg" fit="cover"></van-image>
    </div>
    <div class="content">
      <div class="title ds-multi-ellipsis-2">
        <span style="color: #FF3600;font-weight: bold;margin-right: 5px">NEW</span>
        <span>课题课题课题课题课题课题课题课题课题课题课题课题课题课题课题课题课题</span>
      </div>
    </div>
    <div class="footer">
      <span>张三</span>
      <span><i class="iconfont icon-guankanbeifen4" style="font-size: .9em"></i> 1234</span>
    </div>
  </div>
  <div v-else class="ds-platform-card-item-by-course mobile" @click="_clickBefore(itemClick)">
    <div class="cover">
      <van-image :src="data['cover'] | imgFormat" fit="cover"></van-image>
    </div>
    <div class="content">
      <div class="title ds-multi-ellipsis-2">
        <span v-if="data['isNew']" style="color: #FF3600;font-weight: bold;">NEW </span>
        <span>{{ data['title'] }}</span>
      </div>
    </div>
    <div class="footer">
      <span class="ds-multi-ellipsis-1">{{ data['teacher'] }}</span>
      <span><i class="iconfont icon-guankanbeifen4" style="font-size: .9em"></i> {{ data['views'] || 0 }}</span>
    </div>
  </div>
</template>

<script>
export default dsf.component({
  name: "DsfMobilePlatformCardItemByCourse",
  ctrlCaption: "课程卡片",
  mixins: [$mixins.card],
  props: {
    keysMap: {
      type: Array,
      default() {
        return [
          { from: 'cover', to: 'cover', type: 'text', desc: '封面' },
          { from: 'isNew', to: 'isNew', type: 'boolean', desc: '标签' },
          { from: 'title', to: 'title', type: 'text', desc: '课题' },
          { from: 'teacher', to: 'teacher', type: 'text', desc: '授课教师' },
          { from: 'views', to: 'views', type: 'text|number', desc: '浏览量' }
        ];
      }
    },
    // 跳转url
    openUrl: {
      type: String,
      default: ''
    }
  },
  methods: {
    itemClick() {
      if (!this.openUrl) return;
      window.location.href = this.$replace(this.openUrl, this.local);
    }
  }
});
</script>