<template>
  <div class="ds-control ds-form-item ds-date-picker mobile" :class="getCss">
    <template v-if="simple && readOnly && !isDesign">
      <DsfTextProxy v-model="value" v-bind="_self.$props"></DsfTextProxy>
    </template>
    <template v-else-if="readOnly && !isDesign">
      <van-field
        :value="value | proxy('dateTime^' + $timeFormat)"
        :label-width="getLabelWidth(true)"
        :label="(showLabel && label) || ''"
        :disabled="disabled"
        :readonly="readOnly"
        :error-message="errorsMsg"
        :error-message-align="inputAlign"
        input-align="left"
      >
        <template v-if="hasIcon" #right-icon>
          <i class="iconfont icon-tab_attendance_d"></i>
        </template>
      </van-field>
    </template>
    <template v-else>
      <van-field
        :value="value | proxy('dateTime^' + $timeFormat)"
        v-bind="attr"
        v-on="events"
      >
        <template v-if="hasIcon" #right-icon>
          <i class="iconfont icon-tab_attendance_d"></i>
        </template>
      </van-field>
      <van-popup
        v-if="!isDesign"
        v-model="showPicker"
        get-container="#app"
        position="bottom"
        round
      >
        <dsf-calendar
          :show="showPicker"
          :default-value="currentDate"
          :type="chooseType"
          :min-date="minDate"
          :max-date="maxDate"
          :clearable="clearable"
          :error-message="errorsMsg"
          :error-message-align="inputAlign"
          :default-view="pickerStepTypeStr"
          :default-view-date="
            (pickerInitDate && new Date(pickerInitDate)) || undefined
          "
          @cancel="showPicker = false"
          @confirm="onConfirm"
        />
      </van-popup>
    </template>
  </div>
</template>

<script>
import dateMixin from "./dateMixin";
export default dsf.component({
  name: "DsfMobileDatePicker",
  mixins: [$mixins.formControl, dateMixin],
  ctrlCaption: "日期选择",
  props: {
    value: {
      type: String,
      default: "",
    },
    label: {
      type: String,
      default: "日期选择",
    },
    placeholder: {
      type: String,
      default: "请选择",
    },
    // 是否显示清除按钮
    clearable: {
      type: Boolean,
      default: false,
    },
    // 弹框初始时间
    pickerInitDate: {
      type: Number,
      default: null,
    },
    // 日期选择器类型
    chooseType: {
      type: String,
      default: "date",
    },
    // 时间选择步骤
    // 默认直接选择日
    pickerStepType: {
      type: [Number, String],
      default: 0
    },
    // 显示小时段
    timeInter: {
      type: Array,
      default() {
        return ["00:00:00", "23:59:59"];
      },
    },
    // 时间显示格式
    timeFormat: {
      type: String,
      default: "yyyy-MM-dd",
    },
    //只读状态下格式化
    formatter: {
      type: String,
      default: "dateTime^yyyy-mm-dd",
    },
    // 文字对齐方式
    inputAlign: {
      type: String,
      default: "right",
    },
    // 是否有右侧图标
    hasIcon: {
      type: Boolean,
      default: true,
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("date-picker-meta-data");
      },
    },
  },
  data() {
    return {
      currentDate: null,
      showPicker: false,
    };
  },
  computed: {
    // todo 兼容代码，后续版本可能会去掉
    pickerStepTypeStr() {
      let type = this.pickerStepType;
      if (dsf.type(type) == 'string') {
        return type;
      }
      let config = {
        0: 'day',
        1: 'month'
      };
      return config[type];
    },
    attr() {
      let res = {
        labelWidth: this.getLabelWidth(true),
        label: (this.showLabel && this.label) || "",
        placeholder: this.placeholder,
        disabled: this.disabled,
        readonly: true,
      };
      if (!this.readOnly) {
        res.inputAlign = this.inputAlign;
        res.required = this.required;
        res.errorMessage = this.errorsMsg;
        res.errorMessageAlign = this.inputAlign;
        if (this.hasIcon) {
          res.rightIcon = "notes-o";
        }
      } else {
        res.inputAlign = "left";
      }
      return res;
    },
    events() {
      let res = {};
      if (!this.readOnly && !this.disabled) {
        res.click = this.fieldClick;
      }
      return res;
    },
    $timeFormat() {
      let timeFormat = this.timeFormat.replace(/M/g, "m");
      if (this.chooseType === "datetime") {
        return timeFormat + " hh:ii";
      }
      if (this.chooseType === "datetime_s") {
        return timeFormat + " hh:ii:ss";
      }
      return timeFormat;
    },
    valueFormat() {
      if (this.chooseType === "datetime_s" || this.chooseType === "datetime") {
        return "yyyy-mm-dd hh:ii:ss";
      }
      return "yyyy-mm-dd";
    },
  },
  watch: {
    value(to) {
      this.initValue(to);
    },
  },
  created() {
    this.initValue(this.value);
  },
  methods: {
    initValue(to) {
      if (
        this.simple ||
        this.readOnly ||
        this.isDesign ||
        dsf.isUnDef(to) ||
        _.isEqual(to, this.valueCopy)
      ) {
        return;
      }
      if (to && dsf.type(to) == "string") {
        this.currentDate = dsf.date.parse(to, this.valueFormat);
      }
    },
    fieldClick() {
      this.showPicker = true;
    },
    onConfirm(date) {
      this.showPicker = false;
      if (dsf.type(date) === 'array') {
        date = date?.[0];
      }
      let value = date ? dsf.date.format(date, this.valueFormat) : null;
      this.emitValueChange(value);
      this.$dispatch("change", value);
      this.$nextTick(() => {
        this.emitFormValidate(value);
      });
    },
  },
});
</script>