<template>
  <div v-if="isDesign" class="ds-control ds-form-item ds-textbox mobile" :class="getCss">
    <template>
      <van-field
        v-model="chooseFlow.name"
        type="text"
        :disabled="true"
        :label-width="getLabelWidth(true)"
        :label="'流程信息'"
        input-align="right"
      ></van-field>
    </template>
  </div>
</template>
<script>
export default dsf.component({
  name: "DsfMobileFlowInfo",
  ctrlCaption: "流程信息",
  mixins: [$mixins.formControl, $mixins.flowInfo],
  methods: {
    sendBack(args, id) {
      let _this = this;
      this.$openDialog({
        showClose: false,
        title: "退回理由",
        fullscreen: true,
        width: "100vw",
        height: "100vh",
        template: `<van-field
            ref="back"
            v-model="params.value"
            rows="2"
            type="textarea"
            placeholder="请输入退回理由"
          />`,
        btnPosition: "bottom",
        params: {
          value: "",
        },
        btns: [
          {
            text: "确认退回",
            handler: (component, refs) => {
              if (!refs.back.value) {
                dsf.layer.message("请输入退回理由", false);
                return false;
              }
              let param = {
                sPID: args[0] || null,
                sCurLinkID: args[1] || null,
                sReason: refs.back.value || null,
                sBackLinkID: id,
              };
              return this.$http
                .post("wfr/sendBack", param)
                .then((res) => {
                  if (res.data.success && res.data.data && res.data.data.Code == "21000") {
                    if (window.$isMobile) {
                      this.$router.go(-1);
                      return true;
                    }
                    if (this.$vm && this.$vm.$dialog) {
                      this.$vm.$dialog.openerVm.reloadData();
                    } else {
                      this.$vm.openerVm && this.$vm.openerVm.reloadData && this.$vm.openerVm.reloadData();
                    }
                    dsf.layer.message("退回成功", true);
                  } else {
                    dsf.layer.message(res.data.data.Message || "退回异常", false);
                  }
                })
                .catch((res) => {
                  dsf.layer.message("退回异常", false);
                });
            },
          },
        ],
      });
    },
    chooseSendNode(data) {
      const showBack = data.SendParams.Lines && data.SendParams.Lines[0] && data.SendParams.Lines[0].SendToLineID ? true : false;
      this.BackLineID = showBack ? data.SendParams.Lines[0].SendToLineID : "";
      const height = data.SendParams.Lines.length * 50 + 62 + (showBack ? 44 : 0);
      this.dialog = this.$openDialog({
        height: height + "px",
        hasHeader: false,
        hasFooter: false,
        content: "DsfMobileChooseSendNode",
        params: {
          lines: data.SendParams.Lines,
          title: "请选择要发送的环节：",
          chooseNode: this.chooseNode,
          showBack: showBack,
          back: this.back,
        },
        btns: [],
      });
    },
    chooseNode(line) {
      let param = {
        sRequestID: this._RequestID,
        sLineID: line.LineID,
      };
      const url = "wfr/sendToLine";
      this.$http.post(url, param).then((res) => {
        if (res.data.state == 20000) {
          this.handler(res, true);
        } else {
          dsf.layer.message(res.data.Message || "发送异常", false);
          if (this.dialog) {
            this.dialog.close();
          }
        }
      });
    },
    back() {
      let param = {
        sRequestID: this._RequestID,
        sLineID: this.BackLineID,
      };
      this.$http.post("wfr/setLineToBack", param).then((res) => {
        if (res.data.state == 20000) {
          this.handler(res);
        }
      });
      if (this.dialog) {
        this.dialog.close();
      }
    },
    chooseSendTree(data, isShowBack) {
      this.dialog = this.$openDialog({
        showClose: false,
        title: "请选择办理人",
        fullscreen: true,
        width: "100vw",
        height: "100vh",
        class: "work_flow-usersDialog",
        content: "DsfMobileChooseUsers",
        btnPosition: "bottom",
        params: {
          lines: data.SendParams.Lines,
          isFristSend: this.isFristSend ? true : false,
        },
        btns: [
          {
            text: "取消",
          },
          {
            text: "返回",
            class: `choose-send-node ${isShowBack ? "" : "hide"}`,
            handler: (component) => {
              let param = {
                sRequestID: this._RequestID,
                sLineID: data.SendParams.Lines && data.SendParams.Lines[0] && data.SendParams.Lines[0].SendToLineID,
              };
              this.$http.post("wfr/setLineToBack", param).then((res) => {
                if (res.data.state == 20000) {
                  this.handler(res);
                }
              });
              if (this.dialog) {
                this.dialog.close();
              }
              return true;
            },
          },
          {
            text: "确定",
            handler: (component) => {
              if (component.validate()) {
                const result = component.yes();
                let param = {
                  sRequestID: this._RequestID,
                  sReceivers: result.sReceivers,
                };
                if (result.disposeIdea) {
                  let disposeIdea = result.disposeIdea;
                  // 发送时有处理意见
                  const sOpinion = {
                    Content: disposeIdea.inputValue,
                    Attachments: disposeIdea.multipleFiles,
                  };
                  param.sOpinion = JSON.stringify(sOpinion);
                }
                this.$http.post("wfr/sendToReceivers", param).then((res) => {
                  if (res.data.state == 20000) {
                    this.handler(res);
                  }
                });
                if (this.dialog) {
                  this.dialog.close();
                }
                return true;
              }
              return false;
            },
          },
        ],
      });
    },
  },
});
</script>
