<template>
  <div class="ds-forgot-password">
    <dsf-mobile-nav-bar label=""></dsf-mobile-nav-bar>
    <section>
      <!--验证码-->
      <div v-if="step == 0" class="ds-forgot-password-box">
        <div class="title">{{title}}</div>
        <div class="tips"></div>
        <label class="input">
          <i class="beforeIcon iconfont icon-lianxidianhua"></i>
          <input v-model.trim="phoneNumber" maxlength="20" type="text" placeholder="请输入手机号码">
        </label>
        <div class="input">
          <i class="beforeIcon iconfont icon-yanzhengma1"></i>
          <div class="input-bt" :class="[codeBtType]" @click.stop="getCode">
            {{ codeBtType != '' ? `${countdownTime}s` : '获取验证码' }}
          </div>
          <input v-model.trim="phoneCode" maxlength="12" type="text" placeholder="请输入验证码">
        </div>
        <div class="error-tips" v-if="codeAuthResult && phoneCode"> <i class="iconfont icon-tixingshixin" />{{codeAuthResult}}</div>
        <van-button
          :disabled="!phoneNumber.length || !phoneCode.length"
          :loading="isWaiting"
          type="primary"
          block
          @click="nextStep(1)">
          下 一 步
        </van-button>
      </div>
      <!--重置密码-->
      <div v-if="step == 1" class="ds-forgot-password-box">
        <div class="title">新密码设置</div>
        <div class="tips">{{notice}}</div>
        <label class="input">
          <i class="beforeIcon iconfont icon-password1"></i>
          <input type="password" v-model="newpwd" placeholder="请输入密码">
        </label>
        <label class="input">
          <i class="beforeIcon iconfont icon-password1"></i>
          <input type="password" v-model="confirmNewpwd" placeholder="请再次输入密码">
        </label>
        <van-button type="primary" :loading="isWaitingTwo" block @click="nextStep(2)" :disabled="!newpwd||(newpwd&&newpwd!==confirmNewpwd)">确 定</van-button>
      </div>
      <!--修改成功-->
      <div v-if="step == 2" class="ds-forgot-password-box">
        <div class="title"></div>
        <div class="success-image">
          <i class="iconfont icon-duigou"></i>
        </div>
        <p class="success-tip">修改密码成功</p>
        <van-button type="primary" block @click="reLogin">确 定</van-button>
      </div>
    </section>
  </div>
</template>

<script>
export default dsf.component({
  name: "DsfMobilePlatformForgotPassword",
  ctrlCaption: "忘记密码",
  mixins: [$mixins.control],
  props: {
    title:{
      type:String,
      default:'忘记密码'
    },
    intervalUrl:{
      type:String,
      default:'oua2/user/pwd/send/interval'
    },
    getCodeUrl:{
      type:String,
      default:'oua2/user/pwd/send'
    },
    authCodeUrl:{
      type:String,
      default:'oua2/user/pwd/token'
    },
    alterPwdUrl:{
      type:String,
      default:'oua2/user/pwd/update'
    }
  },
  data() {
    return {
      // 步骤
      step: 0,
      // 是否等待状态
      isWaiting: false,
      // 获取验证码按钮的状态。countdown：倒计时中
      codeBtType: "",
      // 验证码倒计时的时间
      countdownTime: 0,
      countdownTimes:10,
      notice:'',
      // 手机号
      phoneNumber: '',
      // 短信验证码
      phoneCode: '',
      //验证码换取的token
      token:'',
      // 验证码结果
      codeAuthResult:'',
      // 新设置密码
      newpwd: "",
      //确认新密码
      confirmNewpwd:'',
      // 第二步是否等待状态
      isWaitingTwo:false,
    }
  },
  watch: {
    phoneCode(){
      this.codeAuthResult=''
    }
  },
  mounted() {
    // 获取倒计时
    this.getInterval();
  },
  methods: {
    getInterval(){
      this.$http
        .get(this.intervalUrl, {}, $$webRoot.dsfa)
        .done(({ success, data, message }) => {
          if (success) {
            this.countdownTimes = data.interval;
            this.countdownTime = data.interval;
            this.notice = data.notice
          } else {
            dsf.layer.message(message || "请求异常", false);
          }
        })
        .error((err) => {
          dsf.error(err);
          dsf.layer.message(err?.message || "请求异常", false);
        })
        .always(() => {
        });
    },
    // 获取验证码
    getCode() {
      if (this.codeBtType != "") return;
      if (this.phoneNumber === "") {
        dsf.layer.message("手机号不能为空！", false);
        return;
      }
      if (!dsf.validate("isMobileOrTelPhone", this.phoneNumber)) {
        dsf.layer.message("手机号输入有误", false);
        return;
      }
      let loading = dsf.layer.loading();
      // todo 获取验证码，这次setTimeout模拟了异步请求
      this.$http
        .post(this.getCodeUrl, { phone: this.phoneNumber }, $$webRoot.dsfa)
        .done(({ success, data, message }) => {
          if (success) {
            dsf.layer.message("发送成功");
            this.codeBtType = "countdown";
            dsf.layer.closeLoading(loading);
            this.doCountdown();
          } else {
            dsf.layer.message(message || "请求异常", false);
          }
        })
        .error((err) => {
          dsf.error(err);
          dsf.layer.message(err?.message || "请求异常", false);
        })
        .always(() => {});

    },
    // 执行倒计时
    doCountdown() {
      this.countdownTime = this.countdownTimes - 1;
      this.timeout = setInterval(() => {
        this.countdownTime--;
        if (this.countdownTime <= -1) {
          this.codeBtType = "";
          clearInterval(this.timeout);
        }
      }, 1000);
    },
    // 验证验证码
    authCode(){
      return new Promise((resolve)=>{
        this.$http
          .get(this.authCodeUrl, { phone: this.phoneNumber,code:this.phoneCode }, $$webRoot.dsfa)
          .done(({ success, data }) => {
            if (success) {
              resolve({err:null,token:data})
            } else {
              resolve({err:data||'请求异常',token:null})
            }
          })
          .error((err) => {
            resolve({err:err?.message||'请求异常',token:null})
          })
          .always(() => {});
      })
     
    },
    // 下一步
    async nextStep(step) {
      if(step===1){
        // 验证验证码值
        this.isWaiting = true;
        let {err,token} = await this.authCode()
        this.isWaiting = false;
        if(err){
          this.codeAuthResult = err
          return false
        }
        this.token = token
        this.step = step;
      }else if(step===2) {
        this.isWaitingTwo = true;
        let {err} = await this.alterPassword()
        this.isWaitingTwo = false;
        if(err) return dsf.layer.message(err, false);
        
        this.step = step;
      }
    },
    // 修改密码
    alterPassword(){
      return new Promise((resolve)=>{
        this.$http
          .post(this.alterPwdUrl, { phone: this.phoneNumber,token:this.token,pwd:this.newpwd}, $$webRoot.dsfa)
          .done(({ success, data, message }) => {
            if (success) {
              resolve({err:null,token:data})
            } else {
              resolve({err:message||'请求异常',token:null})
            }
          })
          .error((err) => {
            resolve({err:err?.message||'请求异常',token:null})
          })
          .always(() => {});
      })
    },
    reLogin() {
      this.$dispatch('complete')
      this.$router.go(-1)
    },
  }
})
</script>