<template>
  <div class="ds-control ds-form-item ds-textbox mobile" :class="getCss">
    <template v-if="simple && readOnly && !isDesign">
      <DsfTextProxy v-model="value" v-bind="_self.$props"></DsfTextProxy>
    </template>
    <template v-else>
      <van-field
        v-model="inputValue"
        :placeholder="!readOnly ? placeholder : ''"
        :type="inputType"
        :disabled="disabled"
        :label-width="getLabelWidth(true)"
        :label="showLabel && label ? label : ''"
        :readonly="readOnly"
        :required="!readOnly ? required : false"
        :input-align="$inputAlign"
        :error-message="errorsMsg"
        :error-message-align="$inputAlign"
        :class="{ 'has-passwd-strength': isCheckStrength }"
        @blur="blurHandler"
        @change="changeHandler"
        @click-right-icon="passwdIsOpen = !passwdIsOpen"
      >
        <div
          v-if="isCheckStrength && (isDesign || (inputValue && inputValue.length > 0))"
          class="passwd-strength"
          slot="extra"
          :style="{ paddingLeft: getLabelWidth(true) }"
        >
          <span class="title">密码强度：</span>
          <div class="process">
            <div
              class="fore"
              :style="{
                width: passwdStutas.width + '%',
                background: passwdStutas.color,
              }"
            ></div>
          </div>
          <span>{{ passwdStutas.text }}</span>
        </div>
        <van-icon v-if="ishideCon" class="iconfont" class-prefix="icon" slot="right-icon" :name="getIcon"></van-icon>
      </van-field>
    </template>
  </div>
</template>
<script>
export default dsf.component({
  name: "DsfMobileTextbox",
  mixins: [$mixins.formControl],
  ctrlCaption: "文本框",
  props: {
    ishideCon: {
      type: Boolean,
      default: false,
    },
    openIcon: {
      type: String,
      default: "icon-icon-invisible",
    },
    hideIcon: {
      type: String,
      default: "icon-icon-visible",
    },
    isCheckStrength: {
      type: Boolean,
      default: false,
    },
    placeholder: {
      type: String,
      default: "请输入",
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("text-meta-data");
      },
    },
    inputAlign: {
      type: String,
      default: "right",
    },
  },
  data() {
    return {
      passwdIsOpen: false,
      passwdAllStutas: {
        1: {
          text: "弱",
          color: "#ff3b30",
        },
        2: {
          text: "中",
          color: "#ff8f1f",
        },
        3: {
          text: "强",
          color: "#00b578",
        },
        4: {
          text: "强",
          color: "#00b578",
        },
      },
      passwdStutas: {
        width: 0,
        text: "弱",
        color: "#ff3b30",
      },
    };
  },
  computed: {
    $inputAlign() {
      return this.readOnly ? "right" : this.inputAlign;
    },
    inputType() {
      return this.ishideCon && !this.passwdIsOpen ? "password" : "text";
    },
    inputValue: {
      get() {
        return this.value;
      },
      set(v) {
        this.emitValueChange(v);
      },
    },
    getIcon() {
      // iconfont 前缀都是icon-
      const icon = this.passwdIsOpen ? this.hideIcon : this.openIcon;
      return icon.replace("icon-", "");
    },
  },
  mounted() {
    // console.log(this.props);
  },
  methods: {
    changeHandler() {
      this.$dispatch("change", this.value);
      this.$nextTick(() => {
        this.emitFormValidate(this.value);
      });
    },
    blurHandler() {
      this.$dispatch("blur");
      if (this.isCheckStrength) {
        const modes = this.checkStrong(this.inputValue);
        this.passwdStutas.width = modes * 25;
        Object.assign(this.passwdStutas, this.passwdAllStutas[modes]);
      }
    },
    // 密码强度检测
    checkStrong(sValue) {
      var modes = 0;
      //正则表达式验证符合要求的
      if (sValue.length < 1) return modes;
      if (/\d/.test(sValue)) modes++; //数字
      if (/[a-z]/.test(sValue)) modes++; //小写
      if (/[A-Z]/.test(sValue)) modes++; //大写
      if (/\W/.test(sValue)) modes++; //特殊字符
      return modes > 2 ? (sValue.length < 12 ? 3 : 4) : modes;
    },
  },
});
</script>
