<template>
  <div class="ds-control ds-button-bar mobile" :class="getCss">
    <div v-if="align != 'justify-iconleft-buttonright'" class="bar-wrap">
      <template v-for="(button, $index) in showButtonsReal">
        <DsfMobileButton
          :key="$index"
          class="ds-button-bar-item"
          :style="getButtonStyle(button)"
          :icon="getIcon(button)"
          :title="button.title"
          :disabled="button.disabled"
          :size="size"
          :type="button.type"
          :btn-style="button.btnStyle"
          @click="clickHandler(button, $event)"
        >
          {{ button.text }}
        </DsfMobileButton>
      </template>
      <DsfMobileButton
        v-if="moreButtonsReal.length > 0"
        class="ds-button-bar-item"
        :style="getButtonStyle()"
        :btn-style="moreButtonStyle"
        type="plain"
        :size="size"
        :icon="moreButtonStyle == 'icon-text' ? 'gengduo1' : ''"
        @click="open"
      >
        {{ moreButtonText }}
      </DsfMobileButton>
    </div>
    <!--图标按钮居左 普通按钮居右-->
    <div v-else-if="align == 'justify-iconleft-buttonright'" class="bar-wrap">
      <div>
        <template v-for="(button, $index) in iconButtons">
          <DsfMobileButton
            :key="$index"
            class="ds-button-bar-item"
            :style="getButtonStyle(button)"
            :icon="getIcon(button)"
            :title="button.title"
            :disabled="button.disabled"
            :size="size"
            :type="button.type"
            :btn-style="button.btnStyle"
            @click="clickHandler(button, $event)"
          >
            {{ button.text }}
          </DsfMobileButton>
        </template>
        <DsfMobileButton
          v-if="moreButtonsReal.length > 0 && moreButtonStyle == 'icon-text'"
          class="ds-button-bar-item"
          :style="getButtonStyle()"
          :btn-style="moreButtonStyle"
          :size="size"
          icon="gengduo1"
          @click="open"
        >
          {{ moreButtonText }}
        </DsfMobileButton>
      </div>
      <div>
        <template v-for="(button, $index) in generalButtons">
          <DsfMobileButton
            :key="$index"
            class="ds-button-bar-item"
            :style="getButtonStyle(button)"
            :icon="getIcon(button)"
            :title="button.title"
            :disabled="button.disabled"
            :size="size"
            :type="button.type"
            :btn-style="button.btnStyle"
            @click="clickHandler(button, $event)"
          >
            {{ button.text }}
          </DsfMobileButton>
        </template>
        <DsfMobileButton
          v-if="moreButtonsReal.length > 0 && moreButtonStyle != 'icon-text'"
          class="ds-button-bar-item"
          :style="getButtonStyle()"
          :btn-style="moreButtonStyle"
          type="plain"
          :size="size"
          icon=""
          @click="open"
        >
          {{ moreButtonText }}
        </DsfMobileButton>
      </div>
    </div>
    <van-action-sheet v-if="actionSheetShow" v-model="moreOpen" get-container="body" @closed="closed">
      <button
        v-for="(button, index) in moreButtonsReal"
        :key="index"
        type="button"
        class="van-action-sheet__item"
        :class="{ 'van-action-sheet__item--disabled': button.disabled }"
        @click="clickHandler(button, $event)"
      >
        <span class="van-action-sheet__name">{{ button.text }}</span>
      </button>
    </van-action-sheet>
  </div>
</template>
<script>
export default dsf.component({
  name: "DsfMobileButtonBar",
  ctrlCaption: "按钮组",
  mixins: [$mixins.control, $mixins.buttonBar],
  props: {
    size: {
      type: String,
      default: "",
    },
    align: {
      type: String,
      default: "center",
    },
    buttons: {
      type: Array,
      default() {
        return [];
      },
    },
    emitPrefix: {
      type: String,
      default: "",
    },
    renderButton: {
      type: Function,
      default: null,
    },
    maxNumber: {
      type: Number,
      default: 2,
    },
    moreButtonStyle: {
      type: String,
      default: "",
    },
    moreButtonText: {
      type: String,
      default: "更多",
    },
    styles: {
      type: String,
      default: "normal",
    },
  },
  data() {
    return {
      moreOpen: false,
      actionSheetShow: false,
      flowButtons: [],
      buttonHandler: dsf.buttons.getButtonHander,
      client: "mobile",
    };
  },
  computed: {
    owner() {
      return this.$buttonOwner;
    },
    actions() {
      return _.map(this.moreButtonsReal, (button) => {
        return {
          name: button.text,
          disabled: button.disabled,
        };
      });
    },
    showBar() {
      if (this.isDesign) {
        return true;
      } else {
        return this.validButtons.length > 0;
      }
    },
    validButtons() {
      return _.filter(this.buttons, (button) => {
        if (this.showAndHide(button)) {
          this.disabledFactor(button);
          return true;
        }
        return false;
      });
    },
    moreButtonsReal() {
      return _.filter(this.validButtons, (button, index) => {
        return index >= this.maxNumber;
      });
    },
    showButtonsReal() {
      return _.filter(this.validButtons, (button, index) => {
        return index < this.maxNumber;
      });
    },
    getCss() {
      return {
        ...this.$getClass(),
        ["align-" + this.align]: true,
      };
    },
    iconButtons() {
      return _.filter(this.showButtonsReal, (button) => {
        return button.btnStyle == "icon-text";
      });
    },
    generalButtons() {
      return _.filter(this.showButtonsReal, (button) => {
        return button.btnStyle == "";
      });
    },
  },
  beforeCreate() {},
  created() {},
  methods: {
    getButtonStyle(button) {
      //button可能为空
      let style = {};
      if (this.align == "justify-horizontal") {
        if (this.showButtonsReal.length > 1) {
          let w = `calc(100% / ${this.showButtonsReal.length + (this.moreButtonsReal.length > 0 ? 1 : 0)} - ${this.pxToRem("5px")})`;
          style["min-width"] = w;
          style["max-width"] = w;
          style["width"] = w;
        } else {
          style["min-width"] = "100%";
        }
      }
      if (this.align == "justify-vertical") {
        style["min-width"] = "100%";
      }
      return style;
    },
    reloadData() {
      this.$forceUpdate();
    },
    disabledFactor(button) {
      let result = false;
      if (!this.isDesign) {
        //非设计模式下运行表达式
        if (button.disabledFactor) {
          let obj = {
            $vm: {
              value: this.$vm,
            },
          };
          let data = Object.create(this.local, obj);
          result = this.$eval(button.disabledFactor, data) !== false;
        } else {
          result = false;
        }
      }
      button.disabled = result;
    },
    showAndHide(button) {
      let result = true;
      if (!this.isDesign) {
        //非设计模式下运行表达式
        if (button.if) {
          let obj = {
            $vm: {
              value: this.$vm,
            },
          };
          let data = Object.create(this.local, obj);
          result = this.$eval(button.if, data) === false ? false : true;
        } else {
          if (button.syncRead && (this?.owner?.readOnly || this?.$vm?.$data?.$forceReadOnly)) {
            result = false;
          }else{
            result = true;
          }
        }
      }
      // 流程按钮需要判断
      if (result && button.isSystem && (button.actionName.indexOf("system_flow") > -1 || button.actionName.indexOf("system_batch_flow") > -1)) {
        result = this.hideFlowBtn(button);
      }
      if (button.visible === false) {
        result = false;
      }
      if (this.renderButton) {
        result = this.renderButton(button, result);
      }
      return result;
    },
    async clickHandler(button, $event, response) {
      var fn = $mixins.buttonBar.methods.clickHandler;
      await fn.call(this, button, $event, response);
      this.moreOpen = false;
      $event.stopPropagation();
    },
    getIcon(button) {
      let icon = button.icon;
      if (icon && icon.startsWith("icon-")) {
        return icon.replace("icon-", "");
      }
      return icon;
    },
    //触发指定按钮的点击事件
    triggerButtonClick(button) {
      this.clickHandler(button);
    },
    open() {
      this.actionSheetShow = true;
      this.$nextTick(() => {
        this.moreOpen = true;
      });
    },
    closed() {
      this.actionSheetShow = false;
      this.moreOpen = false;
    },
    onSelect() {},
  },
  beforeDestroy() {
    this.actionSheetShow = false;
    this.moreOpen = false;
  },
  destroyed() {},
});
</script>
