<template>
  <div
    class="ds-control mobile ds-me-menus"
    :class="{ 'ds-no-padding': !isDesign }"
    :style="controlStyle"
  >
    <div class="ds-me-menus-wrap" :style="wrapStyle">
      <template v-for="(menu, index) in menus">
        <div
          :key="index"
          class="ds-me-menus-row"
          :style="rowStyle"
          @click="menuRowClick(menu, index)"
        >
          <DsfIcon
            class="ds-me-menus-row-icon"
            :name="(menu && menu.icon) || ''"
            :style="iconStyle(index)"
          ></DsfIcon>
          <div class="ds-me-menus-row-label">
            {{ (menu && menu.name) || "" }}
          </div>
          <div class="ds-me-menus-row-desc">
            {{ (menu && menu.subhead) || "" }}
          </div>
          <DsfIcon
            v-if="menu && menu.url"
            class="ds-me-menus-row-arrow"
            name="arrow-right"
          ></DsfIcon>
        </div>
      </template>
    </div>
  </div>
</template>
<script>
export default dsf.component({
  name: "DsfMobileMeMenus",
  ctrlCaption: "我的-菜单",
  mixins: [$mixins.control],
  props: {
    height: {
      type: String,
      default: "auto",
    },
    padding: {
      type: Array,
      default: () => [0, 0, 0, 0],
    },
    rowPadding: {
      type: Array,
      default: () => [0, 15, 0, 12],
    },
    rowMargin: {
      type: Array,
      default: () => [0, 0, 0, 0],
    },
    rowHeight: {
      type: Number,
      default: 58,
    },
    menus: {
      type: Array,
      default: () => [
        {
          id: "1",
          name: "个人信息",
          icon: "tab-addressBook-d",
          url: "1",
          subhead: "",
        },
        {
          id: "2",
          name: "修改密码",
          icon: "password",
          url: "1",
          subhead: "",
        },
        {
          id: "3",
          name: "我的班级",
          icon: "guidang",
          url: "1",
          subhead: "已参加0个班级",
        },
        {
          id: "4",
          name: "我的网课",
          icon: "shipinbofangyingpian",
          url: "1",
          subhead: "",
        },
        {
          id: "5",
          name: "我的收藏",
          icon: "shoucang",
          url: "1",
          subhead: "",
        },
        {
          id: "6",
          name: "我的订阅",
          icon: "iconfontzhizuobiaozhun13",
          url: "1",
          subhead: "",
        },
      ],
    },
    url: {
      type: String,
      default: "",
    },
    iconColorList: {
      type: Array,
      default: () => ["#E74E42", "#2495C7", "#FFA529"],
    },
  },
  computed: {
    controlStyle() {
      let { padding, isDesign } = this;
      // let paddings = padding.map((num) => this.pxToRem(num));
      let style = {
        // padding: `${paddings[0]} ${paddings[2]}`,
      };
      if (isDesign) {
        style.padding = "10px 0";
      }
      return style;
    },
    wrapStyle() {
      let { padding, bgColor, isDesign } = this;
      // let paddings = padding.map((num) => this.pxToRem(num));
      let style = {
        // margin: `${paddings[1]} ${paddings[3]}`,
      };
      if (isDesign) {
        style.margin = "0 10px";
      }
      if (bgColor) {
        style.backgroundColor = bgColor;
      }
      return style;
    },
    rowStyle() {
      let { rowPadding, rowMargin, rowHeight } = this;
      let style = {
        height: this.pxToRem(rowHeight),
        padding: rowPadding.map((num) => this.pxToRem(num)).join(" "),
        margin: rowMargin.map((num) => this.pxToRem(num)).join(" "),
      };
      return style;
    },
  },
  data() {
    return {};
  },
  watch: {
    url: {
      immediate: true,
      handler() {
        this.getMenus();
      },
    },
  },
  beforeCreate() {},
  created() {},
  beforeMount() {},
  mounted() {},
  methods: {
    getMenus() {
      let { url, isDesign } = this;
      if (!url || isDesign) return;
      this.$emit("update:menus", []);
      let params = {};
      this.$http
        .get(url, params)
        .then(({ data }) => {
          if (data && data.success) {
            this.$emit("update:menus", data.data || []);
          } else {
            dsf.layer.message(data.message, false);
          }
        })
        .catch((error) => {
          console.error(error);
          dsf.layer.message("请求我的菜单，未知错误！", false);
        });
    },
    iconStyle(index) {
      let { iconColorList } = this;
      let style = {};
      let color = iconColorList && iconColorList[index % iconColorList.length];
      if (color) {
        style.color = color;
      }
      return style;
    },
    menuRowClick(menu) {
      if (!menu.url || this.isDesign) return;
      this.$open({ url: this.$replace(menu.url, {}), state: 4 });
    },
  },
  beforeDestroy() {},
  destroyed() {},
});
</script>
