<template>
  <div class="ds-control" style="padding: 0">
    <div v-if="(isDesign || sPID) && data.length > 0" class="ds-form-item ds-flowprocess mobile work_flow-process">
      <div class="content">
        <div
          v-for="line in data"
          :key="line.linkid"
          class="item"
          :class="{
            'no-user': !line.rusername,
            unusual: line.STATUS == '-4',
            noyj: !line.opinionatts || (line.opinionatts && !line.opinionatts.Content && !line.opinionatts.Attachments),
          }"
        >
          <van-image round width="40px" height="40px" fit="cover" :src="'/oua2/getUserPhoto?type=relativePath&userId=' + line.ruserid">
            <template v-slot:loading>
              <van-loading type="spinner" size="20" />
            </template>
            <template v-slot:error>
              <img class="header" src="~_platform/assets/styles/themes/workFlow/default-header.png" />
            </template>
          </van-image>

          <van-image class="icon" round fit="cover" :src="getStateImg(line)"></van-image>
          <div class="des">
            <p class="first-child">
              <span class="title">{{ line.nodename }}</span>
              <span class="time">{{ format(line.reachtime) }}</span>
            </p>
            <p v-if="line.rusername" class="name">{{ line.rusername }}</p>
            <p v-if="getIdea(line)" class="yj">{{ getIdea(line) }}</p>
            <p class="files">
              <!-- <van-grid :column-num="3" :border="false">
                <van-grid-item
                  v-for="(file, i) in getFiles(line)"
                  :key="i"
                  :icon="getType(file)"
                  :text="file.originalFileName"
                  @click="preview(file)"
                >
                </van-grid-item>
              </van-grid> -->
              <DsfMobileFlowFileList
                v-if="line.opinionatts && line.opinionatts.Attachments && line.opinionatts.Attachments.length > 0"
                :Attachments="line.opinionatts.Attachments"
              ></DsfMobileFlowFileList>
            </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import DsfMobileFlowFileList from "./mobileFlowFileList";
export default dsf.component({
  name: "DsfMobileFlowProcess",
  mixins: [$mixins.control],
  ctrlCaption: "办理情况",
  components: {
    DsfMobileFlowFileList,
    // situationSimple,
  },
  props: {
    caption: {
      type: String,
      default() {
        return "wfprocess";
      },
    },
  },
  data() {
    return {
      sPID: this.$route.query.sPID,
      sCurLinkID: this.$route.query.sCurLinkID,
    };
  },
  computed: {
    data() {
      if (this.isDesign || !this.sPID) {
        return [
          {
            nodename: "开始",
            STATUS: 0,
            linkid: "faedda583f7742948b0714f0f17d1085",
            opinionatts: {
              Content: "同意",
            },
            ruserid: "",
            pid: "af60e455757d48dd801d07b1538eb806",
            rusername: "平台管理员",
            rdeptname: "平台管理组",
            reachtime: "2020年01月06日 18:21:21",
            receivetime: "2020年1月2日 10:21:21",
          },
          {
            nodename: "审批",
            STATUS: -4,
            linkid: "saedda583f7742948b0714f0f17d1085",
            opinionatts: {
              Content: "不同意",
            },
            ruserid: "",
            pid: "sf60e455757d48dd801d07b1538eb806",
            rusername: "平台管理员",
            rdeptname: "平台管理组",
            reachtime: "2020年01月06日 18:21:21",
            receivetime: "2020年1月2日 10:21:21",
          },
          {
            nodename: "拟稿",
            STATUS: 1,
            linkid: "caedda583f7742948b0714f0f17d1085",
            opinionatts: {
              Content: "",
            },
            ruserid: "",
            pid: "cf60e455757d48dd801d07b1538eb806",
            rusername: "平台管理员",
            rdeptname: "平台管理组",
            reachtime: "2020年01月06日 18:21:21",
            receivetime: "2020年1月2日 10:21:21",
          },
        ];
      }
      return this.$vm?.flows?.flowProcess ? this.formatData(_.cloneDeep(this.$vm.flows.flowProcess)) : [];
    },
  },
  mounted() {
    const data = (this.$dialog && this.$dialog.dialogArgs && this.$dialog.dialogArgs.data && this.$dialog.dialogArgs.data[0]) || null;
    this.sPID = data ? data.sPID : this.$route.query.sPID || this.queryString.sPID;
    this.sCurLinkID = data ? data.sCurLinkID : this.$route.query.sCurLinkID || this.queryString.sCurLinkID;
    if (this.sPID) {
      this.init();
    }
  },
  methods: {
    init() {
      this.$http
        .get("wfr/getProcessInfoByPID", {
          sPID: this.sPID,
        })
        .then((res) => {
          if (res.data.success) {
            let params;
            if (this.$vm?.flows) {
              this.$set(this.$vm.flows, "flowProcess", res.data.data);
            }
            res.data.data.forEach((item) => {
              if (item.linkid === this.sCurLinkID && item.pid === this.sPID) {
                if (item.STATUS != 0 && item.STATUS != 1) {
                  params = dsf.mix({}, this.$route.query, {
                    isHide: true,
                  });
                }
              }
            });
            //更新url添加id参数
            if (params) {
              this.$router.replace({
                path: this.$route.path,
                query: params,
              });
            }
          }
        });
    },
    getStateImg(data) {
      let imgI = data.STATUS;
      if (imgI == "-31") {
        imgI = "-3";
      } else if (imgI == "-1") {
        imgI = "-2";
      }
      if (!imgI && imgI !== 0) {
        return null;
      }
      return require(`_platform/assets/styles/themes/workFlow/status${imgI}.png`);
    },
    getIdea(data) {
      if (data.opinionatts) {
        return data.opinionatts.Content;
      }
      return null;
    },
    format(str) {
      if (str) {
        return dsf.date.format(str, "mm-dd hh:ii");
      } else {
        return "";
      }
    },
    formatData(list) {
      list.forEach((item) => {
        item.opinionatts = JSON.parse(item.opinionatts);
      });
      return list;
    },
  },
});
</script>
