<template>
  <div class="dropdown-box-main" :class="rootClass" :style="rootStyle" @click.stop>
    <div class="dropdown-box-main-content">
      <!--下拉选择，下拉，单选-->
      <template v-if="item.type == 'enum' && item.showModel=='select' && !item.isAllowMultipe">
        <dsf-super-search-down-item-select ref="formItem" :values.sync="item.values" :item="item" @change="change"/>
      </template>
      <template v-else>
        <!--下拉选择，树选择-->
        <template v-if="item.type == 'enum' && item.showModel=='treeselect'">
          <dsf-super-search-down-item-tree ref="formItem" :values.sync="item.values" :item="item" @change="change"/>
        </template>
        <template v-else>
          <dsf-super-search-form-item ref="formItem" :values.sync="item.values" :item="item" line />
        </template>
      </template>
    </div>
    <div v-if="item.showModel !=='select' || item.isAllowMultipe" class="dropdown-box-main-footer">
      <van-goods-action  style="position: static;">
        <van-goods-action-button type="info" text="重 置" @click="reset" />
        <van-goods-action-button type="primary" :text="sureBtText" @click="sure" />
      </van-goods-action>
    </div>
  </div>
</template>

<script>
import DsfSuperSearchFormItem from './superSearchFormItem';
import DsfSuperSearchDownItemSelect from './superSearchDownItemSelect';
import DsfSuperSearchDownItemTree from './superSearchDownItemTree';

export default {
  name: "DsfSuperSearchDownItem",
  components: {
    DsfSuperSearchFormItem,
    DsfSuperSearchDownItemSelect,
    DsfSuperSearchDownItemTree
  },
  inject: {
    $list: {
      default: null
    },
    $superSearch: {
      default: null
    }
  },
  props: {
    item: {
      type: Object,
      required: true
    },
    show: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      formItem: null,
      rootClass: []
    }
  },
  computed: {
    sureBtText() {
      if (this.item.showModel == 'select' && !this.item.isAllowMultipe) return '';
      if (this.item.showModel=='treeselect') {
        return '确 定';
      }
      let value = this?.formItem?.value;
      if (dsf.type(value) === 'array' && value.length) {
        return `确 定 (${value.length})`;
      }
      return '确 定';
    },
    rootStyle() {
      let res = {};
      if (this.item.showModel == 'treeselect') {
        res['max-height'] = 'none';
        res['height'] = '100%';
        res['border-radius'] = '0';
      }
      return res;
    }
  },
  watch: {
    show(to) {
      if (!to) {
        this.rootClass = [];
      } else {
        this?.formItem.open?.();
        setTimeout(() => {
          this.rootClass = ['show'];
        }, 50)
      }
    }
  },
  mounted() {
    this.formItem = this.$refs.formItem;
  },
  methods: {
    reset() {
      this.formItem?.reset();
      this.change();
    },
    sure() {
      this.formItem?.sure();
      this.change();
    },
    change() {
      this.$superSearch.dropdownActive = null;
      // 触发列表重新请求
      this.$superSearch.emitChange();
    }
  }
}
</script>