<template>
  <div class="work_flow-usersDialog">
    <div class="content">
      <template>
        <div class="txt">
          <van-field v-model="sOpinion.Content" rows="2" autosize type="textarea" placeholder="请输入审批意见" />
        </div>
        <div v-if="tpls.length > 0" class="template">
          <div>
            <span v-for="(tpl, i) in tpls" :key="i" @click="sOpinion.Content = tpl.key_word">{{ tpl.key_word }}</span>
          </div>
        </div>
        <div class="funs">
          <i class="iconfont icon-fujian" @click="uploader(0)"></i>
          <i class="iconfont icon-tupian" @click="uploader(1)"></i>
        </div>
        <van-uploader
          ref="uploader"
          v-model="uploaderData"
          :class="{ no: uploaderData.length == 0 }"
          :max-count="9"
          multiple
          :after-read="uploaderAfterRead"
          :accept="acceptsType"
          @click-preview="preview"
        />
        <van-popup v-model="previewing" :style="{ height: '80%', width: '100%' }">
          <div>
            <dsf-iframe v-if="previewing" :src="filePath"></dsf-iframe>
          </div>
        </van-popup>
      </template>
    </div>
  </div>
</template>
<script>
export default {
  name: "DsfMobileAddOpinion",
  props: {
    sOpinion: {
      type: Object,
      default() {
        return {
          Content: "",
          Attachments: [],
        };
      },
    },
  },
  data() {
    return {
      message: "",
      loader: null,
      tpls: [],
      acceptTypes: [".doc,.docx,.xml,application/msword,application/vnd.openxmlformats-officedocument.wordprocessingml.document", "image/*"],
      acceptsType: "",
      uploaderData: [],
      previewing: false,
      filePath: "",
    };
  },
  created() {
    this.init();
    this.getTemplate();
  },
  methods: {
    init() {
      if (this.sOpinion?.Attachments) {
        this.uploaderData = this.sOpinion?.Attachments.map((item) => {
          item.url = dsf.url.getWebPath(item.relativePath);
          return item;
        });
      }
    },
    getLinkName(name) {
      let ns = name.split("→");
      return ns[ns.length - 1];
    },
    getUserData(d, parent, index) {
      if (!d) {
        return;
      }
      if (d.Objs) {
        d.ChildDepts = d.ChildDepts.concat(d.Objs);
      }
      if (d.Type != 0) {
        if (d.Selected * 1 > 0) {
          // 0 人工选择、1 默认选择、2必选
          this.data[index]["selects"].push(d);
          this.data[index]["sNum"]++;
          d.isSelect = true;
        }
        d["index"] = this.data[index]["users"].length;
        this.data[index]["users"].push(d);
      }
      for (let i in d.ChildDepts) {
        this.getUserData(d.ChildDepts[i], d, index);
      }
    },
    getTemplate() {
      let loader = dsf.layer.loading(),
        msg = "";
      this.$http
        .get("wfr/getOpinionKeyWord", {})
        .done((res) => {
          if (res.success) {
            this.tpls = res.data || [];
          } else {
            msg = `获取意见模板，请求异常`;
          }
        })
        .error((res) => {
          msg = `获取意见模板，服务器异常`;
        })
        .always(() => {
          dsf.layer.closeLoading(loader);
          if (msg) {
            dsf.layer.toast(msg, false);
          }
        });
    },
    uploader(acceptsIndex) {
      this.acceptsType = this.acceptTypes[acceptsIndex];
      this.$nextTick(() => {
        this.$refs.uploader.chooseFile();
      });
    },
    uploaderAfterRead(files) {
      if (!files.length) {
        files = [files];
      }
      _.forEach(files, (file, index) => {
        file.status = "uploading";
        file.message = "上传中...";

        let error = false;
        let formData = new FormData();
        formData.append("file", file.file);
        this.$http
          .post("file/upload", formData, {
            headers: {
              "Content-Type": "multipart/form-data",
            },
          })
          .done((d) => {
            if (d.state == "20000") {
              file.status = "done";
              file.data = d.data[0];
            } else {
              error = d.message || true;
            }
          })
          .error((d) => {
            error = d.message || true;
          })
          .always(() => {
            if (error) {
              file.status = "failed";
              file.message = "上传失败";
              file._message = error;
            }
            // 全部上传完毕
            if (index + 1 === files.length) {
              this.sOpinion.Attachments = this.uploaderData.map((item) => item.data || item);
              this.$emit("update:sOpinion", this.sOpinion);
            }
          });
      });
    },
    preview(files) {
      if (files && files.data && files.data.contentType && files.data.contentType.slice(0, 5) !== "image") {
        this.filePath = dsf.config.webRoot + "preview/file?fileId=" + files.data.id;
        this.previewing = true;
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.choose-content {
  position: relative;
  width: 100%;
  height: 100%;
}
.fl-box {
  padding: 0 10px;
  box-sizing: border-box;
  height: 100%;
  overflow: auto;
  float: left;
  width: 65%;
  border-left: 1px solid #e2e3e4;
  &.ds_tree_box {
    width: 35%;
  }
  .top {
    overflow: hidden;
    height: 40px;
    line-height: 40px;
    padding-bottom: 10px;
    .ds-button {
      float: right;
    }
  }
  .el-table .cell {
    .iconfont {
      width: 20px;
      display: inline-block;
      text-align: right;
      cursor: pointer;
    }
  }
}
</style>
