<template>
  <div :data-uid="user.stuid" class="user-item" >
    <div class="user-item-content van-hairline--top">
      <van-image lazy-load :key="key" :src="img" round fit="cover">
        <template v-slot:error>
          <div class="ds-address-user-phone-default">{{ uname ? uname.toString().slice(-2) : '未知' }}</div>
        </template>
      </van-image>
      <div class="info-b">
        <div class="name">{{ user.username || '' }}</div>
        <div class="sjhm">{{ user.phone || '' }}</div>
      </div>
      <div v-if="user.phone" class="call"><a :href="'tel:' + user.phone"><i class="iconfont icon-lianxidianhua"></i></a></div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    getKey:{
      type:[String,Function],
      default(){
        return "_id";
      }
    },
    user: {
      type: Object,
      default() {
        return {
          stuid: dsf.uuid(4)
        }
      }
    }
  },
  data () {
    return {
    };
  },

  components: {},

  computed: {
    uname(){
      let reg=/\(.+\)|（.+）/g;
      let userName=this.user.username.replace(reg,'');
      return userName;
    },
    img() {
      if (!this.user.photo) {
        return ''
      }
      return dsf.getFilePath(this.user.photo)
    },
    key(){
      if(dsf.isFunction(this.getKey)){
        return this.getKey();
      }
      return this.getKey;
    }
  },

  mounted() {},

  created() {},

  methods: {}
}

</script>
<style scoped>
</style>