<template>
  <div class="ds-control ds-section mobile" :class="rootClass" :style="rootStyle">
    <div slot-name="default" class="ds-section-wrap">
      <slot name="default"></slot>
    </div>
  </div>
</template>

<script>
export default dsf.component({
  name: "DsfMobileSection",
  ctrlCaption: "页面段落",
  mixins: [$mixins.layout],
  props: {
    isBlockControl:{
      type:Boolean,
      default:true
    },
    slots: {
      type: Array,
      default() {
        return [
          { name: 'default', controls: [] }
        ]
      }
    },
    height: {
      type: String,
      default: 'auto'
    },
    margin: {
      type: Array,
      default() {
        return [0, 0, 0, 0];
      },
    },
    padding: {
      type: Array,
      default() {
        return [0, 0, 0, 0];
      },
    },
    bgColor: {
      type: String,
      default: '#fff'
    },
    shadow: {
      type: Boolean,
      default: true
    },
    borderRadius: {
      type: String,
      default: '10px'
    },
    zIndex: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
    }
  },
  computed: {
    rootClass() {
      let res = this.$getClass();
      if (this.shadow) {
        res.push('shadow');
      }
      return res;
    },
    rootStyle() {
      let margin = this.margin.map(m => dsf.pxToRem(m));
      let padding = this.padding.map(p => dsf.pxToRem(p));
      let res = {
        'background-color': this.bgColor,
        'z-index': this.zIndex || 'auto',
        'padding': padding.join(' '),
        'border-radius': dsf.pxToRem(this.borderRadius)
      };
      if (this.isDesign) {
        this.updateParent({
          height: this.height == 'auto' ? 'auto' : dsf.pxToRem(this.height),
          margin: `${margin[0]} 0 ${margin[2]}`,
          padding: `0 ${margin[1]} 0 ${margin[3]}`,
          zIndex: this.zIndex || 'auto'
        })
        res.height = '100%';
        res.padding = '10px';
      } else {
        res.margin = margin.join(' ');
        res.height = dsf.pxToRem(this.height);
      }
      return res;
    }
  },
  mounted() {
    if (this.isDesign) {
      this.parentEl = this.$el.parentElement;
      let margin = this.margin.map(m => dsf.pxToRem(m));
      this.updateParent({
        height: dsf.pxToRem(this.height),
        margin: `${margin[0]} 0 ${margin[2]}`,
        padding: `0 ${margin[1]} 0 ${margin[3]}`,
        zIndex: this.zIndex || 'auto'
      });
    }
  },
  methods: {
    updateParent({ height, margin, padding, zIndex }) {
      if (this.parentEl) {
        this.parentEl.style.height = height;
        this.parentEl.style.margin = margin;
        this.parentEl.style.padding = padding;
        this.parentEl.style.zIndex = zIndex;
        this.parentEl.style.position = 'relative';
        this.parentEl.style.boxSizing = 'border-box';
      }
    }
  }
})
</script>